<template>
	<div class="container">
		<div class="main addpage" :style="{'margin-left':$store.state.app.isCollapse ? '84px':'240px'}">
			<div class="main_container">
                <el-breadcrumb separator-class="el-icon-arrow-right">
                    <!-- <el-breadcrumb-item v-for="(mat, index) in $route.matched" v-bind:key="index"> -->
						<span class="color_tip">项目详情</span>
					<!-- </el-breadcrumb-item> -->
				</el-breadcrumb>
				
				<!--from 框-->
				<div style="height: calc(100vh - 150px);overflow: auto;background:#dff1f1" class="info_form">
					<el-form :model="tableLabel" ref="saveForm" label-position="right" v-loading="dialogLoading" :inline="true" style="overflow:auto;padding-top:15px">
						
						<el-form-item
							v-for="(domain, index) in tableLabel"
							:key="domain.colDname"
							:label="domain.dispName"
							:label-width="formLabelWidth"
							:prop="index + '.value'"
							:rules="domain.rule"
						>

							<el-date-picker class="diy_long" 
											v-if="domain.dispType == 'DATE'" 
											v-model="domain.value" 
											:type="domain.colLimit" 
											:value-format="domain.isDispDetail" 
											placeholder="选择日期"
											disabled>
							</el-date-picker>
							<!--下拉框-->
							<el-select class="diy_long" 
									v-if="domain.dispType == 'dropdown' && domain.colDname !='speciality'
										&& domain.colDname !='itemClass1'&& domain.colDname !='itemClass2'
										&& domain.colDname !='itemClass3'&& domain.colDname !='fragmentaryType'" 
									v-model="domain.value" 
									disabled>
								<el-option v-for="Titem in dictMap.get(domain.colDname)" :key="Titem.codeValue" :label="Titem.codeName" :value="Titem.codeValue"></el-option>
							</el-select>
							<!--零星购置专项类型-->
							<el-select v-model="domain.value" placeholder="请选择零星购置专项类型" clearable 
									class="diy_long" style="width:300px" 
									v-if="domain.dispType == 'dropdown' && domain.colDname =='fragmentaryType'" 
									disabled>
									<el-option v-for="item in LGTypeArr" :key="item.id" :label="item.name" :value="item.id"></el-option>
							</el-select>
							<!--命名器三级联动-->
							<el-select class="diy_long" 
									style="width:300px"
									v-if="domain.dispType == 'dropdown' && domain.colDname =='itemClass1'" 
									v-model="domain.value" :placeholder="domain.dispName" clearable
									@change="changeItemClass2(domain.value,false)"
									disabled>
								<el-option v-for="Titem in itemClassArr1" :key="Titem.typeCode" :label="Titem.typeName" :value="Titem.typeCode"></el-option>
							</el-select>
							<el-select class="diy_long" 
									style="width:300px"
									v-if="domain.dispType == 'dropdown' && domain.colDname =='itemClass2'" 
									v-model="domain.value" :placeholder="domain.dispName" clearable
									@change="changeItemClass3(domain.value,false)"
									disabled>
								<el-option v-for="Titem in itemClassArr2" :key="Titem.typeCode" :label="Titem.typeName" :value="Titem.typeCode"></el-option>
							</el-select>
							<el-select class="diy_long" 
									style="width:300px"
									v-if="domain.dispType == 'dropdown' && domain.colDname =='itemClass3'" 
									v-model="domain.value" :placeholder="domain.dispName" clearable
									disabled>
								<el-option v-for="Titem in itemClassArr3" :key="Titem.typeCode" :label="Titem.typeName" :value="Titem.typeCode"></el-option>
							</el-select>
							
							<!--树形下拉控件-->
							<SelectTree 
								v-if="domain.dispType == 'deptDropdown'"
								:disabled="true"
								:size="'large'"
								:props="props"
								:options="deptArr"
								:value="valueId"
								:clearable="isClearable"
								:accordion="isAccordion"
								@getValue="getValue1($event)"></SelectTree>
							<!--固定资产分类三级联动-->
							<el-select class="diy_long" 
									style="width:300px"
									v-if="domain.dispType == 'cascadeDropdown' && domain.colDname =='gdAssetType'" 
									v-model="domain.value" :placeholder="domain.dispName" clearable
									@change="getCascadeLabel2(domain.value,false)"
									disabled>
								<el-option  v-for="item in cascadeLabel" :key="item.codeValue" :label="item.codeName" :value="item.codeValue"></el-option>
							</el-select>
							<el-select class="diy_long" 
									style="width:300px"
									v-if="domain.dispType == 'cascadeDropdown' && domain.colDname =='assetType2'" 
									v-model="domain.value" :placeholder="domain.dispName" clearable
									@change="getCascadeLabel3(domain.value,false)"
									disabled>
								<el-option v-for="item in cascadeLabel2" :key="item.codeValue" :label="item.codeName" :value="item.codeValue"></el-option>
							</el-select>
							<el-select class="diy_long" 
									style="width:300px"
									v-if="domain.dispType == 'cascadeDropdown' && domain.colDname =='assetType3'" 
									v-model="domain.value" :placeholder="domain.dispName" clearable
									disabled>
								<el-option v-for="item in cascadeLabel3" :key="item.codeValue" :label="item.codeName" :value="item.codeValue"></el-option>
							</el-select> 
							<!--单选框-->
							<el-radio-group class="diy_long"
								v-if="domain.dispType == 'radio'"
								v-model="domain.value"
								disabled>
								<el-radio v-for="item in dictMap.get(domain.colDname)" :key="item.codeValue" :label="item.codeValue">
									{{item.codeName}}	
								</el-radio>
							</el-radio-group>
							<!--文本域-->
							<el-input
								class="diy_long"
								v-if="domain.dispType == 'text'"
								type="textarea"
								v-model="domain.value"
								disabled
							></el-input>
							<!--数字-->
							<el-input
								v-if="domain.dispType=='number'" 
								type="number"
								v-model="domain.value"
								style="width:300px"
								disabled
							></el-input>
							<!--计数器-->
							<el-input-number 
									v-if="domain.dispType=='integer'"  
									v-model="domain.value"
									:min="1"
									:precision="0"
									style="width:300px"
									disabled>
							</el-input-number>
							<el-input
								class="diy_long"
								v-if="(domain.dispType == '' || domain.dispType == null ||domain.dispType=='string')"
								v-model="domain.value"
								autocomplete="off"
								disabled
							></el-input>
							</el-form-item>
					</el-form>
					<el-divider ><span style="font-family:'微软雅黑';font-weight:bold;">报告列表</span></el-divider>
						<el-form>
							<el-form-item label="可研报告" :label-width="formLabelWidth" v-if="reportFileName!=''">
									<a style="color: #409EFF;" :href="GLOBALURL+'/prjCmFeaPrep/downloadReport?itemId='+feaReportId+'&token='+token" >{{reportFileName}}</a>
							</el-form-item>
							<el-form-item label="可研报告附件" :label-width="formLabelWidth" v-if="reportFileList.length>0">
									<a v-for="(item,index) in reportFileList" :key="index" style="color:#409EFF;margin-right:10px;display:inline-block" :href="GLOBALURL+'/pmpFileUpload/downloadFile?fileId='+item.fileId+'&token='+token" >{{item.fileName}}</a>
							</el-form-item>

							<el-form-item label="可研评审文件" :label-width="formLabelWidth" v-if="feaFileName!=''">
									<a style="color: #409EFF;" :href="GLOBALURL+'/pmpFileUpload/downloadFile?fileId='+feaFileId+'&token='+token" >{{feaFileName}}    </a>
							</el-form-item>
							<el-form-item label="批复文件" :label-width="formLabelWidth" v-if="approvalFileName!=''">
									<a style="color: #409EFF;" :href="GLOBALURL+'/pmpFileUpload/downloadFile?fileId='+approvalFileId+'&token='+token" >{{approvalFileName}}</a>
							</el-form-item>
						</el-form>
					<el-divider v-if="showChildren"><span style="font-family:'微软雅黑';font-weight:bold;">子项目列表</span></el-divider>
					<el-table
						:data="childrenShowData"
						border
						:header-cell-style="{background:'#359691',color:'#fff'}"
						row-class-name="row"
						cell-class-name="column"
						:highlight-current-row="true"
						style="width:100%;heigth:auto"
						v-if="showChildren"
					    >
						<el-table-column type="index" width="50" label="序号"></el-table-column>
						<el-table-column
							v-for="(item, index) in childrenLable"
							v-if="item.colDname != 'itemName' && item.showType == 1 && item.colDname != 'itemCont' &&item.colDname !='questionCode'"
							:key="index"
							:prop="item.colDname"
							:label="item.dispName"
						></el-table-column>
						<el-table-column v-else-if="item.colDname == 'itemName'" :label="item.dispName">
							<template  slot-scope="scope">
								<span  style="color:#333">{{ scope.row.itemName }}</span>
							</template>
							
						</el-table-column>
						<el-table-column
							v-else-if="item.colDname == 'itemCont' "
							
							:label="item.dispName">
							<template slot-scope="scope" v-if="scope.row.itemCont">
								
								<el-tooltip placement="top">
								<div slot="content">{{scope.row.itemCont}}</div>
								<p>{{formatStr(scope.row.itemCont)}}</p>
								</el-tooltip>
								
							</template>
						</el-table-column>
						<el-table-column
							v-else-if="item.colDname == 'questionCode' "
							
							:label="item.dispName">
							<template slot-scope="scope" v-if="scope.row.questionCode">
								<p v-if="scope.row.questionCode.length < 20">
									{{scope.row.questionCode}}
								</p>
								<el-tooltip placement="top" v-else>
								<div slot="content">{{scope.row.questionCode}}</div>
								<p>{{formatStr(scope.row.questionCode)}}</p>
								</el-tooltip>
								
							</template>
						</el-table-column>


					</el-table>
				</div>
				<div class="save_btn">
					<el-button type="primary" size="small" @click="cancle">返回</el-button> 							
				</div>
            </div>
		</div>
	</div>   
</template>

<script>
import { mapState, } from 'vuex'
import SelectTree from "@/components/treeSelect.vue";
import {
	getItemInfoByIdWithLabel,
	getCmNameRule,
	getFragmentaryList,
	getUserDept,
	getTreeDeptCode,
	getCascadeLabel,
	selectReport
} from '@/api/data';

export default {
	data() {
		return {
			//单位名称
			deptName:"",
			//所属单位
			deptArr:[],
			//所选单位code
			deptCode:"",
			isClearable:true,      // 可清空（可选）
			isAccordion:true,      // 可收起（可选）
			valueId:"",            // 初始ID（可选）
			props:{                // 配置项（必选）
				value: 'id',
				label: 'name',
				children: 'children',
				code: 'deptCode'
				// disabled:true
			},
			//项目分类联动数组
			itemClassArr:[],
			itemClassArr1:[],
			itemClassArr2:[],
			itemClassArr3:[],
			//是否可选
			itemClassShow3:false,
			itemClassShow2:false,
			itemClassShow1:false,
			token:'',
			finalFileData:{},
			fileList3:[],
			//表单数据
			itemForm:{},
			itemInfo:{},

			newItemName: false,
			//项目专项
			specClass:"",
			//专项名称
			specClassName:"",
			// 子组件的表格数据
			tableData: [],
			// 子组件的表头数据
			tableLabel: {},
			dictMap: new Map,
			tempId:'',
			columnMap: new Map,
			title:"",
			totalLableArr:[],
			dialogLoading:false,
			formLabelWidth: '120px',
			//父项目id，新增项目包时用
			parentItemId: null,
			//子项目
			childTableData:[],
			//子项目展示数据
			childrenShowData:[],
			//子项目表头
			childrenLable:{},
			//子项目字典
			childrenDictMap: new Map,
			//子项目字段类型
			childrenColumnMap: new Map,
			//是否展示子项目
			showChildren: false,
			//可研报告名称
			reportFileName:'',
			//科研报告文件id
			feaReportId:'',
			//评审文件名
			feaFileName:'',
			//评审文件id
			feaFileId:'',
			//批复文件名
			approvalFileName:'',
			//批复文件id
			approvalFileId:'',
			LGTypeArr:[],
			//固定资产级联
			cascadeLabel:[],
			cascadeLabel2:[],
			cascadeLabel3:[],
			//可研报告附件
			reportFileList:[]
		};
	},
	created(){
		//表格自动高度
		this.tableHeight=window.innerHeight-320+'px';
	},
	mounted() {
		//表格自动高度
		window.onresize = () => {
			return (() => {
				this.tableHeight=window.innerHeight-320+'px';
			})()
		}
		this.token = sessionStorage.getItem('token');
		//为专项类型赋值
		this.tempItemId = this.$route.query.itemId
		this.oldPath = this.$route.query.oldPath
		this.getLabel(this.tempItemId)	
		this.getLgTypeArr()
	},
	components: {
		SelectTree
	},
	computed: {
		...mapState({
			// token:state=>state.user.token,
            addItemParams:state=>state.itemadd.addItemParams
        })
    },
	methods: {
		//固定资产分类（一级）
		getCascadeLabel(headId,flag){
			let _this = this
			let formData = new FormData()
			formData.append('parentId',headId)
			getCascadeLabel({"parentId":headId}).then(response=>{
				if(response.data.successful){
					console.log(response)
					_this.cascadeLabel = response.data.resultValue
					if(_this.tempItemId!=null){//修改
							 let assetType2 = _this.getValue("assetType2")
							 let gdAssetType = _this.getValue("gdAssetType")
							 _this.getCascadeLabel2(gdAssetType,true)
							 _this.getCascadeLabel3(assetType2,true)
				    }
				}
			})
		},
		getCascadeLabel2(val,flag){
			let _this = this
			if(typeof(flag) == "undefined"||flag==false){
				_this.clearValue("assetType2")
			    _this.clearValue("assetType3")
			}
			_this.cascadeLabel2 = []
			_this.cascadeLabel3 = []
			for(let i=0;i<_this.cascadeLabel.length;i++){
					let tempObj = _this.cascadeLabel[i]
					if(tempObj.codeValue == val){
						_this.cascadeLabel2 = tempObj.childList
						break
					}
			}
		},
		getCascadeLabel3(val,flag){
			let _this = this
			_this.cascadeLabel3 = []
			if(typeof(flag) == "undefined"||flag==false){
				_this.clearValue("assetType3")
			}	
			for(let i=0;i<_this.cascadeLabel2.length;i++){
					let tempObj = _this.cascadeLabel2[i]
					if(tempObj.codeValue==val){
						_this.cascadeLabel3 = tempObj.childList
					}
			}
		},
			//获取单位树形结构
		getTreeDeptCode(){
			let _this = this
			getUserDept().then(response=>{
				let valueId  = response.data.resultValue
				
				getTreeDeptCode().then(response=>{
					console.log(response)
					if(response.data.successful){
						_this.valueId = valueId
						_this.deptArr = response.data.resultValue
						
					}
				})
			})
		},
		//获取零购专项下拉框,新增时只显示本人所属对应权限的零购专项
		getLgTypeArr(){
			let _this = this
			getFragmentaryList().then(response=>{
				if(response.data.successful){
					_this.LGTypeArr = response.data.resultValue
				}
			})
		},
		//清空指定字段的值
		clearValue(key){
			let _this = this
			for (let i in _this.tableLabel) {
				let item = _this.tableLabel[i]
				if(item.colDname==key){
					item.value = ''
					break
				}
			}
		},
		getValue(key){
			let _this = this
			for (let i in _this.tableLabel) {
				let item = _this.tableLabel[i]
				if(item.colDname==key){
					return item.value
				}
			}
		},
		setValue(key,value){
			let _this = this
			for (let i in _this.tableLabel) {
				let item = _this.tableLabel[i]
				if(item.colDname==key){
					item.value = value
				}
			}
		},
		//查询
		changeItemClass1(speciality){
			let _this = this
			let param = {"speciality": speciality}
			getCmNameRule(param).then(response=>{
				if(response.data.successful){
					_this.itemClassArr = response.data.resultValue[0].idLevel2	
					_this.itemClassArr1 = response.data.resultValue[0].idLevel2	
					//解析出2级，3级，4级分类
					if(_this.itemClassArr1.length==0){
						_this.itemClassShow1 = true
					}else{
						_this.itemClassShow1 = false
						if(_this.tempItemId!=null){//修改
							 let itemClass1 = _this.getValue("itemClass1")
							 let itemClass2 = _this.getValue("itemClass2")
							 _this.changeItemClass2(itemClass1,true)
							 _this.changeItemClass3(itemClass2,true)
						}
					}
				}
			})
		},
		changeItemClass2(val,flag){
			let _this = this
			// let param = {"idLevel":"3", "parentTypeCode": val}
			if(typeof(flag) == "undefined"||flag==false){
				_this.clearValue("itemClass2")
			    _this.clearValue("itemClass3")
			}
			for(let i=0;i<_this.itemClassArr.length;i++){
					let tempObj = _this.itemClassArr[i]
					if(tempObj.typeCode == val){
						_this.itemClassArr2 = tempObj.idLevel3
						if(_this.itemClassArr2.length==0){
							_this.itemClassShow2 = true
							_this.itemClassShow3 = true
						}else{
							_this.itemClassShow2 = false
							_this.itemClassShow3 = false
						}
						break
					}
			}
		},
		changeItemClass3(val,flag){
			let _this = this
			if(typeof(flag) == "undefined"||flag==false){
				_this.clearValue("itemClass3")
			}	
			for(let i=0;i<_this.itemClassArr.length;i++){
					let tempObj = _this.itemClassArr[i]
					let idLevel3 =  tempObj.idLevel3
					for(let j=0;j<idLevel3.length;j++){
						let tempObj = idLevel3[j]
						if(tempObj.typeCode == val){
							_this.itemClassArr3 = tempObj.idLevel4
							if(_this.itemClassArr3.length==0){
								_this.itemClassShow3 = true
							}else{
								_this.itemClassShow3 = false
							}
						}
					}
			}
		},
		//格式化过长字段
		formatStr(str){
			if(str.length < 10){
				return str
			}else{
				return str.substring(0,10)+'...'
			}
	    },
		formartDate(param) {
			var time = new Date(param);
			var y = time.getFullYear();
			var m = time.getMonth()+1;
			var d = time.getDate();
			// var h = time.getHours();
			// var mm = time.getMinutes();
			// var s = time.getSeconds();
			// return y+'-'+this.add0(m)+'-'+this.add0(d)+' '+this.add0(h)+':'+this.add0(mm)+':'+this.add0(s);
			return y+'-'+this.add0(m)+'-'+this.add0(d);
		},
		add0(m){
			return m<10?'0'+m:m;
		},
		//返回
		cancle(){
			let _this = this
			this.$router.push({path:_this.oldPath});  
		},
		//查询可研报告信息
		getKYInfo(itemId){
			//根据项目id查询科研报告信息
			let _this = this
            selectReport({"itemId":itemId}).then(response => {
                console.log('response',response)//后端返回数据
                if(response.data.successful){
					// let fileName = response.data.resultValue.fileName;
					_this.reportFileList = response.data.resultValue.itemFileList
                }

            }).catch(error => {
                console.log(error)//error
            })
		},
		//查询表单头部信息
		getLabel(itemId){
			let _this = this
			_this.dialogLoading = true
			getItemInfoByIdWithLabel(itemId).then(response=>{
				_this.dialogLoading = false
				if(response.data.successful){
					let arr = response.data.resultValue.tableLable
					let tableInfo = response.data.resultValue.tableData

					
					//获取表头
					_this.tableLabel = _this.getLabelList(arr)
					//获取字典
					_this.dictMap = _this.getDictArr(arr)

					for (let i in _this.tableLabel) {
						if(tableInfo[_this.tableLabel[i].colDname]==null){
								_this.tableLabel[i].value = ''
							}else if(_this.tableLabel[i].dispType == 'DATE'){
								_this.tableLabel[i].value  = _this.formartDate(tableInfo[_this.tableLabel[i].colDname])
							}else{
								_this.tableLabel[i].value = tableInfo[_this.tableLabel[i].colDname] + '';
						}
					}

					_this.reportFileName = tableInfo.feaReportName
					_this.feaReportId = itemId

					//apprFileId, apprFileName, auditFileId, auditFileName
			        //评审文件名
					if(typeof(tableInfo.apprFileName) != "undefined"){
						_this.approvalFileName = tableInfo.apprFileName
						_this.approvalFileId = tableInfo.apprFileId
					}

					if(typeof(tableInfo.auditFileName) != "undefined"){
						_this.feaFileName = tableInfo.auditFileName
						_this.feaFileId = tableInfo.auditFileId
					}
					
					let speciality = tableInfo.speciality
					//获取一级分类下拉框
					_this.changeItemClass1(speciality)
					//子项目列表数据
					let childrenData = response.data.resultValue.tableDataList
					this.childrenLable = response.data.resultValue.tableLableList
					if(typeof(childrenData) == "undefined"||childrenData.length==0){
						_this.showChildren = false
					}else{
						_this.showChildren = true
						//从表头中抽取出字典信息列表
						_this.childrenDictMap = _this.getDictArr(_this.childrenLable);
						//获取插入或修改时的表头
						// _this.getLabelList(_this.tableLabel);
						_this.childrenColumnMap = _this.getColumnTypeArr(_this.childrenLable)
				
						_this.childrenShowData = _this.getShowDateTable(childrenData, _this.childrenDictMap,_this.childrenColumnMap);
					}
					//查询建管单位树形下拉菜单
					_this.getTreeDeptCode()
					_this.getKYInfo(itemId)
				}else{
					this.$message.error("查询项目信息失败")
				}
			})
			
		},
		getDictArr(labelList){ 
			let map = new Map();
			for(let i=0;i<labelList.length;i++){
				let temp = labelList[i];
				if(temp.codeList!=null){

					map.set(temp.colDname,temp.codeList);
				}
			}
			return map
    	},
		//获取插入和修改的表头
		getLabelList(labelList){ 
			let _this =this
			//双向数据绑定
			labelList.forEach(function (item) {
				_this.$set(item,'value','');
			})
			let common_table_info = {};//数组转对象，避免类型报错
			for(let i in labelList){
				let tempObj = labelList[i]
			
				common_table_info[i] = labelList[i];
					if (tempObj.isMandatory == 1) {
						//必填项
						let rule = new Object();
						rule.required = true;
						rule.message = tempObj.dispName + '不能为空';
						rule.trigger = 'blur';
						common_table_info[i].rule = rule;
					} else {
						let rule = new Object();
						rule.required = false;
						common_table_info[i].rule = rule;
					}
			}

			//构建是否必填规则
			return common_table_info
		},
		//获取字段类型字典
		getColumnTypeArr(labelList){
			let map = new Map();
			for(let i=0;i<labelList.length;i++){
				let temp = labelList[i];
				map.set(temp.colDname,temp.colType);
			}
			return map
		},
		//获取展示数据
		getShowDateTable(tableData, dictMap,columnDictMap) {
			let _this = this
			let arr = [];
			for (let i = 0; i < tableData.length; i++) {
				let tempObj = tableData[i];
				//拷贝对象
				let copyObj = new Object();
				//遍历获取的数据中的属性，如果属性名有和数据字典中的key相对应的，取出数据字典对应的值赋值过去
				for (let [key, value] of Object.entries(tempObj)) {
					//console.log(key + ':' + value);
					let lowerKey = key.toLowerCase()
					if (dictMap.has(key)) {
						let dictValueList = dictMap.get(key);
						for (let j = 0; j < dictValueList.length; j++) {
							let tempObj2 = dictValueList[j];
							if (tempObj2.codeValue == value) {
								copyObj[key] = tempObj2.codeName;
							}
						}
					} else if(dictMap.has(lowerKey)){
						let dictValueList = dictMap.get(key.toLowerCase);
						for (let j = 0; j < dictValueList.length; j++) {
							let tempObj2 = dictValueList[j];
							if (tempObj2.codeValue == value) {
								copyObj[key] = tempObj2.codeName;
							}
						}
					}else if(columnDictMap.get(key)=="DATE"){
						copyObj[key] = _this.formartDate(value);
					}else {
						copyObj[key] = value;
					}

				
				}
				arr.push(copyObj);
			}
			return arr;
		},
		//上传评审意见成功
	    uploadSuccess(response){
	       let _this = this
	       if(response.successful){
	            _this.$message({
	                  showClose: true,
	                  message: '上传正式评审文件成功',
	                  type: 'success'
	                });
	       }else{
	            _this.$message({
	              showClose: true,
	              message: '提交评审上传正式评审文件失败',
	              dangerouslyUseHTMLString: true,
	              type: 'error'
	            });
	       }
	               
	    },
	}
};
</script>
