import { asyncRoutes, constantRoutes } from '@/router'

/**
 * Use meta.role to determine if the current user has permission
 * @param roles
 * @param route
 */
function hasPermission(roles, route) {
  // console.log(route.meta && route.meta.role)
  if (route.meta && route.meta.role) {
    return roles.some(role => route.meta.role.includes(role))
  } else {
    return true
  }
}

/**
 * Filter asynchronous routing tables by recursion
 * @param routes asyncRoutes
 * @param roles
 */
export function filterAsyncRoutes(routes, roles) {
  const res = []

  routes.forEach(route => {
    const tmp = { ...route }

    if (hasPermission(roles, tmp)) {
      if (tmp.children) {
        tmp.children = filterAsyncRoutes(tmp.children, roles)
      }
      res.push(tmp)
    }
  })

  return res
}

const state = {
  routes: [],
  addRoutes: []
}

const mutations = {
  SET_ROUTES: (state, routes) => {
    state.addRoutes = routes
    state.routes = constantRoutes.concat(routes)
  },
  SET_ROUTESDYN: (state, routes) => {//后端传递的路由设置routes
    state.routes = routes
  }
}

const actions = {
  setBackRoutes({commit},roles){//设置后端动态路由
      commit('SET_ROUTESDYN', roles)
  },
  generateRoutes({ commit }, roles) {
    // console.log(asyncRoutes)
    return new Promise(resolve => {
      let accessedRoutes
      roles = roles.roles
      

      if (roles.includes('admin')) {//admin所有导航都可以
        accessedRoutes = asyncRoutes || []

      } else {
        accessedRoutes = filterAsyncRoutes(asyncRoutes, roles)
        // console.log('accessedRoutesaccessedRoutes',accessedRoutes)
      }
      commit('SET_ROUTES', accessedRoutes)
      // console.log(state.addRoutes)
      resolve(accessedRoutes)
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
