<template>
  <div>
         <!-- <div id="myChart" style="display:none"></div> -->
         <div ref="chart" class="myChart"></div>
  </div>
</template>

<script>
// @ is an alias to /src
import right from '@/views/right.vue'

// 引入基本模板
let echarts = require('echarts/lib/echarts')
// 引入柱状图组件
require('echarts/lib/chart/bar')
require('echarts/lib/chart/pie')
require('echarts/lib/chart/line')
require('echarts/lib/component/polar')//极坐标
require('echarts/lib/component/legend')//图例
require('echarts/lib/component/dataZoom')//缩放

// 引入提示框和title组件
require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')


export default {
  name: 'chart',
  props: ['option'],
  data() {
  	return {
      myChart:null,
  	}
  },
  watch: {
   
    option (newVal, oldVal) {
      console.log(newVal)
      const _this=this
      _this.drawLine()
     
    }
  },
  mounted(){
    console.log(this.option)
    
  },
  methods: {
    drawLine(){
        console.log('drawLine')
        let _this = this
        _this.myChart = echarts.init(_this.$refs.chart)
        // 绘制图表
        if(_this.option){
            _this.myChart.setOption(_this.option);
        }
        
    },
  }
}
</script>
