            :label="initem.codeName" 
            :name="initem.codeValue">
              
            </el-tab-pane>
            
          </el-tabs>
        </el-tab-pane>
        
      </el-tabs>
      

      <!-- <el-table :data="permissionData"
                  border
                  :span-method="objectSpanMethod">
          
          <el-table-column prop="deviceId"
                           label="一级菜单"
                           align="center">
