          </el-table-column>
          <el-table-column prop="taxerId"
                           label="三级菜单"
                           align="center">
          </el-table-column>
          
        </el-table> -->
        <el-table
          class="storage-table"
          :data="tableData"
          :span-method="idSpanMethod"
          border
          :header-cell-style="{background:'#359691',color:'#fff'}"
          :height="'60vh'"
          style="width: 100%; margin-top: 20px;">
          <el-table-column
            prop="labelCode"
            label="一级标签权重"
            width="180">
            <template slot-scope="scope" >
              <el-input v-model="scope.row.labelCode" @change="changeinp"></el-input>
            </template>
          </el-table-column>
          <el-table-column
            prop="labelName"
            label="指标分类">
          </el-table-column>
          <el-table-column
            prop="labelCode1"
            label="二级标签权重">
            <template slot-scope="scope" >
              <el-input v-model="scope.row.labelCode1" @change="changeinp"></el-input>
            </template>
          </el-table-column>
          <el-table-column
            prop="labelName1"
            label="指标名称">
          </el-table-column>
          <el-table-column
            prop="labelCode2"
            label="分数">
            <template slot-scope="scope" >
              <el-input v-model="scope.row.labelCode2" @input="changeNumber(scope.row.labelCode2,scope.row)" ></el-input>
            </template>
          </el-table-column>
          <el-table-column
            prop="labelName2"
            label="属性">
          </el-table-column>
        </el-table>
        <div style="margin-top:10px;text-align:center">
          <el-button type="primary" @click="SubmissionTable">提交</el-button>
            <!-- <el-button >取消</el-button> -->
          <el-button ><router-link to="/storageOrder">取 消</router-link></el-button>

        </div>
      </div>
      
     
    </div>
    
