        // },
      ],
    }
  },
  watch: {
   
    orderData (newVal, oldVal) {
      const _this=this
      console.log('orderData',newVal)

      _this.getTable()
     
    },
    acitve(newVal, oldVal) {
      //切换时初始化
      this.isDisabled = true
      this.start =''
      this.end =''
      this.search =''
      
    }
  },
  created(){
    
