  computed: {
    // liData.filter(data => !search || data.itemName.toLowerCase().includes(search.toLowerCase()))
    ...mapGetters([
      'roles',
      'userCity'
    ]),
    unsigns () {
        if(this.search){
          console.log("search")
          return this.liData.filter(data => 
            data.itemName.toLowerCase().includes(this.search.toLowerCase())
          )
        }else if(this.start  && !this.end){
          return this.liData.slice(this.start-1)
        }else if(this.start && this.end){
          return this.liData.slice(this.start-1,this.end)
        }else if(!this.start && this.end){
          return this.liData.slice(0,this.end)
        }
        else{
          return this.liData
        }

       
       
