    },
    setCurrent(row) {
      console.log(row)
      this.$refs.singleTable.setCurrentRow(row);
      this.selectCode = row.localCode

    },
    indexMethod(index){
      return index + 1;
    },
    submitOrder(){
      const _this =  this
      let arr = {'prjCmStItemNumVos':[]}
      _this.liData.forEach((item,idx) => {
        // let numb=''//去掉补0逻辑直接传
        // if(_this.userRole == "sheng"){
        //   numb = _this.PrefixInteger(item.localCode,4)//格式化数字
        // }else if(_this.userRole == "shi"){
        //   numb = _this.PrefixInteger(item.localCode,3)//市
        // }else{
        //   numb = _this.PrefixInteger(item.localCode,2)//县
        // }
        let numb = item.localCode
        arr.prjCmStItemNumVos.push({'itemId':item.itemId,serialNum:numb})
      })
      saveNum(arr).then(response => {
        if(response.data.successful){
          _this.$message({
            showClose: true,
            message: '保存成功',
            type: 'success'
          });
          _this.$router.push({path:'/storageManagement'})
          
        }

      })
    },
    getTable(){
      let _this = this
      // _this.loading = true
       // getFs().then(response => {//{}写需要的参数
       //  _this.loading = false
       //  if(response.data.successful){
          
          _this.liData = [..._this.orderData]
          
          console.log(_this.liData)
          _this.liData.forEach((item,idx) =>{
            _this.$set(item,'localCode',idx+1)//设置本级序号
          })
          console.log('liDataliDataliDataliData',_this.liData)
          
        // }
     // })
    },
    showInp(index){
      this.isShow = index
    },
    PrefixInteger(num, length) {
     return (Array(length).join('0') + num).slice(-length);
    },
    PlacedTop(){
      if(this.selectCode != ''){
        this.changeOrder(1,this.selectCode).then(data => {
          this.setCurrent(this.liData[0])
        })
        
        // this.setCurrent(this.liData[0])//选中第一条
      }else{
        this.$message({
          showClose: true,
          message: '请选择要移动的数据',
          type: 'warning'
        });
      }
    },
    PlacedDown(){
      if(this.selectCode != ''){
        this.changeOrder(this.liData.length + 1,this.selectCode).then(data => {
          this.setCurrent(this.liData[this.liData.length - 1])
        })
        // this.setCurrent()
        console.log(this.liData.length)
        // this.setCurrent(this.liData[this.liData.length - 1])//选中最后一条
      }else{
        this.$message({
          showClose: true,
          message: '请选择要移动的数据',
          type: 'warning'
        });
      }
    },
    up3(){//上移
      console.log(this.selectCode)
      if(this.selectCode != ''){
        this.changeOrder(this.selectCode - 1,this.selectCode).then(data => {
          this.setCurrent(data)
        })

      }else{
        this.$message({
          showClose: true,
          message: '请选择要移动的数据',
          type: 'warning'
        });
      }
    },
    down3(){//下移3
      if(this.selectCode != ''){
        this.changeOrder(this.selectCode + 1,this.selectCode).then(data => {
          this.setCurrent(data)
        })

      }else{
        this.$message({
          showClose: true,
          message: '请选择要移动的数据',
          type: 'warning'
        });
      }
    },
    onChangeOrder(val,index){
      this.changeOrder(val,index).then(data => {
        this.setCurrent(data)
      })
    },
    changeOrder(val,index){//把第index条数据插入到第val的位置
      return new Promise((resolve, reject) => {
        const _this = this
        _this.isDisabled = false
        val = Number(val)
        console.log('val',val,'index',index)
        if(val < 1){//设置极限值
          val = 1
        }
        if(val > _this.liData.length){
          val = _this.liData.length
        }
        let item = _this.liData.splice(index-1, 1)[0]//选中要移动的数据

        _this.liData.splice(val-1, 0, item)
        
        _this.liData.forEach((liItem,idx) => {//重置排序号 index
          let codeArr =[]
          liItem.localCode = idx + 1//本地序号 
          codeArr[0] = liItem.grandSort.substring(0,2)//专业号

          codeArr[1] = liItem.grandSort.substring(2,6)//省
          codeArr[2] = liItem.grandSort.substring(6,9)//市
          codeArr[3] = liItem.grandSort.substring(9,11)//县
          console.log(codeArr)
          if(_this.userRole == "sheng"){
            codeArr[1] = _this.PrefixInteger(liItem.localCode,4)//格式化数字
          }else if(_this.userRole == "shi"){
            codeArr[2] = _this.PrefixInteger(liItem.localCode,3)//市
          }else{
            codeArr[3] = _this.PrefixInteger(liItem.localCode,2)//县
          }

          liItem.grandSort = codeArr.join('')
          console.log(liItem.grandSort )

        })

        _this.isShow = ''
        resolve( this.liData[val-1])//返回需要选中的数据
        console.log(this.liData)
      })
    },
   
