<template>
  <div class="container">
    
    <div class="main clearfix" :style="{'margin-left':$store.state.app.isCollapse ? '84px':'240px'}">
      
      <div class="main_container">
        <querys @changeQuery="changeQuery" :showSwitch = "false">
          <div slot="queryTabs">
            <div class="query_tab">
                <div class="query_label">专家姓名</div>
                <div class="query_select">
                  <el-input v-model="getTableData.expertName" clearable @change="selectItem" size="small" placeholder="请输入专家姓名"></el-input>
                </div>
              </div>
<!-- 
              <div class="query_tab">
                <div class="query_label">专业类别</div>
                <div class="query_select">
                  <el-select  v-model="getTableData.professional" clearable v-if="dictMap!=undefined"
                       @change="selectItem" placeholder="请选择" size="small">
                    <el-option
                      v-for="item in dictMap.get('professional')"
                      :key="item.codeValue"
                      :label="item.codeName"
                      :value="item.codeValue">
                    </el-option>
                  </el-select>
                </div>
              </div> -->

              <div class="query_tab">
                <div class="query_label">性别</div>
                <div class="query_select">
                  
                  <el-select  v-model="getTableData.expertSex" clearable v-if="dictMap!=undefined"
                       @change="selectItem" placeholder="请选择" size="small">
                    <el-option
                      v-for="item in dictMap.get('expertSex')"
                      :key="item.codeValue"
                      :label="item.codeName"
                      :value="item.codeValue">
                    </el-option>
                  </el-select>
                </div>
              </div>

          </div>
          <div slot="queryBtns">
            <!-- <el-upload
                class="upload"
                :action="GLOBALURL+'/prjStExperts/importExpertsFile'"
               :on-success="uploadSuccess"
                :multiple='false'
                :show-file-list="false">
                <el-button size="small" type="primary">导入</el-button>
                
              </el-upload> -->
              <el-button type="primary" size="small" v-loading="exportBtn" @click="exportExperts">
                  导出
               </el-button>
              <!-- <el-button type="primary" size="small" ><a :href="GLOBALURL+'/prjStTmpl/export?templateType=8&token='+token">导出模版</a></el-button> -->
              <el-button type="primary" size="small" @click="showDialog(null)">新增</el-button>
              <el-button type="primary" size="small" :loading="delLoading" @click="del">删除</el-button>
          </div>
        </querys>
      
        
        <el-table 
          :data="this.tableDataShow"
          v-loading="loading"
          @selection-change="handleSelectionChange"
          :height="tableHeight"
          border
          :header-cell-style="{background:'#359691',color:'#fff'}"
          row-class-name="row"
          cell-class-name="column"
          :highlight-current-row="true"
          ref="expertTable"
          >
            <el-table-column
              type="selection"
              width="45">
            </el-table-column>
            <el-table-column
                v-for="(item, index) in tableLabel"
                v-if="item.colDname != 'expertName'&&item.showType==1"
                :key="index" 
                :prop="item.colDname"
                :label="item.dispName"
                min-width='150'
                :show-overflow-tooltip="true">
            </el-table-column>
            <el-table-column v-else-if="item.colDname == 'expertName'" :label="item.dispName" min-width='150'
            :show-overflow-tooltip="true">
              <template slot-scope="scope" >
                  <span class="blue" @click="showDialog(scope.row)">{{scope.row.expertName}}</span>
                </template>
            </el-table-column>
        </el-table>
        <div class="page">
          
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[10,20,30,50]"
            :page-size="20"
            layout="sizes, total, prev, pager, next, jumper"
            :total="totalNum">
          </el-pagination>
        </div>

      </div>
    </div>
    <!-- <right></right> -->
    <!-- 新增 -->
    <el-dialog :title="dialogTitle" class="experts-dialog" :close-on-click-modal = "false" 
    width="70%" :visible.sync="dialogFormVisible"
    :modal-append-to-body='false'>    
        <el-form :model="tableLabel" ref="lableForm" :inline="true" label-position="right" v-loading="dialogLoading">
          <el-form-item v-for="(domain, index) in tableLabel"
            :key="index" 
            :label="domain.dispName" :label-width="formLabelWidth"
            :prop=" index + '.value'"
            :rules="domain.rule"
            v-if="domain.dispType!='text'" >
            <el-input
              v-if="domain.dispType=='number'" 
              type="number"
              placeholder="请输入内容"
              v-model="domain.value"
            >
            </el-input>
            <el-input v-if="domain.dispType=='string' " :placeholder="domain.dispName" v-model="domain.value" autocomplete="off" ></el-input>
            <el-select v-model="domain.value" placeholder="请选择" v-if="domain.dispType=='dropdown'" style="width:100%;">
              <el-option
                v-for="item in domain.codeList"
                :key="item.codeId"
                :label="item.codeName"
                :value="item.codeValue">
              </el-option>
            </el-select>

            <el-date-picker
              v-if="domain.dispType=='date'"
              v-model="domain.value"
              type="date"
              placeholder="选择日期" >
            </el-date-picker>
          </el-form-item>
          <el-form-item v-for="(domain, index) in tableLabel"
            :key="index" 
            :label="domain.dispName" :label-width="formLabelWidth"
            :prop=" index + '.value'"
            :rules="domain.rule"
            v-if="domain.dispType=='text'" >
          <el-input
              v-if="domain.dispType=='text'" 
              type="textarea"
              placeholder="请输入内容"
              v-model="domain.value"
            >
            </el-input>
          </el-form-item>
          
        </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" :loading="submitLoading" @click="submitForm('lableForm')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
// @ is an alias to /src
import right from '@/views/right.vue'
import SelectTree from "@/components/treeSelect.vue";
import {
getExperts,savePrjStExperts,deletePrjStExperts,getExpertById,ExportExperts,getTreeDeptCode,getUserDept} from '@/api/data'
import querys from '@/components/querys'

export default {
  name: 'questions',
  data() {
  	return {
      submitLoading:false,
      delLoading:false,
      token:'',
      deptCode:"",
			isClearable:true,      // 可清空（可选）
			isAccordion:true,      // 可收起（可选）
			valueId:"",            // 初始ID（可选）
			props:{                // 配置项（必选）
				value: 'id',
				label: 'name',
				children: 'children',
				code: 'deptCode'
				// disabled:true
			}, 
      tableHeight:'',
      isShowQuery:false,
      isShowFilter:false,
      dialogLoading:false,
      loading:false,
      delData:{
        "ids": [],
      },
      lableList:{},
      totalNum:0,
      currentPage:1,
      currentNum:20,
      year: '',
      dialogFormVisible:false,
      form: {
        
      },
      options: [],
      value:'',
      formLabelWidth: '40%',
      input: '',
  		// 子组件的表格数据
  		tableData: [],
  		// 子组件的表头数据
      tableLabel: [],
      //表格展示数据
      tableDataShow:[],
      getTableData:{
        'pageIndex':1,
        'pageSize':20,
        'professional':'',
        'orgNo':'',
        'orgDept':'',
        'expertSex':'',
        'expertName':'',
        'deptCode':''
      },
      dictMap: new Map,
      tempId:'',
      columnMap: new Map,
      dialogTitle:"",
      //导出专家id数组
      expertIdArr:[],
      exportBtn:false,
      deptArr:[]
  	}
  },
  components: {
    querys,
    SelectTree
  },
  created(){
    this.tableHeight=window.innerHeight-220;
  },
  mounted(){
    this.token = sessionStorage.getItem('token');
    this.getTable(1,20)
    // this.getTreeDeptCode()
  },
  watch:{
		deptCode(){
      this.getTableData.deptCode = this.deptCode
      this.currentPage = 1
			this.getTable(1,this.currentNum)
		}
	},
  methods: {
    getValue(value){
			this.deptCode = value
    },
		//获取单位树形结构
		getTreeDeptCode(){
			let _this = this
			getUserDept().then(response=>{
				let valueId = response.data.resultValue
				getTreeDeptCode().then(response=>{
					console.log(response)
					if(response.data.successful){
						_this.valueId = valueId
						_this.deptArr = response.data.resultValue
					}
				})
			})
		},
    //导出专家
    exportExperts(){
        let _this = this
        let expertParam = _this.expertIdArr.join(',')
        _this.exportBtn = true;
        let formData = new FormData()
        formData.append("expertIs",expertParam)
        ExportExperts(formData).then(res=>{
            _this.$refs.expertTable.clearSelection()
            _this.exportBtn = false;
            _this.download(res.data, '专家信息.xlsx');
        })
    },
    //处理导出响应结果
		download(data, fileName) {
			console.log(data);
			const link = document.createElement('a');
			let blob = new Blob([data], { type: 'application/vnd.ms-excel' });
			link.style.display = 'none';
			link.href = URL.createObjectURL(blob);
			link.setAttribute('download', fileName);
			document.body.appendChild(link);
			link.click();
			document.body.removeChild(link);
		},
    changeQuery(state,height){
      if(state){
        this.tableHeight = height
        this.isShowQuery = true
      }else{
        this.tableHeight = height
        this.isShowQuery = false

      }
    },
    switchFilter(){
      this.isShowFilter = !this.isShowFilter
    },
    isRequired(data){
      console.log(data)
      if(data && data==1){
        
        return true
      }else{
        return false
      }
    },
    // 搜索框
    selectItem(){
      this.currentPage = 1;
      this.getTable(1,this.currentNum)
    },
    //input输入查询--没用
    changeProbName(val){
      // if(val != ""){
        this.getTable()
      // }
    },
    // 没用到的方法
    uploadSuccess(response, file, fileList){
      let _this =  this
      if(response.successful){
        _this.$message({
          showClose: true,
          message: '导入成功',
          type: 'success'
        });
        _this.currentPage = 1;
        _this.getTable(1,_this.currentNum)
      }else{
        _this.$message({
          showClose: true,
          message: '导入失败',
          dangerouslyUseHTMLString: true,
          type: 'error'
        });
      }
      
    },
    handleSelectionChange(val){
      let _this = this
      _this.delData = {
        "ids": [],
      }
      _this.expertIdArr = []
      val.forEach(function(item,idx){
        _this.delData.ids.push(item.expertId)
        _this.expertIdArr.push(item.expertId)
      })
    },
    del(){
      let _this = this
      if(_this.delData.ids.length){
          this.$confirm('此操作将永久删除文件, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
              _this.loading =true
              _this.delLoading = true
              deletePrjStExperts(_this.delData).then(response => {
                _this.delLoading = false
                  console.log(response)
                if(response.data.successful){
                  
                    this.$message({
                      showClose: true,
                      message: '删除成功',
                      type: 'success'
                    });
                    _this.currentPage = 1;
                    _this.getTable(1,_this.currentNum)

                }else{
                  this.$message({
                      showClose: true,
                      message: '删除失败',
                      dangerouslyUseHTMLString: true,
                      type: 'error'
                    });
                }
                _this.loading =false

            })

          }).catch(() => {
            this.$message({
              type: 'warning',
              message: '已取消删除'
            });          
          });
          
        }else{
          this.$message({
                  showClose: true,
                  message: '请选择至少一条数据',
                  type: 'warning'
                });
        }
      
    },
    submitForm(formName) {

      let _this = this
      console.log(_this.tempId)
      _this.$refs[formName].validate((valid) => {
        if (valid) {

          let subdata = {"items": []}
          

          console.log(_this.tableLabel)

          let arr={}

          if(_this.tempId.length){
            arr['expertId']= _this.tempId
          }
          
          Object.keys(_this.tableLabel).forEach(function (key) {
            console.log(key,_this.tableLabel[key])

            arr[_this.tableLabel[key].colDname] = _this.tableLabel[key].value
          })
          subdata.items.push(arr)
          console.log('subdata',subdata)
          _this.dialogFormVisible = false

          _this.loading =true
          _this.submitLoading = true
          savePrjStExperts(subdata).then(response => {
            _this.submitLoading = false
            console.log(response)
            if(response.data.successful){
              this.$message({
                showClose: true,
                message: '操作成功',
                type: 'success'
              });
              _this.tempId = ""
              //如果修改弹窗点击确定之后停留在当前页，新增到第一页
              if(_this.dialogTitle == "修改"){
                _this.getTable(_this.currentPage,_this.currentNum)
              }else{
                _this.currentPage = 1;
                _this.getTable(1,_this.currentNum)
              }
              
            }else{
              this.$message({
                showClose: true,
                message: '操作失败',
                dangerouslyUseHTMLString: true,
                type: 'error'
              });
            }
            _this.loading =false

          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    showDialog(data){
      let _this =this
      _this.dialogFormVisible = true
      if(data!=null){//修改
        _this.dialogTitle = "修改"
        
        _this.tempId = data.expertId
        getExpertById(data.expertId).then(response =>{
              if(response.data.successful){
                    let obj = response.data.resultValue.items[0]

                    for (let i in _this.tableLabel) {

                      if(obj[_this.tableLabel[i].colDname]==null){
                          _this.tableLabel[i].value = ''
                      }else{
                        _this.tableLabel[i].value = obj[_this.tableLabel[i].colDname] + '';
                      }
                      
                    }
              }else{
                this.$message({
                      showClose: true,
                      message: '查询失败',
                      dangerouslyUseHTMLString: true,
                      type: 'error'
                    });
              }

        })
      }else{//新增
          _this.dialogTitle = "新增"
          Object.keys(_this.tableLabel).forEach(function (key) {
            _this.tableLabel[key].value = ""
          })
      }
      
    },
    //获取当前每页条数
    handleSizeChange(val) {
      this.currentNum = val;
      this.getTable(this.currentPage,val)
    },
    //获取当前页码
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage = val
      this.getTable(val,this.currentNum)
    },
    getTable(page,size){
      let _this = this
      _this.loading = true
      let filter = ''
      //拼接查询条件
      for(let [key, value] of Object.entries(_this.getTableData)) {
          if(key=="pageIndex"||key=="pageSize"||value==''){
              continue
          }
          console.log(key + ':' + value);
          filter+='&'+key+'='+value
      }
      filter = filter.substr(1)
      let searchParam = {}
      searchParam.pageIndex = page
      searchParam.pageSize = size
      if(filter!=""){
          searchParam.filter = filter     
      }
      
      getExperts(searchParam).then(response => {//{}写需要的参数
          console.log('response',response)//后端返回数据
          if(response.data.successful){
              _this.totalNum = response.data.resultValue.totalNum
              let arr = response.data.resultValue.tableLable
              _this.tableLabel = _this.getLabelList(arr)
              _this.dictMap = _this.getDictArr(arr)
              _this.columnMap = _this.getColumnTypeArr(arr)

              _this.tableData = response.data.resultValue.tableData
              _this.tableDataShow = _this.getShowDateTable(_this.tableData,_this.dictMap,_this.columnMap)
              //从表头中抽取出字典信息列表
                _this.getDictArr(_this.tableLabel)
          }
          _this.loading = false
          
        }).catch(error => {
          console.log(error)//error
        })
        
    },
    //获取展示数据
    getShowDateTable(tableData,dictMap,columnTypeMap){
      let _this = this
      let arr = [];
      for(let i=0;i<tableData.length;i++){
        let tempObj = tableData[i];
        //拷贝对象
        let copyObj = new Object();
        //遍历获取的数据中的属性，如果属性名有和数据字典中的key相对应的，取出数据字典对应的值赋值过去
        for (let [key, value] of Object.entries(tempObj)) {
          console.log(key + ':' + value);
          if(dictMap.has(key)){
            let dictValueList = dictMap.get(key);
            for(let j=0;j<dictValueList.length;j++){
              let tempObj2 = dictValueList[j];
              if(tempObj2.codeValue==value){
                copyObj[key] = tempObj2.codeName;
              }
            }
          }else if(columnTypeMap.get(key)=="DATE"){
               copyObj[key+"Rel"] = value
               let timeStr = _this.formartDate(value)
               copyObj[key] = timeStr;
          }else{
            copyObj[key] = value;
          }
        }
        if(tempObj.expertAge==0){
            copyObj.expertAge = ''
        }
        arr.push(copyObj);
      }
      return arr;
    },
        //获取字典
    getDictArr(labelList){ 
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        if(temp.codeList!=null){
          map.set(temp.colDname,temp.codeList);
        }
      }
      return map
    },
     //获取字段类型字典
    getColumnTypeArr(labelList){
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        map.set(temp.colDname,temp.colType);
      }
      return map
    },
    //获取插入和修改的表头
    getLabelList(labelList){ 
      let _this =this
      labelList.forEach(function (item) {
        _this.$set(item,'value','');
      })
      let common_table_info = {};//数组转对象，避免类型报错
      for(let i in labelList){
        let tempObj = labelList[i]
        common_table_info[i] = labelList[i];
        	if (tempObj.isMandatory == 1) {
            //必填项
            let rule = new Object();
            rule.required = true;
            rule.message = tempObj.dispName + '不能为空';
            rule.trigger = 'blur';
            common_table_info[i].rule = rule;
				} else {
            let rule = new Object();
            rule.required = false;
            common_table_info[i].rule = rule;
				}
      }

      //构建是否必填规则
			

      return common_table_info
    },
    
  }
}
</script>
