  .experts-dialog .el-form--inline .el-form-item {
    width: 49%;
    margin-right: 0;
  }


  .experts-dialog .el-form-item__content {
    width: 60% !important;
  }

  .experts-dialog .el-dialog .el-input__inner,
  .experts-dialog .el-dialog .el-textarea__inner,
  .experts-dialog .el-dialog .el-select>.el-input {
    width: 100% !important;
  }
  
</style>
