<template>
  <div class="container">
            <template slot="title">
                <div style="font-size: large"><span>{{itemName}}</span></div>
            </template>
            <el-form :model="itemLabel" label-position="right" ref="lableFormQuestion"  v-loading="dialogLoading">
                <el-form-item v-for="(domain, index) in itemLabel"
                            :key="domain.colDname"
                            :label="domain.dispName"
                            :label-width="formLabelWidth"
                            :prop="index + '.value'"
                            :rules="domain.rule"
                            v-if="domain.showType != 3">
                <el-col :span="8" v-if="domain.dispType=='DATE'">
                    <el-date-picker
                        v-model="domain.value"
                        type="year"
                        value-format="yyyy"
                        placeholder="选择日期" disabled>
                    </el-date-picker>
                </el-col>
                <el-col :span="8" v-if="domain.dispType=='dropdown'" >
                    <el-select 
                            v-model="domain.value" :placeholder="domain.dispName" clearable disabled>
                    <el-option
                            v-for="Titem in itemDictMap.get(domain.colDname)"
                            :key="Titem.codeValue"
                            :label="Titem.codeName"
                            :value="Titem.codeValue">
                    </el-option>
                    </el-select>
                </el-col>
                
                <el-col :span="8" v-if="domain.dispType=='text'">
                     <el-input  class="diy"  type="textarea" v-model="domain.value" :placeholder="domain.dispName" disabled></el-input>
                </el-col>
                <el-col :span="8" v-if="domain.dispType=='number'">
                    <el-input
						type="number"
						placeholder="请输入内容"
						v-model="domain.value"
						style="width:300px"
                        disabled
					></el-input>
                </el-col>
                <el-col :span="8" v-if="domain.dispType==''||domain.dispType==null">
                      <el-input  v-model="domain.value" autocomplete="off" size="mini" :placeholder="domain.dispName" disabled></el-input>
                </el-col>
                </el-form-item>
                <el-form-item prop="filename" label="可研报告文件">
                        <a style="color: #409EFF;" :href="GLOBALURL+'/prjCmFeaPrep/downloadReport?itemId='+feaReportId+'&token='+token" >{{reportFileName}}</a>
                    <!-- <el-link type="primary" @click="downloadReport">{{reportFileName}}</el-link> -->
                </el-form-item>

            </el-form>
    </div>

</template>
<script>
import {getItemInfoByIdWithLabel,downloadReport} from '@/api/data'
export default {
    name: 'itemDialog',
    data() {
        return {
            token:'',
            formLabelWidth: '120px',
            //项目表头数据
            itemLabel:{},
            //保存或修改数据
            insertParam: Object,
            //表头字典map
            itemDictMap: new Map(),
            //列表展示数据
            itemTableDataShow:[],
            DialogTitle:"",
            dialogLoading: false,
            //可研报告文件名
            reportFileName: "",
            //项目名称
            itemName:"",
            //可研报告文件id
            feaReportId:""
        }
        
    },
    props:{
        itemId:{
            type: String
        }
    },
    watch:{
        // alert(this.itemName)
        itemId(val){
            this.updateItem(val)
        }
    },
    mounted(){
        this.token = sessionStorage.getItem('token');
        this.updateItem(this.itemId)
    },
    methods: {
        //下载可研报告
        formartDate(param) {
			var time = new Date(param);
			var y = time.getFullYear();
			var m = time.getMonth()+1;
			var d = time.getDate();
			// var h = time.getHours();
			// var mm = time.getMinutes();
			// var s = time.getSeconds();
			// return y+'-'+this.add0(m)+'-'+this.add0(d)+' '+this.add0(h)+':'+this.add0(mm)+':'+this.add0(s);
			return y+'-'+this.add0(m)+'-'+this.add0(d);
		},
		add0(m){
			return m<10?'0'+m:m;
		},
        //下载可研报告
        downloadReport(){
            let _this = this
            _this.loading = true
            downloadReport({"itemId":_this.feaReportId}).then(res =>{
                let filename = _this.reportFileName
                _this.loading = false
                console.log('download res',res);
                //通过后台返回 的word文件流设置文件名并下载
                var blob = new Blob([res.data], { type: 'application/vnd.openxmlformats-officedocument.wordprocessingml.document;charset=utf-8'}); //application/vnd.openxmlformats-officedocument.wordprocessingml.document这里表示doc类型
                var downloadElement = document.createElement('a');
                var href = window.URL.createObjectURL(blob); //创建下载的链接
                downloadElement.href = href;
                downloadElement.download =filename; //下载后文件名
                document.body.appendChild(downloadElement);
                downloadElement.click(); //点击下载
                document.body.removeChild(downloadElement); //下载完成移除元素
                window.URL.revokeObjectURL(href); //释放掉blob对象
            })
        },
        //展示项目信息
        updateItem(itemId){
            let _this =this
            _this.DialogTitle = "项目";
            _this.dialogFormVisible = true
            _this.dialogLoading = true
            getItemInfoByIdWithLabel(itemId).then(response => {//{}写需要的参数
              console.log('response',response)//后端返回数据

              if(response.data.successful){
                  //获取项目表头信息
                  let labelArr = response.data.resultValue.tableLable
                  _this.itemLabel = _this.getLabelList(labelArr)
                  //获取项目字典
                  _this.itemDictMap = _this.getDictArr(labelArr)
                  //项目详细信息
                  let itemData = response.data.resultValue.tableData;
                //   _this.itemName = itemData.itemName
                  //为项目信息赋值
                  for(let i in _this.itemLabel){
                        if(itemData[_this.itemLabel[i].colDname]==null){
                            _this.itemLabel[i].value = ''
                        }else if(_this.itemLabel[i].dispType == 'DATE'){
                            console.log(itemData[_this.itemLabel[i].colDname])
                            _this.itemLabel[i].value  = _this.formartDate(itemData[_this.itemLabel[i].colDname])
                        }else{
                            _this.itemLabel[i].value = itemData[_this.itemLabel[i].colDname]+''
                        }
                  }
                   //如果是项目包，则展示子项目列表
                  if(itemData.itemType=='1'){
                        _this.childItemDataShow = true
                        //如果该项目包包含子项目，则渲染子项目列表
                        if(itemData.childrenItemList!=null&&itemData.childrenItemList.length>0){
                            _this.childItemShowTableData = _this.getShowDateTable(itemData.childrenItemList,_this.dictMap)
                        }
                  }else{
                        _this.childItemDataShow = false
                  }
                  //找出项目可研报告文件名
                  _this.reportFileName = itemData.feaReportName
                  _this.feaReportId = itemId
              }
              
              _this.dialogLoading = false
          }).catch(error => {
              console.log(error)//error
              _this.dialogLoading = false
          })
        
        },
        //获取数据字典
        getDictArr(labelList){ 
            let map = new Map();
            for(let i=0;i<labelList.length;i++){
                let temp = labelList[i];
                if(temp.codeList!=null){
                map.set(temp.colDname,temp.codeList);
                }
            }
            return map
        },
        //获取展示数据
        getShowDateTable(tableData,dictMap){
            let arr = [];
            for(let i=0;i<tableData.length;i++){
                let tempObj = tableData[i];
                //拷贝对象
                let copyObj = new Object();
                //遍历获取的数据中的属性，如果属性名有和数据字典中的key相对应的，取出数据字典对应的值赋值过去
                for (let [key, value] of Object.entries(tempObj)) {
                console.log(key + ':' + value);
                if(dictMap.has(key)){
                    let dictValueList = dictMap.get(key);
                    for(let j=0;j<dictValueList.length;j++){
                    let tempObj2 = dictValueList[j];
                    if(tempObj2.codeValue==value){
                        copyObj[key] = tempObj2.codeName;
                    }
                    }
                }else{
                    copyObj[key] = value;
                }
                }
                arr.push(copyObj);
            }
            return arr;
        },
        //获取插入和修改的表头
        getLabelList(labelList){ 
            let _this =this
            labelList.forEach(function (item) {
                _this.$set(item,'value','');
            })
            let common_table_info = {};//数组转对象，避免类型报错
            for(let i in labelList){
                common_table_info[i] = labelList[i];
                let tempObj = labelList[i];
                //构建是否必填规则
                if(tempObj.isMandatory==1){ //必填项
                    let rule = new Object;
                    rule.required = true
                    rule.message = tempObj.dispName+'不能为空'
                    rule.trigger = 'blur'
                    common_table_info[i].rule = rule
                }else{
                    let rule = new Object;
                    rule.required = false
                    common_table_info[i].rule = rule
                }
            }
            return common_table_info
        },
    }
}
</script>
<style scoped lang="stylus">
    
    .del{
        color:#ff0000
    }
    
</style>