			//调用项目生成器路径
		    getNameUrl: '/pcinfo/resUniname/pages/pgUnifiedNameDefine.jsp?openType=1&appID=00&applyID=2',
			newItemName: false,
			//项目专项
			specClass:"",
			//专项名称
			specClassName:"",

			dictMap: new Map,
			tempId:'',
			columnMap: new Map,
			title:"",
			totalLableArr:[],
			dialogLoading:false,
			formLabelWidth: '45%',
			//选择问题或需求列表
			innerVisible: false,
			//内层弹框标题
			questionTitle: '',
			//问题或需求列表表头
			questionLabelList: [],
			//问题或需求列表数据
			questionTableData: [],
			//问题或需求列表返回总页数
			questionListTotalNum: 0,
			//问题或需求字典
			questionDictMap: new Map(),
			//问题或需求展示数据
			questionShowData: [],
			//被选中问题或需求编码
			selectedQuestionCode: [],
			currentPage: 1,
			currentNum:5,
			//问题或需求列表加载
			loading:false,
			//问题名称
			probName:"",
			//专业类别禁用 true、启用 false
			specEnabled: true, 
			//父项目id，新增项目包时用
			parentItemId: null,
