			deptCodeT:'',
			LGTypeArr:[],
			//固定资产级联
			tableLabelArr:[],
			//tab页类表
			tabMap: new Map(),
			// 子组件的表格数据
			tableData: [],
			// 子组件的表头数据
			tableLabel: {},
			tableLabelLX: {},
			tableLabelKY: {},
			tableLabelCB: {},
			//可浏览的tab页
			activeTab:'first',
			//项目所属阶段 0 浏览，1立项，2可研，3储备
			phash:'',
			//加载条
			dialogLoading1:false,
			dialogLoading2:false,
			dialogLoading3:false,
			searchCode:'',
			itemIdT:'',
			tabHeight:"",
			/* 结束时间不能大于开始时间 */
            endTime: {
               disabledDate: this.disabledEnd
            },
			startTime: {
				disabledDate: this.disabledStart
			},
			//项目状态
			itemPhaseDet:''

		};
	},
	created (){
		
		this.$store.dispatch("itemadd/setItemParamStatus")
		
	},
    mounted (){
		// 清空表单
		
        //为专项类型赋值
        console.log('showData'+this.showData.phash)
		this.specClass = this.showData.specClass
		this.specClassName = this.showData.specClassName
		this.tempItemId = this.showData.itemId
		this.parentItemId = this.showData.parentItemId
        this.tableLabelArr = this.showData.tableLabelArr
        this.phash = this.showData.phash
		this.showDrawer = this.showAddDialog
		this.parentItemLevel = this.showData.parentItemLevel
		this.itemPhaseDet = this.showData.itemPhaseDet
		this.isChangeQuery = this.isShowQuery
		this.tabHeight = this.tableHeightTab
		this.isHasChildren = this.hasChildren
		this.isHasParent = this.hasParent
		console.log(this.isHasChildren )
		console.log(this.isHasParent)
		
        if(this.phash == "2"){
			this.activeTab = "second"
		}else if(this.phash == "3"){
			this.activeTab = "third"
		}else{
			this.activeTab = "first"
		}
		// 如果点击展示出右拉框的时候是修改
		if(this.tempItemId != "" && this.tempItemId != null && this.tempItemId != undefined){
			this.isToggle = true;
		}
		this.token = sessionStorage.getItem('token');
        //构造表单
		this.getLabel()
	},
	watch: {
		itemId() {
			this.tempItemId = this.showData.itemId
			console.log("itemid变化")
			//构造表单
			this.getLabel()
		},
		isShowQuery() {
			if(this.isShowQuery){
				this.tabHeight = (this.tableHeightTab-55)+"px"
			}else{
				this.tabHeight = (this.tableHeightTab-45)+"px"
			}
		}
	},
    computed: {
		...mapState({
			 addItemParams:state=>state.itemadd.addItemParams,
			 isSaved:state=>state.itemadd.isSaved
		})
		
	},
	created() {
		
		this.tabHeight = (this.tableHeightTab-45)+"px"
		console.log(this.tabHeight)
		
	},
	components: {
		SelectTree
	},
	methods: {
		changeQuery(state, height) {
                if (state) {
                    this.tableHeight = height
                    this.isShowQuery = true
                } else {
                    this.tableHeight = height
                    this.isShowQuery = false
                }
            },
		selectItemXQ() {
			this.currentPage = 1
			if(this.detailFlag){
				this.questionDetail(this.codeStr,1,this.currentNum)
			}else{
				this.getXQList(1,this.currentNum)
			}
			
		},
		selectItemWT() {
			this.currentPage = 1
			if(this.detailFlag){
				this.questionDetail(this.codeStr,1,this.currentNum)
			}else{
				this.getWTList(1,this.currentNum)
			}
			
		},
		// 结束时间不能早于开始时间
		disabledEnd(time) {
			if(this.getValue("startDate") != ""&& this.getValue("startDate") != undefined && this.getValue("startDate") != null){
				return time.getTime() < new Date(this.getValue("startDate")).getTime()
			}
		},
		disabledStart(time) {
			if(this.getValue("cmplDate") != "" && this.getValue("cmplDate") != undefined && this.getValue("cmplDate") != null){
				return time.getTime() > new Date(this.getValue("cmplDate")).getTime()
			}
		},
		// 删除文件--可研信息
		deleteFile(fileId,type) {
			let _this = this
			let fileParams = {
				fileId:fileId,
				type:type,
				itemId:_this.tempItemId
			}
			_this.$confirm('确定删除此文件吗?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					deleteFeaFile(fileParams).then(res => {
						if(res.data.successful){
							// 成功之后刷新文件列表
							
							if(type == 1){
								let param = {
									itemId:_this.tempItemId,
									itemPhaseDet:'3'
								}
								updateItemPhaseDet(param).then(res => {
									if(res.data.successful){
										_this.getKYInfo(_this.tempItemId)
										_this.$parent.getTable(1,20)
									}else{
										_this.$message({
											showClose: true,
											message: "删除失败",
											type: 'error'
										});
									}
									
								})
							}else{
								_this.getKYInfo(_this.tempItemId)
							}
						}
					})
				})
			
		},
		getRowKeys(row){

			if(this.itemSourseFlag ==1){
                // this.rowKeys.push(row.probCode)
				return row.probCode;//返回唯一ID

			}else if(this.itemSourseFlag ==2){
                // this.rowKeys.push(row.dmdCode)

				return row.dmdCode;//返回唯一ID
			}
			
		},
        closeDrawer() {
            this.$store.dispatch('itemadd/setPathChangeActive', false)
            this.showDrawer = false;
			this.$emit('changeStatus',this.showDrawer)
        },
		//各专项change事件
		commonChange(domain,val){

			let _this = this
			
			//级联效果
			if(domain.dispType=='cascadeDropdown'){
				 if(domain.detailColDname!=null){
					 let childLevel = domain.detailColDname.split(",")
					 if(childLevel.length>0){
						//清空下级的数据
						for(let i=0;i<childLevel.length;i++){
							let levelName = childLevel[i]
							_this.dictMap.set(levelName,new Array())
							_this.setValue(levelName,null)
						}
						//查询下级所有选项
						let param = {}
						param.speciality = _this.specClass
						param.codeValue = val
						param.groupCode = domain.groupCode
						//查询groupCode
						let groupCode = null
						if(domain.codeList!=null){
							for(let i=0;i<domain.codeList.length;i++){
								let temp = domain.codeList[i];
								if(temp.codeValue==val){
									groupCode = temp.groupCode
									
									break
								}
							}
							if(groupCode!=null){
								param.groupCode = groupCode
							}
							queryChildInfo(param).then(res=>{
								console.log(res)
								if(res.data.successful){
									console.log(res.data.resultValue)
									if(!_this.tempItemId){//如果是新增，过滤enabled不为1的码值
										let tempArr = []
										let tempArr2 = res.data.resultValue
										if(tempArr2.length>0){
											for(let i=0;i<tempArr2.length;i++){
												let tempCode = tempArr2[i];
												if(tempCode.enabled==1){
													tempArr.push(tempCode)
												}
											}
										}

										res.data.resultValue = tempArr
									}


									_this.dictMap.set(childLevel[0],res.data.resultValue)
									//给二级赋值下拉框
									_this.setValue(childLevel[0],res.data.resultValue,true)
									this.$forceUpdate()
								}
							})
							
						}
						
					}
				 } 
			}
			//各个专项特异联动效果配置
			switch(_this.specClass){
				//零购专项联动逻辑
				case '08':
					//零星购置需求，零购项目，数量大于1，项目类型自动判断为打捆。
					if(domain.colDname=='quantity'){
						if(val>1){
							_this.setValue("lxItemType","1")
						}
					}
				case '01':
					//电网基建电网基建类型和电压等级联动
					if(domain.colDname=='voltLevel'){
						getSpecDetailClass({"voltCode":val}).then(res=>{
							if(res.data.successful){
								let specDetailClassCode = res.data.resultValue
								let specDetailClassArr = _this.dictMap.get("specDetailClass")
								let tempFlag = false
								for(let i=0;i<specDetailClassArr.length;i++){
									let tempCode = specDetailClassArr[i]
									if(tempCode.codeValue==specDetailClassCode){
										tempFlag = true
										break
									}
								}
								//如果当前用户的电网基建类型中包含返回的电网基建类型码值，则将返回的码值
								//赋值到电网基建类型中，否则弹出提示，并清空所选电压等级
								if(tempFlag){
									_this.setValue("specDetailClass",specDetailClassCode)
								}else{
									_this.$message.warning("您没有所选电压的等级对应的权限，请重新选择")
								}
							}
						})
					}else if(domain.colDname=='specDetailClass'){
						getVoltCodeList({"specDetailClass":val}).then(res=>{
							if(res.data.successful){
								let voltLevelArr = res.data.resultValue
								//如果原来的电压等级中有值，遍历获取的数组，如果数组中包含，则不清空原来的值
								//否则，清空原来的值
								let voltLevelVal = _this.getValue("voltLevel")
								if(voltLevelVal!=null){
									let tempFlag = false
									for(let i=0;i<voltLevelArr.length;i++){
										let tempCode = voltLevelArr[i]
