		//保存&修改
		saveItem(){
			let _this = this;
			let formName = ''
			let saveArr = {}
			
			//不同阶段保存不同的数据
			if(_this.phash=='1'){//立项阶段
				formName = 'saveFormLX'
				saveArr = _this.tableLabelLX
				
				
			}else if(_this.phash=='2'){//可研阶段
				formName = 'saveFormKY'
				saveArr = _this.tableLabelKY
			}else if(_this.phash=='3'){//储备阶段
				formName = 'saveFormCB'
				saveArr = _this.tableLabelCB
			}



			_this.$refs[formName].validate(valid => {
				if (valid) {
						let arr = {};
						let successMsg = '保存成功';
						let failMsg = '保存失败';
						

						_this.dialogLoading = true
						//设置itemId
						arr['itemId'] = _this.tempItemId;
						//设置父级id
						arr['parentId'] = _this.parentItemId;
						//设置专业类别
						arr['speciality'] = _this.specClass;
						//设置项目阶段，用于区分存入哪张表 1立项，2可研，3储备
						arr["phash"] = _this.phash
						//如果命名器获取的itemId不为空，传入获取的itemId
						if(_this.itemIdT!=''){
							arr["itemIdT"] = _this.itemIdT
						}
			
						//赋值
						for (let i in saveArr) {
							let item = saveArr[i]
							arr[item.colDname] = item.value;
						}
						itemEdit(arr).then(response => {
							_this.dialogLoading = false
							console.log(response);
							if (response.data.successful) {
								this.$message({
									showClose: true,
									message: successMsg,
									type: 'success'
								});
								// 成功之后的处理
								_this.itemId = ''
								_this.itemIdT = ''
								this.showDrawer = false;
								this.$emit('changeStatus',this.showDrawer) 
								this.$parent.getTable(1,20)

							} else {
								this.$message({
									showClose: true,
									message: failMsg,
									type: 'error'
								});
							}
							_this.loading = false;
						});
					} else {//表单校验失败
						let warnMsg = '您有未符合规范的输入项，请检查'
						console.log('error submit!!');
						this.$message({
							showClose: true,
							message: warnMsg,
							type: 'warning'
						});
						return false;
					}
			
			})
		},
		// 每页条数
		handleSizeWTChange(val){
			let _this = this
			_this.currentNum = val
			for (let i in _this.tableLabelLX) {
				let temp1 = _this.tableLabelLX[i];
				if (temp1.colDname == 'itemSource') {
					if (temp1.value.length > 0) {
						_this.innerVisible = true;
						if (temp1.value == '01') {
							//如果来源于问题库，查询问题库数据
							_this.questionTitle = '问题库列表';
							if(_this.detailFlag){
								_this.questionDetail(_this.codeStr,_this.currentPage,val) 
							}else{
								_this.isToggle = true
								_this.getWTList(_this.currentPage, val);
							}
							
						} else {
							//如果来源于需求库，查询需求库数据
							_this.questionTitle = '需求库列表';
							if(_this.detailFlag){
								_this.questionDetail(_this.codeStr,_this.currentPage,val) 
							}else{
								_this.isToggle = true
								_this.getXQList(_this.currentPage, val);
							}
							
						}
					} else {
						_this.$message({
							showClose: true,
							message: '请先选择项目来源',
							type: 'warning'
						});
					}
					break;
				}
			}
		},
        
