		//查询表单头部信息
		getLabel(){
			let _this = this
			// _this.dialogLoading = true
			if(_this.tempItemId!=null && _this.tempItemId!="" && _this.tempItemId!=undefined){ //修				
				let param = {}
				param.index = 1
				param.pageSize = 10
				param.speciality = _this.specClass
				param.itemId = _this.tempItemId
				if(_this.tableLabelArr.length==0){
					let formartDate = {}
					formartDate.speciality = _this.specClass
					formartDate.itemId = _this.tempItemId
					queryItemTitle(formartDate).then(response=>{
						if(response.data.successful){
							_this.tableLabelArr = response.data.resultValue;
							//渲染表单
							_this.setLableData()
							//根据id查询项目信息
							let paramTmp = {}
							paramTmp.speciality = _this.specClass
							paramTmp.itemId = _this.tempItemId
							//调用后台接口，根据itemId查询项目详情
							queryByItemId(paramTmp).then(res=>{
								if(res.data.successful){
									//基础信息
									let itemBaseObj = res.data.resultValue.prjStItemBase
									//可研信息
									let itemFeaObj = res.data.resultValue.prjStFea
									//储备信息
									let itemStorage = res.data.resultValue.prjStStorage

									// if(_this.phash==3){//储备阶段，将原系统项目编码数据赋值到储备阶段 add by gao.feng
									// 	itemStorage["projectId"] = itemBaseObj["projectId"]
									// }

									//赋值立项基本信息
									_this.setItemInfoData(_this.tableLabelLX,itemBaseObj)
									//赋值可研基本信息
									_this.setItemInfoData(_this.tableLabelKY,itemFeaObj)
									//赋值储备基本信息
									_this.setItemInfoData(_this.tableLabelCB,itemStorage)
								}
							})
						}
					})
				}else{
					//渲染表单
					_this.setLableData()
					//根据id查询项目信息
					let paramTmp = {}
					paramTmp.speciality = _this.specClass
					paramTmp.itemId = _this.tempItemId
					//调用后台接口，根据itemId查询项目详情
					queryByItemId(paramTmp).then(res=>{
						if(res.data.successful){
							//基础信息
							let itemBaseObj = res.data.resultValue.prjStItemBase
							//可研信息
							let itemFeaObj = res.data.resultValue.prjStFea
							//储备信息
							let itemStorage = res.data.resultValue.prjStStorage
							//赋值立项基本信息
							_this.setItemInfoData(_this.tableLabelLX,itemBaseObj)
							//赋值可研基本信息
							_this.setItemInfoData(_this.tableLabelKY,itemFeaObj)
							//赋值储备基本信息
							_this.setItemInfoData(_this.tableLabelCB,itemStorage)
						}
					})
				}

				_this.getKYInfo(_this.tempItemId)

			}else{ //新增
				let param = {}
				param.speciality = _this.specClass
				//获取表头,如果表头不存在，通过接口查询
				if(_this.tableLabelArr.length==0){
					let formartDate = {}
					formartDate.speciality = _this.specClass
					queryItemTitle(formartDate).then(response=>{
						if(response.data.successful){
							_this.tableLabelArr = response.data.resultValue;
							_this.setLableData()
						}
					})
				}else{
					if(_this.parentItemId!=null){
						let formartDate = {}
						formartDate.speciality = _this.specClass
						queryItemTitle(formartDate).then(response=>{
							if(response.data.successful){
								_this.tableLabelArr = response.data.resultValue;
								_this.setLableData()
							}
						})
					}else{
						_this.setLableData()
					}
					
				}
			}
			
