		//获取字典数组   
		getDictArr(labelList){ 

			let _this = this
			let map = new Map();
			for(let i=0;i<labelList.length;i++){
				let temp = labelList[i];
				if(temp.codeList!=null&&temp.isFilterCol!=1){
					//研究开发专项itemClass没有零星 9  13 10
					if(_this.specClass=='08'||_this.specClass=='10'||_this.specClass=='09'||_this.specClass=='11'){
						if(temp.colDname=='itemClass'){
							let tempArr = []
							temp.codeList.forEach(function(item){
								if(item.codeValue!="3"){
									tempArr.push(item)
								}
							})
							temp.codeList = tempArr
						}
					}
					//信息化专业管理itemType没有零星 9  13 10
					if(_this.specClass=='08'){
						if(temp.colDname=='itemType'){
							let tempArr = []
							temp.codeList.forEach(function(item){
								if(item.codeValue!="LX"){
									tempArr.push(item)
								}
							})
							temp.codeList = tempArr
						}
					}
					// 如果是新增不能选择单体项目
					
					if(_this.tempItemId == "" || _this.tempItemId == null || _this.tempItemId == undefined){
						// debugger
						//如果是新增下级
						if(_this.parentItemId!=null){
							if(temp.colDname=='itemLevel'){
								let tempArr2 = []
								temp.codeList.forEach(function(item){
									tempArr2.push(item)
								})
								temp.codeList = tempArr2
							}
						}else{
							if(temp.colDname=='itemLevel'){
								let tempArr = []
								temp.codeList.forEach(function(item){
									if(item.codeValue!="03"){
										tempArr.push(item)
									}
								})
								temp.codeList = tempArr
							}
						}
						
					}
					

					//如果是新增下级，则不能选择项目包
					// if(_this.parentItemId!=null){
					// 	if(temp.colDname=='itemLevel'){
					// 		let tempArr = []
					// 		//01项目02项目包03单体项目
					// 		if(_this.parentItemLevel == "01"){
					// 			temp.codeList.forEach(function(item){
					// 				if(item.codeValue=="03"){
					// 					tempArr.push(item)
					// 				}
					// 			})
					// 			temp.codeList = tempArr
					// 		}else if(_this.parentItemLevel == "02"){
					// 			temp.codeList.forEach(function(item){
					// 				if(item.codeValue=="01"){
					// 					tempArr.push(item)
					// 				}
					// 			})
					// 			temp.codeList = tempArr
					// 		}	
					// 	}
					// }
					//新增时过滤掉enabled不为1的码值
					if(_this.tempItemId==null || _this.tempItemId=="" || _this.tempItemId!=undefined){
						let tempArr = []
						temp.codeList.forEach(function(item){
							if(item.enabled==1){
								tempArr.push(item)
							}
						})
						temp.codeList = tempArr
					}

					map.set(temp.colDname,temp.codeList);
				}else if(temp.dispType=='cascadeDropdown'){
					map.set(temp.colDname,new Array());
				}
			}
			return map
    	},
     	//获取字段类型字典
		getColumnTypeArr(labelList){
			let map = new Map();
			for(let i=0;i<labelList.length;i++){
				let temp = labelList[i];
				map.set(temp.colDname,temp.colType);
			}
			return map
		},
		//校验数字
		isNumber(rule, value, callback) {
			if(value==null){
				callback();
			}
			let regPos = /^\d+(\.\d+)?$/; //非负浮点数
		    if(!regPos.test(value)){
				callback(new Error("只能输入数字且不能为负值"));
			}else if(value.toString().indexOf('.')>0&&value.toString().split(".")[1].length>4){
				callback(new Error("数字只精确到小数点后四位"));
			}else{
				callback();
			}
		},
		//获取插入和修改的表头
		getLabelList(labelList){ 
			let _this =this
			//双向数据绑定
			labelList.forEach(function (item) {
				if(item.displayType=='dropdown'||item.displayType=='cascadeDropdown'){
					_this.$set(item,'value','');
				}else{
					_this.$set(item,'value',null);
				}
				
			})
			let common_table_info = {};//数组转对象，避免类型报错
			for(let i in labelList){
				let tempObj = labelList[i]
				if(typeof(tempObj.readOnly)=='undefined'||tempObj.readOnly==null||tempObj.readOnly==''){
					tempObj.readOnly = '0'
				}

				//如果是编辑状态，增加过滤，去除掉规划总投资和规划年份
				// if(_this.tempItemId==null){
				// 	if(tempObj.colDname == 'ghYear'||tempObj.colDname=="ghInvest"){
				// 			continue
				//     }	
				// }

				common_table_info[i] = labelList[i];
				//配置校验信息
				let rule = [];
				//add by gao.feng 当前阶段为项目浏览或者字段的阶段和当前阶段不符，不进行校验
				if(_this.phash!=0&&tempObj.phase==_this.phash){
					if (tempObj.isMandatory == 1) {
						//必填项
						let ruleT = new Object();
						ruleT.required = true;
						ruleT.message = tempObj.dispName + '不能为空';
						if(tempObj.dispType=="dropdown"){
							ruleT.trigger = 'change';
						}else{
							ruleT.trigger = 'change';
						}
						
						rule.push(ruleT)
					}

					if(tempObj.dispType=="number"){
						let ruleT1 = new Object();
						ruleT1.validator = _this.isNumber
						ruleT1.trigger = 'change'
						rule.push(ruleT1)
					}

					if(typeof(tempObj.elCode)!='undefined'&&tempObj.elCode!=null&&tempObj.elCode!=''){
						let ruleT2 = new Object();
						ruleT2.required = false;
						ruleT2.pattern = tempObj.elCode
						if(typeof(tempObj.errorText)!='undefined'&&tempObj.errorText!=''&&tempObj.errorText!=null){
							ruleT2.message = tempObj.errorText 
						}else{
							ruleT2.message = tempObj.dispName+'格式不正确'
						}
						
						ruleT2.trigger = 'change'
						rule.push(ruleT2)
					}
						
					common_table_info[i].rule = rule
				}
				

				//设置默认值
				if(tempObj.colDefault!=null&&tempObj.colDefault!=''&&tempObj.colDefault!='null'){
					common_table_info[i].value = tempObj.colDefault
				}
				
				//add by gao.feng 立项阶段，电力营销投入默认限下，管理咨询、股权投资默认限上项目
				if(tempObj.colDname=='itemType'){
					if(_this.specClass=='13'||_this.specClass=='11'){
						common_table_info[i].value = 'XS'
					}else if(_this.specClass=='03'){
						common_table_info[i].value = 'XX'
					}
				}

				//如果是新增下级
				if(_this.parentItemId!=null){	
					if(common_table_info[i].colDname=='itemLevel'){
						if(_this.parentItemLevel=="01"){ //如果上级是项目，则默认为单体项目
						common_table_info[i].value = '03'
						// common_table_info[i].value = '单体项目'
							
						}else if(_this.parentItemLevel=="02"){ //如果是项目包，则默认为项目
							common_table_info[i].value = '01'
						}
						common_table_info[i].readOnly = '1'
					}
					
				}else{
					common_table_info[i].readOnly = '0'
				}

				//可研阶段，根据项目状态去指定某些字段只读
				if(_this.phash==2){
					if(_this.itemPhaseDet==3||_this.itemPhaseDet==4||
						_this.itemPhaseDet==27){
						//立项申请通过,已上传可研 正式评审文件文号不能修改
						if(common_table_info[i].colDname=='auditFileNo'){
							common_table_info[i].readOnly = '1'
						}
					}else if(_this.itemPhaseDet==15||_this.itemPhaseDet==20){
						//已会签评审意见,可研评审通过 可研信息中的两个可研投资字段不能修改
						if(common_table_info[i].colDname=='feaSInvest'
							||common_table_info[i].colDname=='feaDInvest'){
							common_table_info[i].readOnly = '1'
						}
					}
				}
				
			}

			//构建是否必填规则

			return common_table_info
		},
		
	}
}
</script>
