						tableLabel[i].value  = _this.formartDate(itemInfo[tableLabel[i].colDname],tableLabel[i].colType)
				}else{
						tableLabel[i].value = itemInfo[tableLabel[i].colDname] + '';
				}
				//如果是部门树形，通过部门编码查询部门名称
				if(tableLabel[i].dispType == 'deptDropdown'){
					getDeptIdByDeptCode({'deptCode':tableLabel[i].value}).then(response=>{
						if(response.data.successful){
							tableLabel[i].deptName= response.data.resultValue
							_this.$forceUpdate()
						}
					})
				}
				//三级联动，触发change事件
				if(tableLabel[i].dispType=='cascadeDropdown'){
					console.log("*********"+i+"********")
					_this.commonChange(tableLabel[i],tableLabel[i].value)
				}
			}
		},
		//拆分表头数据
		setLableData(){
			let _this = this
			//将表头拆分成立项阶段表头，可研阶段表头和储备阶段表头
			let lxLabelArr = []
			let kyLabelArr = []
			let cbLabelArr = []

			for(let i=0;i<_this.tableLabelArr.length;i++){
				let tempData = _this.tableLabelArr[i]
				if(tempData.phase=='1'){
					lxLabelArr.push(tempData)
				}else if(tempData.phase=='2'){
					kyLabelArr.push(tempData)
				}else if(tempData.phase=='3'){
					cbLabelArr.push(tempData)
				}
			}

			// _this.tableLabel =  _this.getLabelList(_this.tableLabelArr)
			_this.tableLabelLX = _this.getLabelList(lxLabelArr)
			_this.tableLabelKY = _this.getLabelList(kyLabelArr)
			_this.tableLabelCB = _this.getLabelList(cbLabelArr)

			//获取字典
			_this.dictMap = _this.getDictArr(_this.tableLabelArr)
			//零星购置，建管单位字段默认为用户所在单位
			if(_this.specClass=='08'){
				getUserDept().then(res=>{
					if(res.data.successful){
						let reusltMap = res.data.resultValue
						_this.setValue("conDept",reusltMap.deptCode)
						_this.deptName = reusltMap.deptName
					}
					
				})
			}	
			_this.dialogLoading = false			
		},
		//点击取消选择问题列表
		cancleSubmitInnerTable(){
			let _this = this
			_this.probName = ""
			_this.innerVisible = false;
		},
		//检索问题或需求列表
		getChildTableList(){
			let _this = this
			for (let i in _this.tableLabel) {
				let temp1 = _this.tableLabel[i];
				if (temp1.colDname == 'itemSource') {
					if (temp1.value.length > 0) {
						_this.innerVisible = true;
						if (temp1.value == '1') {
							//如果来源于问题库，查询问题库数据
							_this.questionTitle = '问题库列表';
							_this.getXQList(1, 5);
						} else {
							//如果来源于需求库，查询需求库数据
							_this.questionTitle = '需求库列表';
							_this.getWTList(1, 5);
						}
					} else {
						_this.$message({
							showClose: true,
							message: '请先选择项目来源',
							type: 'warning'
						});
					}
					break;
				}
			}
		},
		//获取表单中的值
		getValue(key){
			let _this = this
			for (let i in _this.tableLabel) {
				let item = _this.tableLabel[i]
				if(item.colDname==key){
					return item.value
				}
			}
		},
		//设置表单中的值
		setValue(key,value,flag){
			let _this = this
			//设置值时，根据当前的阶段来找要把值设置到哪个数组
			let tempArr = {}
			if(_this.phash=='1'){
				tempArr = _this.tableLabelLX
			}else if(_this.phash=='2'){
				tempArr = _this.tableLabelKY

			}else if(_this.phash=='3'){
				tempArr = _this.tableLabelCB
			}
			if(typeof(flag)!=undefined&&flag){
				for (let i in tempArr) {
					let item = tempArr[i]
					if(item.colDname==key){
						item.codeList = value
						break
					}
				}
			}else{
				for (let i in tempArr) {
					let item = tempArr[i]
					if(item.colDname==key){
						item.value = value
						break
					}
				}
			}
			
			
		},
		//生成项目名称
		showNewItemName(){
			let _this = this
			// this.newItemName = true
			let param = {}
			param.speciality = _this.specClass
			param.itemClass = _this.getValue('itemClass1')
			param.itemClassChild = _this.getValue('itemClass2')
			param.itemClassChild2 = _this.getValue('itemClass3')
			param.B01 = _this.getValue('area')       //项目所在地
			param.B04 = _this.getValue('voltLevel')  //电压等级
			param.B03 = _this.getValue('stationName') //站名
			param.B05 = _this.getValue('buildType') //建设性质
			param.B08 = 'A' //站名
			doPostApplyItemName(param).then(response=>{
				if(response.data.successful){
					let value = response.data.resultValue
					_this.setValue('itemName',value)
				}else{
					let str = response.resultHit

				}
			})
		},
		//调用命名器
		showNewItemNameJsp(){
			let _this = this
			// this.newItemName = true
			let param = {}
			param.speciality = _this.specClass
			param.itemClass = _this.getValue('itemClass1')
			param.itemClassChild = _this.getValue('itemClass2')
			param.itemClassChild2 = _this.getValue('itemClass3')
			param.B01 = _this.getValue('area')       //项目所在地
			param.B04 = _this.getValue('voltLevel')  //电压等级
			param.B03 = _this.getValue('stationName') //站名
			param.B05 = _this.getValue('buildType') //建设性质
			param.B08 = 'A' //站名
			_this.getNameUrl = '/pcinfo/resUniname/pages/pgUnifiedNameDefine.jsp?appID=00&applyID=123123123123&iscUserId=&loginName=sx_test'
			getApplyNameParam(param).then(response=>{
				if(response.data.successful){
					let param = response.data.resultValue
					_this.getNameUrl += param
					_this.getNameUrl = encodeURI(encodeURI(_this.getNameUrl));
					_this.newItemName = true
				}else{
					let str = response.resultHit
				}
			})
		},
		//调用命名器获取编码
		getBM(){
            var Iframe=document.getElementById("name-iframe");//先获取到iframe元素
            var iframeWindow=(Iframe.contentWindow || Iframe.contentDocument);
            var iframevalue =  iframeWindow.document.getElementById("prjName").value;
            // alert(iframevalue);
            if(iframevalue){
				document.getElementById("ifremVal").value = iframevalue
				for (let i in this.tableLabel) {
						if(this.tableLabel[i].colDname=="itemName"){
							this.tableLabel[i].value = iframevalue
						}
				}
				
                this.newItemName =  false
            }else {
                this.$message({
                showClose: true,
                message: '请先生成项目名称',
                type: 'error'
                });
            }
	    },
		//问题被选中数据改变监听事件
		handleQuestionSelectionChange(val) {
			let _this = this;
			let flag = 1
			for(let i in _this.tableLabel){
			   let temp1 = _this.tableLabel[i];
			   if (temp1.colDname == 'itemSource') {
						if (temp1.value == '1') {
							flag = 1
						} else {
							//如果来源于需求库，查询需求库数据
							flag = 2
						}
						break;
				}
			}
			_this.selectedQuestionCode = []
			val.forEach(function(item) {
				if(flag==1){
					_this.selectedQuestionCode.push(item.probCode);
				}else{
					_this.selectedQuestionCode.push(item.dmdCode);
				}
			});
		},
		//保存被选中的问题或需求编码
		submitQuestioinCode() {
			let _this = this;
			_this.probName = ""
			if (_this.selectedQuestionCode.length > 0) {
				for (let i in _this.tableLabelLX) {
					let temp1 = _this.tableLabelLX[i];
					if (temp1.colDname == 'problemCode') {
						if (temp1.value!=null&&temp1.value.length > 0) {
							let arr = temp1.value.split(',');
							_this.selectedQuestionCode = _this.selectedQuestionCode.concat(arr);
						}
						//去重
						let set = new Set(_this.selectedQuestionCode);
						let array = Array.from(set);
						let codeStr = array.join(',');
						temp1.value = codeStr;
						break;
					}
				}
				_this.selectedQuestionCode = [];
				_this.innerVisible = false;
			} else {
				_this.$message({
					showClose: true,
					message: '请选择问题或需求',
					type: 'warning'
				});
			}
		},
		//获取问题库表头和问题库数据
		getXQList(page, size) {
			let _this = this;
			_this.loading = true;
			getPrjStProb({ pageIndex: page, pageSize: size,probName:_this.probName })
				.then(response => {
					_this.loading = false;
					//{}写需要的参数
					console.log('response', response); //后端返回数据
					if (response.data.successful) {
						_this.questionListTotalNum = response.data.resultValue.totalNum;
						_this.questionLabelList = response.data.resultValue.tableLable;
						_this.questionDictMap = _this.getDictArr(_this.questionLabelList);
						_this.questionTableData = response.data.resultValue.tableData;
						_this.questionShowData = _this.getShowDateTable(_this.questionTableData, _this.questionDictMap);
					}
					
				})
				.catch(error => {
					console.log(error); //error
				});
		},
		//获取需求库表头和需求库数据
		getWTList(page, size) {
			let _this = this;
			_this.loading = true;
			getPrjStDmd({ pageIndex: page, pageSize: size,dmdName:_this.probName })
				.then(response => {
					_this.loading = false;
					//{}写需要的参数
					console.log('response', response); //后端返回数据
					if (response.data.successful) {
						_this.questionListTotalNum = response.data.resultValue.totalNum;
						_this.questionLabelList = response.data.resultValue.tableLable;
						_this.questionDictMap = _this.getDictArr(_this.questionLabelList);
						_this.questionTableData = response.data.resultValue.tableData;
						_this.questionShowData = _this.getShowDateTable(_this.questionTableData, _this.questionDictMap);
					}
				})
				.catch(error => {
					console.log(error); //error
				});
		},
		//展示问题列表
		showQuestionList() {
			let _this = this;
			for (let i in _this.tableLabelLX) {
				let temp1 = _this.tableLabelLX[i];
				if (temp1.colDname == 'itemSource') {
					if (temp1.value.length > 0&&temp1.value!=null) {
						
						if (temp1.value == '01') {
							//如果来源于问题库，查询问题库数据
							_this.questionTitle = '问题库列表';
							_this.getXQList(1, 5);
							_this.innerVisible = true;
						} else if(temp1.value == '02'){
							//如果来源于需求库，查询需求库数据
							_this.questionTitle = '需求库列表';
							_this.getWTList(1, 5);
							_this.innerVisible = true;
						} else{
							this.$message({
								showClose: true,
								message: '项目来源为其他时,不需要选择问题或需求列表',
								type: 'warning'
							})
							// _this.innerVisible = true;
						}
					} else {
						_this.$message({
							showClose: true,
							message: '请先选择项目来源',
							type: 'warning'
						});
					}
					break;
				}
			}
		},
		//生成项目名
		makeItemName() {
			let _this = this;
			_this.newItemName = false;
			console.log('生成项目名:' + _this.newItemNameNo + ';' + _this.newItemNameTime);
			
		}, 
		//获取字典数组   
		getDictArr(labelList){ 
			let _this = this
			let map = new Map();
			if(typeof(labelList)!='undefined'&&labelList!=null&&labelList.length>0){
				for(let i=0;i<labelList.length;i++){
					let temp = labelList[i];
					if(temp.codeList!=null){
						//研究开发专项itemClass没有零星 9  13 10
						if(_this.specClass=='9'||_this.specClass=='11'){
							if(temp.colDname=='itemClass'){
								let tempArr = []
								temp.codeList.forEach(function(item){
									if(item.codeValue!="3"){
										tempArr.push(item)
									}
								})
								temp.codeList = tempArr
							}
						}

						//如果是新增下级，则不能选择项目包
						if(_this.parentItemId!=null){
							if(temp.colDname=='itemType'){
								let tempArr = []
								temp.codeList.forEach(function(item){
									if(item.codeValue!="1"){
										tempArr.push(item)
									}
								})
								temp.codeList = tempArr
							}
						}

						map.set(temp.colDname,temp.codeList);
					}else if(temp.dispType=='cascadeDropdown'){
						map.set(temp.colDname,new Array());
					}
			}
			}
			
			return map
    	},
     	//获取字段类型字典
		getColumnTypeArr(labelList){
			let map = new Map();
			for(let i=0;i<labelList.length;i++){
				let temp = labelList[i];
				map.set(temp.colDname,temp.colType);
			}
			return map
		},
		//校验数字
		isNumber(rule, value, callback) {
			if(value==null){
				callback();
			}
			let regPos = /^\d+(\.\d+)?$/; //非负浮点数
		    if(!regPos.test(value)){
				callback(new Error("只能输入数字且不能为负值"));
			}else if(value.toString().indexOf('.')>0&&value.toString().split(".")[1].length>2){
				callback(new Error("数字只精确到小数点后两位"));
			}else{
				callback();
			}
		},
		//获取插入和修改的表头
		getLabelList(labelList){ 
			let _this =this
			//双向数据绑定
			labelList.forEach(function (item) {
				if(item.displayType=='dropdown'||item.displayType=='cascadeDropdown'){
					_this.$set(item,'value','');
				}else{
					_this.$set(item,'value',null);
				}
				
			})
			let common_table_info = {};//数组转对象，避免类型报错
			for(let i in labelList){
				let tempObj = labelList[i]
				if(typeof(tempObj.readOnly)=='undefined'||tempObj.readOnly==null||tempObj.readOnly==''){
					tempObj.readOnly = '0'
				}

				//如果是编辑状态，增加过滤，去除掉规划总投资和规划年份
				// if(_this.tempItemId==null){
				// 	if(tempObj.colDname == 'ghYear'||tempObj.colDname=="ghInvest"){
				// 			continue
				//     }	
				// }

				common_table_info[i] = labelList[i];
				//配置校验信息
				let rule = [];
				if (tempObj.isMandatory == 1) {
					//必填项
					let ruleT = new Object();
					ruleT.required = true;
					ruleT.message = tempObj.dispName + '不能为空';
					if(tempObj.dispType=="dropdown"){
						ruleT.trigger = 'change';
					}else{
						ruleT.trigger = 'blur';
					}
					
					rule.push(ruleT)
				}

				if(tempObj.dispType=="number"){
					let ruleT1 = new Object();
					ruleT1.validator = _this.isNumber
					ruleT1.trigger = 'change'
					rule.push(ruleT1)
				}

				if(typeof(tempObj.elCode)!='undefined'&&tempObj.elCode!=null&&tempObj.elCode!=''){
					let ruleT2 = new Object();
					ruleT2.required = false;
					ruleT2.pattern = tempObj.elCode
					if(typeof(tempObj.errorText)!='undefined'&&tempObj.errorText!=''&&tempObj.errorText!=null){
						ruleT2.message = tempObj.errorText 
					}else{
						ruleT2.message = tempObj.dispName+'格式不正确'
					}
					
					ruleT2.trigger = 'change'
					rule.push(ruleT2)
				}
					
				common_table_info[i].rule = rule

				//设置默认值
				if(tempObj.colDefault!=null&&tempObj.colDefault!=''&&tempObj.colDefault!='null'){

					common_table_info[i].value = tempObj.colDefault
				}
			}

			//构建是否必填规则

			return common_table_info
		},
		
	}
};
</script>
