            //提交审核
            submitCheck() {
                let _this = this
                let itemIds = ''

                if (_this.submitCheckArr.length == 0) {
                    this.$message.warning('请选择一个项目')
                    return false
                }
                for(let i=0; i<_this.submitCheckArr.length;i++){
                    let item = _this.submitCheckArr[i]
                    itemIds += ',' + item.itemId
                    if(item.itemPhaseDet ==0){
                        _this.$message.warning('不能再次提交立项审核退回的项目，请在待办中操作')
                        return false
                    }
                }
                    
                
                
                if(itemIds){
                    itemIds = itemIds.substr(1)
                    _this.showLoadingBtn = true
                    checkItemParent({'itemIds':itemIds}).then(res => {
                        if(res.data.successful){
                            let resultVal = res.data.resultValue
                            if(!resultVal.result){
                                _this.$message({
                                    showClose: true,
                                    message: "不能选择包含上级的项目",
                                    type: 'warning'
                                });
                                _this.showLoadingBtn = false
                            }else{
                                if(this.$store.state.user.workflowtype==0) {
                                    //modify by liangwu 2020.6.25 调用流程中心流程
                                    let specRoleID = '';//专项角色ID
                                    let iscOrg = window.JSON.parse(sessionStorage.getItem("iscOrg"));//isc组织机构
                                    let specRoleMap = window.JSON.parse(sessionStorage.getItem("specRoleMap"));//isc专项role
                                    let purchaseRoleMap = window.JSON.parse(sessionStorage.getItem("purchaseRoleMap"));//isc零购专项role
                                    let electronicRoleMap = window.JSON.parse(sessionStorage.getItem("electronicRoleMap"));//isc电网基建专项role
                                    let userid = sessionStorage.getItem("userId");
                                    specRoleID = specRoleMap[_this.specClass];//根据专项类型

                                    let params = {
                                        "user": userid,
                                        "orgIds": iscOrg.id,
                                        "roleIds": specRoleID,
                                        "processDefName": "project_approval",
                                        "activityDefID": 'audit',
                                        "processInstName": '',
                                        "processInstDesc": '',
                                        "itemId":""
                                    }
                                    _this.submitCheckArr.forEach(function (item) {
                                        console.log("item foreach"+item.itemName)
                                        itemIds += ',' + item.itemId
                                        if (_this.specClass == '01') {//如果是电网基建角色从electronicRoleMap取
                                            specRoleID = electronicRoleMap[item.specDetailClassT];
                                            params.roleIds = specRoleID
                                        }
                                        if (_this.specClass == '08') {//如果是电网基建角色从purchaseRoleMap取
                                            specRoleID = purchaseRoleMap[item.specClassT];
                                            params.roleIds = specRoleID
                                        }

                                        if(params.orgIds == undefined){
                                            params.orgIds = "";
                                        }

                                        if(params.roleIds == undefined){
                                            params.roleIds = "";
                                        }

                                        
                                        let checkparams = {
                                            itemId: item.itemId,
                                        }
                                        // 检查工作里是否能立项审批
                                        getItemInfo(checkparams).then(res => {
                                            // if (res.data.successful && (res.data.resultValue.isFinished == '')) {
                                            if (res.data.successful) {
                                                params.itemId = res.data.resultValue.itemId
                                                params.processInstName = res.data.resultValue.itemName + '-项目立项审核'
                                                params.processInstDesc = res.data.resultValue.itemName + '-项目立项审核'
                                                //调用流程中心启动
                                                createAndReassignAndfinishWorkItemWithOption(params).then(res => {
                                                    console.log("params foreach"+item.itemId)
                                                    if(res.data.status == 0)
                                                    {
                                                        let finishparams = {
                                                            processInstID: res.data.data,
                                                            userId: userid,
                                                            itemId:item.itemId,
                                                            type:0
                                                        }

                                                        // 将返回值信息及项目信息插入后端数据库
                                                        createProcess(finishparams).then(finishres => {
                                                            if (finishres.data.successful) {
                                                                let saveWorkItemParams ={
                                                                    processInstID:res.processInstID,
                                                                    itemId:item.itemId,
                                                                    userId:userid,
                                                                    isAudit:'',
                                                                    workItemId:'123',
                                                                    auditDesc:'立项申请',
                                                                    itemPhaseDet:'2'
                                                                }

                                                                saveWorkItem(saveWorkItemParams).then(res=>{
                                                                    
                                                                    if (finishres.data.successful) {
                                                                        _this.$message.success("提交立项审核成功")
                                                                        _this.getTable(_this.currentPage, _this.currentNum)
                                                                    }
                                                                    else {
                                                                        _this.showLoadingBtn = false
                                                                        let error = finishres.data.resultHint;
                                                                        _this.$message.error(item.itemName + error);
                                                                    }
                                                                })
                                                            }else {
                                                                let error = finishres.data.resultHint;
                                                                _this.$message.error(item.itemName + error);
                                                                _this.showLoadingBtn = false
                                                            }
                                                        });
                                                    }else {
                                                        _this.showLoadingBtn = false
                                                        _this.$message.error(item.itemName + res.data.message);
                                                    }

                                                })
                                            } else {
                                                _this.showLoadingBtn = false
                                                let error = res.data.resultHint;
                                                _this.$message.warning(item.itemName + "项目流程进行中，不能重复提交。");
                                            }
                                        });
                                    })
                                }

                                if(this.$store.state.user.workflowtype==1) {
                                    
                                    // itemIds = itemIds.substr(1)
                                    // 内部开发使用工作流方式与流程中心功能2选择1
                                    applyBySpeciality({'itemIds': itemIds}).then(response => {
                                        _this.showLoadingBtn = false
                                        if (response.data.successful) {
                                            _this.submitCheckArr = []
                                            _this.$refs.itemTable.clearSelection()
                                            _this.getTable(_this.currentPage, _this.currentNum)
                                            this.$message.success("提交审核成功")
                                        } else {
                                            this.$message.error("提交项目申请报错")
                                        }
                                    })
                                }
                            }
                        }else{
                            _this.showLoadingBtn = false
                        }
                    })
                }
                


                
