                    // this.selectParamDict.set('itemClass123ListChild', [])
                    this.currentPage = 1;
                    this.getTable(1,this.currentNum)
                }
            },
            formartDate(param, colLimit) {
                var time = new Date(param);
                var y = time.getFullYear();
                var m = time.getMonth() + 1;
                var d = time.getDate();
                var h = time.getHours();
                var M = time.getMinutes();
                var s = time.getSeconds();

                var result = ''
                if (colLimit == 'year') {
                    result = y
                } else if (colLimit == 'month') {
                    result = y + '-' + this.add0(m)
                } else if (colLimit == 'date') {
                    result = y + '-' + this.add0(m) + '-' + this.add0(d);
                }
                // var h = time.getHours();
                // var mm = time.getMinutes();
                // var s = time.getSeconds();
                // return y+'-'+this.add0(m)+'-'+this.add0(d)+' '+this.add0(h)+':'+this.add0(mm)+':'+this.add0(s);
                return result;
            },
            add0(m) {
                return m < 10 ? '0' + m : m;
            },
            //获取字段类型字典
            getColumnTypeArr(labelList) {
                let map = new Map();
                for (let i = 0; i < labelList.length; i++) {
                    let temp = labelList[i];
                    // let type ={}
                    // type.colType = temp.colType
                    // type.colLimit = temp.colLimit
                    map.set(temp.colDname, temp.colType == null ? '' : temp.colType);
                }
                return map
            },
            //修改项目包名称
            updatePackageItemName() {
                let _this = this
               
                if (_this.packageItemParam.itemId == "") {
                    this.$message({
                        showClose: true,
                        message: '请选择一个项目包',
                        type: 'warning'
                    })
                    return false
                }

                //构造包调整参数
                let adjustParam = {
                    "ids": [],
                    "primarykey": []
                }

                for (let i = 0; i < _this.adjustArr.length; i++) {
                    let tempObj = _this.adjustArr[i]
                    if (tempObj.itemId == _this.packageItemParam.itemId) {
                        this.$message({
                            showClose: true,
                            message: '所选项目中含有父级项目',
                            type: 'warning'
                        })
                        _this.$refs.itemTable.clearSelection()
                        _this.adjustArr = []
                        return false
                    }
                    adjustParam.ids.push(tempObj.itemId)
                }

                adjustParam.primarykey.push(_this.packageItemParam.itemId)
                console.log(adjustParam)
                _this.submitLoading = true
                adjustPackageItem(adjustParam).then(response => {
                    _this.submitLoading = false
                    if (response.data.successful) {
                        this.$message.success("包调整成功")
                        _this.adjustArr = []
                        _this.$refs.itemTable.clearSelection()
                        _this.packageItemParam.itemId = ''
                        // 刷新主table
                        _this.currentPage = 1;
                        _this.getTable(1, _this.currentNum)
                    } else {
                        this.$message.error("包调整失败")
                    }
                })
            },
