                _this.getAllPackage(_this.packageItemCurrentPage, val)
            },
            //项目包列表分页当前页码
            handlePackageItemCurrentChange(val) {
                let _this = this
                _this.packageItemCurrentPage = val;
                _this.getAllPackage(val, 5)
            },
            //获取所有项目包数据
            getAllPackage(pageIndex, pageSize) {
                let _this = this
                _this.packageItemLoading = true

                let deptArr = []
                // getDictMap({'codeValue':'dwfl'}).then(response=>{
                // 		deptArr = response.data.resultValue;
                // })


                getAllPackageItem({
                    "speciality": _this.specClass,
                    "pageIndex": pageIndex,
                    "pageSize": pageSize,
                    "itemName": _this.packageItemParam.itemName,
                    "itemPhaseDet": '1,0'
                }).then(response => {
                    _this.packageItemLoading = false
                    if (response.data.successful) {
                        let arr = response.data.resultValue.items
                        _this.packageItemTotalNum = response.data.resultValue.itemCount
                        for (let i = 0; i < arr.length; i++) {
                            let tempItem = arr[i]
                            // let deptArr = _this.selectParamDict.get("deptCode")
                            deptArr.forEach(function (item) {
                                if (tempItem.deptCode == item.codeValue) {
                                    tempItem.deptCode = item.codeName
                                }
                            })
                        }
                        _this.packageItemArr = arr
                    } else {
                        this.$message.error("查询项目包列表失败")
                    }
                })
            },
            //展示包调整弹框--加校验
            showAdjustPackage() {
                let _this = this
                if (_this.adjustArr.length == 0) {
                    this.$message({
                        showClose: true,
                        message: '请选择要调整的项目',
                        type: 'warning'
                    })
                    return false
                }
               

                for (let i = 0; i < _this.adjustArr.length; i++) {
                    let tempObj = _this.adjustArr[i]
                    if(_this.userId != tempObj.createorT){
                         this.$message({
                            showClose: true,
                            message: '所选项目中含有不是创建者创建的项目，请重新选择',
                            type: 'warning'
                        })
                        return false
                    }
                    if (tempObj.itemLevelT == '02') {
                        this.$message({
                            showClose: true,
                            message: '项目包不能进行包调整，请重新选择',
                            type: 'warning'
                        })
                        _this.$refs.itemTable.clearSelection()
                        _this.adjustArr = []
                        return false
                    } else if (tempObj.itemLevelT == '03') {
                        this.$message({
                            showClose: true,
                            message: '单项项目不能进行包调整，请重新选择',
                            type: 'warning'
                        })
                        _this.$refs.itemTable.clearSelection()
                        _this.adjustArr = []
                        return false
                    } else {
                        if (tempObj.children && tempObj.children.length > 0) {
                            this.$message({
                                showClose: true,
                                message: '含有单项项目的项目不能进行包调整，请重新选择',
                                type: 'warning'
                            })
                            _this.$refs.itemTable.clearSelection()
                            _this.adjustArr = []
                            return false
                        }
                    }
                }
                _this.showAdjustPackageDialog = true
                _this.getAllPackage(1, 5)
            },
            //移入项目包
            moveInPackage() {
                let _this = this
                if (_this.packageBatchParam.parentId == '') {
                    this.$message({
                        showClose: true,
                        message: '请选择要移入的项目包',
                        type: 'warning'
                    })
                    return false
