                if (_this.moveInPackageIds.length == 0) {
                    this.$message({
                        showClose: true,
                        message: '请选择要移入的项目',
                        type: 'warning'
                    })
                    return false
                }

                let moveInPackageParams = {
                    ids: [],
                    primarykey: []
                }
                moveInPackageParams.ids = _this.moveInPackageIds
                moveInPackageParams.primarykey.push(_this.packageBatchParam.parentId)
                shiftInItem(moveInPackageParams).then(response => {
                    if (response.data.successful) {
                        _this.$refs.batchPackage.clearSelection();
                        _this.moveInPackageIds = [];
                        this.$message.success("移入项目包成功")
                        _this.showBatchPackage()
                    } else {
