            //删除项目
            del() {
				let _this = this;
				if (_this.delData.length == 0) {
					_this.$message({
						showClose: true,
						message: '请选择要删除的项目',
						type: 'warning'
					});
					return false;
				}else{
					for(let i=0;i<_this.delData.length;i++){
						let item = _this.delData[i]
						if(item.itemPhaseDet>1){
							_this.$message({
								showClose: true,
								message: '所选项目中含有已经进入审核流程的项目，请勿删除',
								type: 'warning'
							});
							return false;
						}
						if(item.createorT != _this.userId){
							_this.$message({
								showClose: true,
								message: '所选项目中含有不是创建者创建的项目，请检查后再删除',
								type: 'warning'
							});
							return false;
						}
					}
				}



				this.$confirm('此操作将永久删除项目, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				})
					.then(() => {
						_this.loading = true;
						let delParam = {
							ids : []
						}
						_this.delData.forEach(function(item){
							delParam.ids.push(item.itemId)
						})
						deleteItem(delParam).then(response => {
							console.log(response);
							if (response.data.successful) {
								this.$message({
									showClose: true,
									message: '删除成功',
									type: 'success'
								});
								_this.getTable(_this.currentPage, _this.currentNum);
							} else {
								this.$message({
									showClose: true,
									message: '删除失败',
                                    dangerouslyUseHTMLString: true,
									type: 'error'
								});
							}
							_this.loading = false;
						});
					})
					.catch(() => {
						this.$message({
							type: 'warning',
							message: '已取消删除'
						});
					});
			},
