            //新增下级
            addLowerLevel() {
                let _this = this;
                if (_this.parentItemInfo.length == 0) {
                    this.$message({
                        showClose: true,
                        message: '请选择一个项目',
                        type: 'warning'
                    });
                    return false;
                } else if (_this.parentItemInfo.length > 1) {
                    this.$message({
                        showClose: true,
                        message: '只能选择一个项目',
                        type: 'warning'
                    });
                    return false;
                }
                if (this.userId != this.parentItemInfo[0].createorT) {
                    this.$message.warning('所选项目中含有不是创建者创建的项目，请重新选择')
                    return false
                } 
                let paretnId = _this.parentItemInfo[0].itemId
                let parentItemLevel = _this.parentItemInfo[0].itemLevelT
                if(parentItemLevel == ""){
                    this.$message({
                        showClose: true,
                        message: '此项目项目层级为空，不能新增下级',
                        type: 'warning'
                    });
                    return false
                }
                if (_this.parentItemInfo[0].itemLevelT == "01") {
                    hasParent({
                        'itemId': _this.parentItemInfo[0].itemId
                    }).then(res => {
                        if (res.data.successful) {
                            let flag = res.data.resultValue
                            if (flag == true) {
                                this.$message({
                                    showClose: true,
                                    message: '项目包下的项目不能新增下级',
                                    type: 'warning'
                                });
                                return false
                            } else {
                                // 给showData赋值
                                _this.showData.parentItemId = paretnId
                                _this.showData.parentItemLevel = parentItemLevel
                                _this.showData.itemId = null;
                                _this.showData.phash = 1
                                _this.showDrawer = true
                                _this.timer = new Date().getTime()
                            }

                        }
                    })
                } else if (_this.parentItemInfo[0].itemLevelT == '03') {
                    this.$message({
                        showClose: true,
                        message: '单体项目不能新增下级',
                        type: 'warning'
                    });
                    return false;
                } else {
                    // 给showData赋值
                    _this.showData.parentItemId = paretnId
                    _this.showData.parentItemLevel = parentItemLevel
                    _this.showData.itemId = null;
                    _this.showData.phash = 1
                    _this.showDrawer = true
                    _this.timer = new Date().getTime()
                }


            },
