                    _this.totalNum = res.data.resultValue.total;
                    _this.tableDataShow = _this.getShowDateTable(_this.tableData, _this.dictMap, _this.columnTypeMapDict);
                })
            },
            //获取数据字典
            getDictArr(labelList) {
                let map = new Map();
                if (typeof (labelList) != 'undefined' && labelList != null && labelList.length > 0) {
                    for (let i = 0; i < labelList.length; i++) {
                        let temp = labelList[i];
                        if (temp.codeList != null && temp.codeList.length != 0) {
                            map.set(temp.colDname, temp.codeList);
                        }
                    }
                }
                return map;
            },
            //获取展示数据
            getShowDateTable(tableData, dictMap, columnDictMap) {
                let _this = this
                let arr = [];
                for (let i = 0; i < tableData.length; i++) {
                    let tempObj = tableData[i];
                    //拷贝对象
                    let copyObj = new Object();
                    //遍历获取的数据中的属性，如果属性名有和数据字典中的key相对应的，取出数据字典对应的值赋值过去
                    for (let [key, value] of Object.entries(tempObj)) {
                        //console.log(key + ':' + value);
                        let lowerKey = key.toLowerCase()
                        if (dictMap.has(key)) {
                            let dictValueList = dictMap.get(key);
                            for (let j = 0; j < dictValueList.length; j++) {
                                let tempObj2 = dictValueList[j];
                                if (tempObj2.codeValue == value) {
                                    copyObj[key] = tempObj2.codeName;
                                }
                            }
                        } else if (dictMap.has(lowerKey)) {
                            let dictValueList = dictMap.get(key.toLowerCase);
                            for (let j = 0; j < dictValueList.length; j++) {
                                let tempObj2 = dictValueList[j];
                                if (tempObj2.codeValue == value) {
                                    copyObj[key] = tempObj2.codeName;
                                }
                            }
                        } else if (columnDictMap.get(key) == "date" || columnDictMap.get(key) == "month"
                            || columnDictMap.get(key) == "year") {
                            if(value!=null&&value!=''){
                                copyObj[key] = _this.formartDate(value, columnDictMap.get(key));
                            }  
                        } else {
                            copyObj[key] = value;
                        }

                        if (key == "itemClass") {
                            copyObj["itemClassT"] = value
                        }

                        if (key == "conDept") {
                            copyObj["conDept"] = value
                        }
                        if (key == "itemLevel") {
                            copyObj["itemLevelT"] = value
                        }

                        //add by liangwu
                        if (key == "specDetailClass") {
                            copyObj["specDetailClassT"] = value
                        }

                        if (key == "specClass") {
                            copyObj["specClassT"] = value
                        }
                        //add end
                        
                        //add by gao.feng 填报人和创建人一致
                        if(key=="applyId"){
                            copyObj["applyId"] = tempObj.createor
                        }
                    }
                    if (typeof (tempObj.children) != 'undefined' && tempObj.children != null && tempObj.children.length > 0) {
                        let tempArr = _this.getShowDateTable(tempObj.children, dictMap, columnDictMap)
                        copyObj.children = tempArr
                    }
                    arr.push(copyObj);
                }
                return arr;
            },
            //获取插入和修改的表头
            getLabelList(labelList) {
                let _this = this;
                labelList.forEach(function (item) {
                    // if(item.displayType=='dropdown'||item.displayType=='cascadeDropdown'){
                    _this.$set(item, 'value', '');
                    // }else{
                    // 	_this.$set(item, 'value', null);
                    // }

                });
                //console.log(labelList);
                //let common_table_info = {};//数组转对象，避免类型报错
                for (let i in labelList) {
                    _this.saveLabel[i] = labelList[i];
                    let tempObj = labelList[i];
                    //构建是否必填规则
                    if (tempObj.isMandatory == 1) {
                        //必填项
                        let rule = new Object();
                        rule.required = true;
                        rule.message = tempObj.dispName + '不能为空';
                        rule.trigger = 'blur';
                        _this.saveLabel[i].rule = rule;
                    } else {
                        let rule = new Object();
                        rule.required = false;
                        _this.saveLabel[i].rule = rule;
                    }
                }
            },
            //编辑可研
            adjustStudy(data) {
                let _this = this;
                //判断规划总投资
                // if(typeof(data.ghInvest) == "undefined"||data.ghInvest==null||data.ghInvest==''){
                // 	this.$message({
                // 		showClose: true,
                // 		message: '请先填写总投资',
                // 		type: 'warning'
                // 	});
                // 	return false
                // }
                if (data.itemClassT == '3') { //零星项目上传的是项目建议书
                    _this.kyTitle = '上传项目建议书'
                } else {
                    _this.kyTitle = '上传可研报告'
                }

                _this.uploadReportItemId = data.itemId;
                _this.uploadReportData.itemId = data.itemId;
                _this.loading = true;
                _this.fileList = [];
                selectReport({itemId: data.itemId})
                    .then(response => {
                        console.log('response', response); //后端返回数据
                        _this.loading = false;
                        if (response.data.successful) {
                            let fileName = response.data.resultValue;
                            if (fileName.length > 0) {
                                let fileObj = {};
                                fileObj.name = fileName;
                                fileObj.url = '';
                                _this.fileList.push(fileObj);
                            }
                            //提交后 刷新页面
                            _this.getTable(_this.currentPage, _this.currentNum);
                        }
                    })
                    .catch(error => {
                        console.log(error); //error
                    });
                _this.tzkeVisible1 = true;
            },
            handleExceed(files, fileList) {
                let _this = this;
                if (files.length > 1) {
                    _this.$message.warning(`当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
                }
                fileList[0] = files[0];
                _this.fileList[0] = files[0];
                this.$message.warning(`当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
            },
            beforeRemove(file) {
                return this.$confirm(`确定移除 ${file.name}？`);
            },
            //生成专业类别列表
            specClassList() {
                let _this = this;
                let path = _this.$route.path
                _this.specClass = path.substr(path.lastIndexOf('/') + 1, path.length)
                //赋值showData
                _this.showData.specClass = _this.specClass
            }
        }
    };
</script>
