    //查询项目列表
    getTable(page,size){
      let _this = this
      _this.loading = true;
      _this.param.pageIndex = page;
      _this.param.pageSize = size;
      _this.param.filter = ''
      _this.param.filter = "isPassed="+_this.param.isPassed
      if(_this.param.isPassed==0){
          // _this.param.filter += "&itemPhaseDet=3,10,11,15,16,20,13"
          _this.param.filter += "&itemPhaseDet=7,14,15,17,19,20,24"
      }else{
          // _this.param.filter += "&itemPhaseDet=1,2,4,5,6,7,8,9,12,14,17"
          _this.param.filter += "&itemPhaseDet=4,5,6,8,9,10,11,12,13,16,18,21"
      }
      let filter = ''  
      //拼接查询条件
      for(let [key, value] of Object.entries(_this.param)) {
          if(key=="pageIndex"||key=="pageSize"||key=='itemPhaseDet'||key=='isPassed'||key=='filter'||value==''){
              continue
          }
          console.log(key + ':' + value);
          filter+='&'+key+'='+value
      }
      _this.param.filter += filter
      selectSubmitPlanList(_this.param).then(response => {//{}写需要的参数
        console.log('response',response.data.resultValue)//后端返回数据
        if(response.data.successful){
          _this.totalNum = response.data.resultValue.totalNum
          //获取表头信息
          _this.tableLabel = response.data.resultValue.tableLable
          //提取表头字典
          _this.dictMap = _this.getDictArr(_this.tableLabel);
           _this.columnTypeMap = _this.getColumnTypeArr(_this.tableLabel)
          //获取列表原始数据
          _this.tableData = response.data.resultValue.tableData
          //获取列表展示数据
          _this.tableDataShow = _this.getShowDateTable(_this.tableData)

        }
        _this.loading = false

         _this.param.filter = ""

      }).catch(error => {
        console.log(error)//error
      })

    },
    //获取字典
    getDictArr(labelList){ 
      let _this = this
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        if(temp.codeList!=null){
          if(temp.colDname=="speciality"){
						//过滤当前登录人的专项
						let arr = []
						for(let i=0;i<temp.codeList.length;i++){
							for(let j=0;j<_this.userSpecialityArr.length;j++){
								if(temp.codeList[i].codeValue==_this.userSpecialityArr[j]){
									arr.push(temp.codeList[i])
								}
							}
						}
						temp.codeList = arr
					}
          map.set(temp.colDname,temp.codeList);
        }
      }
      return map
    },
    //获取字段类型字典
    getColumnTypeArr(labelList){
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        map.set(temp.colDname,temp.colType);
      }
      return map
    },
    //获取列表展示字段
    getShowDateTable(tableData){
      let _this =this
      let arr = [];
      for(let i=0;i<tableData.length;i++){
        let tempObj = tableData[i];
        //拷贝对象
        let copyObj = new Object();
        //遍历获取的数据中的属性，如果属性名有和数据字典中的key相对应的，取出数据字典对应的值赋值过去
        for (let [key, value] of Object.entries(tempObj)) {
          //console.log(key + ':' + value);
          if(key=="itemPhaseDet"){
               copyObj["itemPhaseDetT"] = value;
          }
          if(key=="speciality"){
              copyObj["specialityT"] = value;
          }
          if(key=="itemClass"){
              copyObj["itemClassT"] = value;
          }
          if(_this.dictMap.has(key)){
            let dictValueList = _this.dictMap.get(key);
            for(let j=0;j<dictValueList.length;j++){
              let tempObj2 = dictValueList[j];
              if(tempObj2.codeValue==value){
                copyObj[key] = tempObj2.codeName;
              }
            }
          }else if(_this.columnTypeMap.get(key)=="DATE"){
               copyObj[key+"Rel"] = value
               let timeStr = _this.formartDate(value)
               copyObj[key] = timeStr;
          }else{
            copyObj[key] = value;
          }
        }
        arr.push(copyObj);
      }
      return arr;
    },
    //获取插入和修改的表头
    getLabelList(labelList){ 
      let _this =this
      labelList.forEach(function (item) {
        _this.$set(item,'value','');
      })
      let common_table_info = {};//数组转对象，避免类型报错
      for(let i in labelList){
        common_table_info[i] = labelList[i];
      }
      return common_table_info
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      let _this = this
      if(files.length>1){
          _this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
      }
      fileList[0] =  files[0] 
      _this.fileList[0] = files[0]
      this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${ file.name }？`);
    }
  }
}
</script>
