<template>
  <div class="container">
    
    <div class="main clearfix" :style="{'margin-left':$store.state.app.isCollapse ? '94px':'240px'}">
      
      <div class="main_container">
      <!-- query:{{$route.query.tmplTypeId}} -->
        <!-- <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item v-for="mat in $route.matched" ><span class="color_tip">{{mat.name}}</span></el-breadcrumb-item>
        </el-breadcrumb> -->
        
        <div class="clearfix template_main">
          <div class="template_left" v-if="showLeft" v-loading="leftLoading">
            <ul>

              <li v-for="(item,index) in tempList" :key="index" :class="{'active' : selectLeft == index}" @click="getTemplateTable($route.query.tmplTypeId,item.codeValue,index+'')"> {{item.codeName}} </li>
              <!-- <li class="active">电网基建</li> -->
              
            </ul>
            
          </div>
          <div class="template_right">
          
            <div class="template_table">
              <el-table
              border
              v-loading="templateLoading"
              :data="tableData"
              :header-cell-style="{background:'#359691',color:'#fff'}"
              row-class-name="row"
              cell-class-name="column"
              :height="tableHeight"
              >
              <el-table-column
                type="index"
                width="50">
              </el-table-column>
              <el-table-column
                prop="tmplName"
                label="模版名称"
                >
                <!-- <template slot-scope="scope" >
                  {{$route.name}}
                </template> -->
              </el-table-column>
              <el-table-column
                prop="tmplCreator"
                label="创建者"
                >
              </el-table-column>
              <el-table-column
                prop="createDate"
                label="创建日期"
                :formatter="dateFormat"
                >
              </el-table-column>
              
              <el-table-column
                prop="tmplCont"
                label="说明">
              </el-table-column>
              <el-table-column
                prop="release"
                label="发布"
                >
                <template slot-scope="scope">
                  <span class="blue" v-if="scope.$index+1==tableData.length" @click="issueTemplate(scope.row.tmplId)">发布</span>
                  <span v-else>发布</span>
                </template>
              </el-table-column>
              <el-table-column
                align="center"
                label="下载">
                <template slot-scope="scope">
                  <!-- 下载excel -->
                  <span class="iconfont download blue" v-if="scope.$index+1==tableData.length">
                  <a v-if="$route.query.tmplTypeId==1|| $route.query.tmplTypeId==2 || $route.query.tmplTypeId==3 || $route.query.tmplTypeId==8 || $route.query.tmplTypeId==7" :href="GLOBALURL+'/prjStTmpl/export?templateType='+$route.query.tmplTypeId+'&token='+token">&#xe675;</a>
                  <a v-else-if="$route.query.tmplTypeId==5|| $route.query.tmplTypeId==4 || $route.query.tmplTypeId==9 || $route.query.tmplTypeId==11" :href="GLOBALURL+'/prjStTmpl/downloadTmpl?templateType='+$route.query.tmplTypeId+'&codeValue='+codeValue+'&token='+token">&#xe675;</a>
                  <a v-else :href="GLOBALURL+'/prjStTmpl/downloadTmpl?templateType='+$route.query.tmplTypeId+'&token='+token">&#xe675;</a>
                  </span>
                  <!-- 下载文档 -->
                  <!-- <a :href="'http://111.30.23.191:11115/prjStTmpl/uploadTmpl?templateType='+$route.query.tmplTypeId">&#xe675;</a></span> -->
                  
                  <span class="iconfont download" v-else>&#xe675;</span>
                </template>
                <!-- <template slot-scope="scope">
                  <span class="iconfont download">&#xe675;</span>
                </template> -->
              </el-table-column>
              <el-table-column
                align="center"
                label="删除">
                <!-- <template slot-scope="scope">
                  <span class="iconfont delete">&#xe607;</span>
                </template> -->
                <template slot-scope="scope">
                  <span class="iconfont download blue"  @click="deleteTmp(scope.row.tmplId)">&#xe607;</span>
                  <!-- <span class="iconfont download" v-else>&#xe607;</span> -->
                
                </template>
              </el-table-column>
            </el-table>
            
              <div class="upbox" v-if="showUpload">
                
                <el-upload
                  class="upload"
                  :action="GLOBALURL+'/prjStTmpl/uploadTmpl'"
                  :data="{'templateType':$route.query.tmplTypeId,'codeValue':codeValue}"
                  :before-upload='beforeupload'
                  :headers="{'userId':windowUid,'token':token}"
                  :on-error ="errorUpload"
                  v-loading="uploading"
                 :on-success="uploadSuccess"
                  :multiple='false'
                  :show-file-list="false">
                  <span class="iconfont">&#xe608;</span> 上传模版
                  
                </el-upload>
              </div>
              <div class="upbox " v-else><span @click="addTmpData" class="add"><span class="iconfont">&#xe606;</span> 新建模版</span></div>
              
            </div>
            
          </div>
        </div>

      </div>
    </div>
    <!-- <right></right> -->
    <el-dialog :title="$route.name" :close-on-click-modal = "false" width="525px" :visible.sync="DialogConfiguration" >
      
        <el-table
          :data="tableDatatest"
          v-loading="loading"
          border
          max-height="230px"
          highlight-current-row 
          :header-cell-style="{background:'#359691',color:'#fff'}"
          style="width: 100%">
            
          <el-table-column
            prop="name"
            label="数据项中文名">
            <template slot-scope="scope" >
              <div v-if="scope.row.showselect">
                <el-input v-model="scope.row.name" class="rowinp" @change="showAddData" size="small" ></el-input>
              </div>
              <div v-else>{{scope.row.fieldName}}</div>
            </template>
          </el-table-column>
          
          <el-table-column
            prop="date"
            label="类型">
            <template slot-scope="scope" >
              <div v-if="scope.row.showselect">
                <el-select v-model="scope.row.dataType" class="rowinp" @change="showAddData" placeholder="请选择">
                  <el-option
                            v-for="item in regionList"
                            :key="item.fieldType"
                            :label="item.fieldZhName"
                            :value="item.fieldType">
                    </el-option>
                </el-select>
              </div>
              <div v-else>{{scope.row.fieldZhName}}</div>
              
            </template>
          </el-table-column>
        </el-table>
        <div class="addbtn" style="background:#359691;color:#fff;text-align:center;line-height:30px;cursor: pointer;margin-bottom:10px"  @click="addTmp">新建</div>

        <el-form ref="form" :inline="true"  :model="templateDescForm" label-width="100px">
            <el-form-item label-width="80px" label="版本说明">
              <el-input type="textarea" size="mini" v-model="templateDesc" style="width:269px;"></el-input>
              
            </el-form-item>
          </el-form>
        
          
        <div slot="footer" class="dialog-footer">
          <el-button @click="DialogConfiguration = false">取 消</el-button>
          <el-button type="primary" @click="submitAdd">确 定</el-button>
        </div>
        
    </el-dialog>
  </div>
</template>

<script>
// @ is an alias to /src
import { mapGetters } from 'vuex'

import right from '@/views/right.vue'
import {getPrjStTmpl,deleteTemplate,issueTemplate,selectColType,createTemplate,selectField,selectReportTypeList} from '@/api/data'


export default {
  name: 'questions',
  data() {
  	return {
      windowUid:'',
      templateDescForm:{},
      templateDesc:'',
      selectLeft:0,
      uploading:false,
      loading:false,
      showLeft:true,
      showUpload:true,
      DialogConfiguration:false,
      region:'',
      tableDatatest:[],
      tableData: [],
      regionList:[],
      tempList:[],
      leftLoading:false,
      templateLoading:false,
      codeValue:'',
      tableHeight:'',
      token:''
  	}
  },
  // watch:{
  // $route(to,from){
  //     console.log(to.path);
  //     let _this = this
  //     if(to.name == '市场导向需求模版' || to.name == '战略导向需求模版' || to.name == '问题导向需求模版'){
  //       _this.showLeft = true

  //     }else{
  //       _this.showLeft = false

  //     }
  //     if(to.name == '可研报告模版' ||to.name == '项目建议书模版' ||to.name == '市场导向需求模版' || to.name == '战略导向需求模版' || to.name == '问题导向需求模版'){
  //       _this.showUpload=false
  //     }else{
  //       _this.showUpload=true

  //     }
  //   }
  // },

watch: {
    $route:{
      handler:function(to,from){
        // this.$route.query.tmplTypeId
        let _this = this
        if(to.query.tmplTypeId == 5 || to.query.tmplTypeId == 4 || to.query.tmplTypeId == 9 || to.query.tmplTypeId == 11){
          _this.getTempList({'listType': to.query.tmplTypeId}).then(data => {
            _this.codeValue = _this.tempList[0].codeValue
            _this.getTemplateTable(to.query.tmplTypeId,_this.tempList[0].codeValue,'0')
          })
          

        }else{
          _this.codeValue=''
          _this.getTemplateTable(to.query.tmplTypeId)
        }
        if(to.name == '可研报告模版' || to.name == '项目建议书模版'|| to.name == '可研评审意见模版'|| to.name == '可研批复意见模版' ){
          _this.showLeft = true

        }else{
          _this.showLeft = false

        }
        if(to.name == '可研评审专家模版' ||to.name == '评审计划模版' ||to.name == '市场导向需求模版' || to.name == '战略导向需求模版' || to.name == '问题导向需求模版'){
          _this.showUpload=false
        }else{
          _this.showUpload=true

        }
      },
      immediate: true
    } 
  },
  created(){
    this.tableHeight=window.innerHeight-150;
  },
  components: {
    right
  },
  mounted(){
    this.windowUid = sessionStorage.getItem('userId')
    this.token = sessionStorage.getItem('token')
    // window.onresize = () => {
    //   return (() => {
    //     this.tableHeight=window.innerHeight-220+'px';
    //   })()
    // }
  },
   computed: {
    ...mapGetters([
      'token',
    ])
  },
  methods: {

    errorUpload(){

      this.uploading = false
      this.$message({
            type: 'error',
            message: '上传失败'
          });  
    },
    getTempList(data){
      return new Promise((resolve, reject) => {
        this.leftLoading = true
        selectReportTypeList(data).then(response => {
          console.log('response',response)
          if(response.data.successful){
            this.tempList = response.data.resultValue
            this.codeValue = this.tempList[0].codeValue
            this.leftLoading = false
          }
          resolve(this.tempList)
        }).catch(error => {
          console.log(error)//error
        })
      })
    },
    submitAdd(){
      let _this = this
      let addData = {
      "columnList": [],
      "templateDesc": _this.templateDesc,
      "templateName": "string",
      "templateType": _this.$route.query.tmplTypeId
    }
      _this.tableDatatest.forEach((item,idx) => {
        console.log(item.showselect)
        if(item.showselect){

          addData.columnList.push(item)
        }
      })
      
      // console.log(addData)
      createTemplate(addData).then(response => {
        console.log('response',response)
        if(response.data.successful){
          _this.DialogConfiguration = false
          _this.tableDatatest =[]
          _this.getTemplateTable(_this.$route.query.tmplTypeId)

           _this.$message({
            type: 'success',
            message: '新建成功'
          });

        }else{
          _this.$message({
            type: 'info',
            message:response.data.resultHint
          });  
        }
       
        
      }).catch(error => {
        console.log(error)//error
      })

    },
    showAddData(){
      console.log(this.tableDatatest)
    },
    issueTemplate(id){
      let _this = this
      issueTemplate(id).then(response => {
        console.log('response',response)
        if(response.data.successful){
          _this.getTemplateTable(_this.$route.query.tmplTypeId,_this.codeValue)

           // _this.tableData = response.data.resultValue.items
           _this.$message({
            type: 'success',
            message: '发布成功'
          });

        }else{
          _this.$message({
            type: 'info',
            message: '发布失败'
          });  
        }
       
        
      }).catch(error => {
        console.log(error)//error
      })
    },
    addTmpData(){
      let _this = this
      console.log('999')
      _this.tableDatatest=[]
      _this.DialogConfiguration =  true
      selectField({'templateType':_this.$route.query.tmplTypeId}).then(response => {
        if(response.data.successful){
          console.log(response)
          response.data.resultValue.forEach((item,idx) => {
            _this.tableDatatest.push(item)
          })
        }
       
        console.log('tableDatatableData',_this.tableDatatest)
      }).catch(error => {
        console.log(error)//error
      })

      selectColType({'templateType':_this.$route.query.tmplTypeId}).then(response => {
        if(response.data.successful){
          console.log(response)
          _this.regionList = response.data.resultValue

        }
       
        
      }).catch(error => {
        console.log(error)//error
      })
    },
    addTmp(){
      let _this= this
      
      
      let addItem ={
          name: '',
          dataType: '',
          showselect:true
        }
      _this.tableDatatest.push(addItem)
    },
    deleteTmp(id){
      let _this = this
      _this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let deleteForm = new FormData()
          deleteForm.append('tmplId',id)
          deleteTemplate(deleteForm).then(response => {
            console.log('response',response)
            if(response.data.successful){
              _this.getTemplateTable(_this.$route.query.tmplTypeId,_this.codeValue)

              console.log("chengg")
               // _this.tableData = response.data.resultValue.items
               _this.$message({
                type: 'success',
                message: '删除成功!'
              });

            }else{
              _this.$message({
                type: 'info',
                message: '删除失败'
              });  
            }
           
            
          }).catch(error => {
            console.log(error)//error
          })
          
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
    },
    dateFormat(row, column) {
        var moment = require('moment');
        var date = row[column.property];
        return moment(date).format("YYYY-MM-DD")
    },
    getTemplateTable(tmplTypeId,codeValue,index){
      let _this = this
      let data={}
      console.log(tmplTypeId,codeValue,index)
      if(codeValue){
        if(index){
          console.log(index)
          _this.selectLeft = index
        }
        _this.codeValue = codeValue
        data = {'tmplTypeId':tmplTypeId,'codeValue':codeValue}
      }else{
        data = {'tmplTypeId':tmplTypeId}
      }
      _this.templateLoading = true

      getPrjStTmpl(data).then(response => {
          console.log('response',response)
          _this.templateLoading = false
          if(response.data.successful){
             _this.tableData = response.data.resultValue.items

          }
          _this.loading = false
          
        }).catch(error => {
          console.log(error)//error
        })

    },
    beforeupload(){
      this.uploading =  true
    },
    uploadSuccess(response, file, fileList){
      console.log(response)
      let _this = this
      _this.uploading =  false

      if(response.successful){
          _this.$message({
                showClose: true,
                message: "上传成功",
                type: 'success'
        });
        _this.getTemplateTable(_this.$route.query.tmplTypeId,_this.codeValue)
      }else{
        this.$message({
                showClose: true,
                message: response.data.resultHint,
        });
      }
      

    }
  }
}
</script>
<style type="text/css">
  .el-dialog .rowinp .el-input__inner,.el-dialog .el-select.rowinp > .el-input{
    width:150px!important;
  }
  .template_left{ 
    width:20%;
    text-align:center;
    height:calc(100vh - 106px);
    overflow-y:auto
  }
  /* 修改选中状态时背景颜色 */
</style>
