  .template_left{
    margin-top:20px;
  }
  .template_left li{
    display:block;
    border:solid 1px #d4d4d4;
    margin-bottom:15px;
    text-align:center;
    padding:10px;
    cursor:pointer
  }
  
  .template_left li.active{
    border-color:#359691
    background-image: linear-gradient(to right,#359691 0%,#ffffff 25%) !important;
  }
  .template_right{
   flex-grow:9;
   width:80%;
   position:relative
  }
  
  .template_table{
    margin:20px 0 0 20px;
    position:relative
  }
  
  .upbox{
    border:solid 1px #d4d4d4;
    width:100%;
    border-top:none;
