
// initial state
  const state = {
    pathChangeActive:false,
      addItemParams: { 
          itemId:'',
          specClass:'',
          specClassName:''
      },
    //   是否点击了保存按钮
      isSaved:false,
  }
  
  // getters
  const getters = {}
  
 
  
  // mutations
    const mutations = {
      SET_PARTHCHANGEACTIE: (state, pathChangeActive) => {
        state.pathChangeActive = pathChangeActive
      },
        setItemParams(state, addItemParams) { //设置参数
            state.addItemParams = addItemParams;
        },
        setItemParamStatus(state, isSaved) { //设置参数
            state.isSaved = isSaved;
        }
    }

   // actions
   const actions = {
    setPathChangeActive({ commit }, pathChangeActive) {
    return new Promise((resolve, reject) => {
        commit('SET_PARTHCHANGEACTIE', pathChangeActive)
      }).catch(error => {
        reject(error)
      })
    },
    setItemParams(state, addItemParams) { //设置参数
        state.addItemParams = addItemParams;
    },
    setItemParamStatus({ commit }, isSaved) { //设置参数
        commit('setItemParamStatus',isSaved)
    }
  }
  
  export default {
      namespaced: true,
      state,
      mutations,
      actions,
      getters
  }
