				pageIndex: 1,
				itemName: '',  //项目名称
				itemType: '',  //项目类型
				pageSize: 10,
				speciality: '',  //专业类型
				voltLevel: '', //电压等级
				feaStatus: 0,  //可研状态
				ghYear: '',     //规划年份
				itemSource:'',
				isCreateor:'',
				itemPhaseDet:''//项目状态
			},
			// dictMap: Object,
			dictMap: new Map(),
			 //查询条件
            selectParamDict: new Map(),
			tableDataShow: [],
