			saveLabel: {},
			//项目可研报告文件名
			reportFileName: '',
			//获取字段类型字典，用于日期类型的展示
			columnTypeMap: new Map(),
			isShowFilter: false,
			//自动表格高度
			tableHeight: '',
			tableHeightTab:"",

			auditPlanId:"",
			//点击提交传入的参数
			saveParams:[],
			//用户所属专项类型值
			userSpecialityArr:[],
			//上传可研评审的项目
			bzkyArr:[],
			kyTitle:'',
			fileList:[],
			fileList2:[],
			reportFileId:'',
			itemIds:"",
			fjIds:[],
			userId:'',
			//上传科研报告时携带参数
			uploadReportData: { itemId: '', itemType: 1 },
			//上传科研报告提交实体
			formInline: { user: '' },
			//专项类型
			tempSpecClass:'',
			submitBtn:false,
			token:''
		};
	},
	components: {
		querys,
		SelectTree,
		ItemAddCom
	},
	created() {
		//表格自动高度
    	this.tableHeight=window.innerHeight-220;
	},
	mounted() {
		this.getItemLabel()
		this.getSpecialityIds()
		this.getTableWithTitle(1, 20);

		this.userId = sessionStorage.getItem('userId');
		this.token = sessionStorage.getItem("token");
		// this.getTreeDeptCode()
	},
	watch:{
		$route:{
			handler:function(to,from){
				let _this = this
				console.log('路由变化')

				//赋值showData
				_this.showData.specClassName = _this.$route.name;
				_this.showData.oldPath=_this.$route.path
				_this.showDrawer = false;

			},
			immediate: true
		},
		deptCode(){
			this.param.createOrg = this.deptCode
			this.currentPage = 1
			this.getTable(1,this.currentNum)
		}
	},
	computed: {
		...mapGetters([
			// 'token',
		])
    },
	methods: {
		// 全部项目
		getAllItems(){
			this.currentPage = 1;
			this.param.isCreateor = ""
			this.getTable(1,10)
		},
		// 我的项目
		getMyItems(){
			this.param.isCreateor = "1"
			this.currentPage = 1;
			this.getTable(1,10)
		},
		getValue(value){
			this.deptCode = value
		},
		// 导出
		exportItemKY() {
			window.location.href=this.GLOBALURL+'/stBaseItem/exportItem?itemIds='+this.exportData.ids+'&token='+this.token
		},
		// 退回
		sendBack() {
			if(this.backData.length == 0){
				this.$message({
					showClose: true,
					message: '请选择一个项目',
					type: 'warning'
				});
				return false
			}
			let itemIdArr = []
			//校验所选项目有没有上传可研报告
			for(let i=0;i<this.backData.length;i++){
				let tempItem = this.backData[i]
				
				//校验是否为创建者 add by gao.feng
				if(this.userId!=tempItem.createorT){
					this.$message({
						showClose: true,
						message: "所选项目中包含非当前用户创建的项目",
						type: 'warning'
					});
					return false
				}

				if(tempItem.itemPhaseDetT!=3){
					this.$message({
						showClose: true,
						message: "所选项目中包含非立项审核通过状态的项目",
						type: 'warning'
					});
					return false
				}

				itemIdArr.push(tempItem.itemId);
			}
			let param = {
				itemIds : itemIdArr.toString()
			}
			rollBackItem(param).then(res => {
				if(res.data.successful){
					this.getTable(this.currentPage,this.currentNum)
				}
			})

		},
		// 标识
		tableRowClassName({row, rowIndex}) {
			if (row.itemPhaseDetT == 27) {
				return 'success-row';
			}else{
			    return '';
			} 
		
		},
		// 子组件向父组件传递参数
		changeStatus(status) {
			this.showDrawer = status
			console.log('status'+status)
		},
		//展示项目详细信息
		showDetailDialog(row) {
			this.showData.itemId = row.itemId
			this.showData.phash = 0
			this.showDrawer = true;
			this.timer = new Date().getTime()
		},
		//获取查询条件下拉框
		getItemLabel() {
			let _this = this
			queryFilterParam().then(response => {
				console.log('response', response)//后端返回数据
				if (response.data.successful) {
					let labelList = response.data.resultValue.tableLable
					_this.selectParamDict.set('voltLevel', response.data.resultValue.voltCodeList)
					_this.selectParamDict.set('itemLevel', response.data.resultValue.itemLevelList)
					_this.selectParamDict.set('itemSource', response.data.resultValue.itemSourceList)
					_this.selectParamDict.set('itemPhaseDet', response.data.resultValue.itemPhaseDet)
					_this.selectParamDict.set('speciality', response.data.resultValue.specialityList)
					// _this.$forceUpdate()
				}
			})
		},
		//获取单位树形结构
		getTreeDeptCode(){
			let _this = this
			getUserDept().then(response=>{
				let valueId = response.data.resultValue
				getTreeDeptCode().then(response=>{
					console.log(response)
					if(response.data.successful){
						_this.valueId = valueId
						_this.deptArr = response.data.resultValue
					}
				})
			})

		},
		changeQuery(state,height){
	      if(state){
	        this.tableHeight = height
			this.isShowQuery = true
			this.showQuery = true
	      }else{
	        this.tableHeight = height
			this.isShowQuery = false
			this.showQuery = false

	      }
		},
		// 点击提交
		checkDialogSubmit(){
			let _this = this
			// this.showDrawer = false;
			if(this.bzkyArr.length == 0){
				this.$message({
					showClose: true,
					message: '请选择一个项目',
					type: 'warning'
				});
				return false
			}
			let itemIdArr = []
			//校验所选项目有没有上传可研报告
			for(let i=0;i<this.bzkyArr.length;i++){
				let tempItem = this.bzkyArr[i]
				
				//校验是否为创建者 add by gao.feng
				if(this.userId!=tempItem.createorT){
					this.$message({
						showClose: true,
						message: "所选项目中包含非当前用户创建的项目",
						type: 'warning'
					});
					return false
				}

				if(tempItem.itemPhaseDetT != "4"){
					this.$message({
						showClose: true,
						message: "所选项目状态必须是'已上传可研'",
						type: 'warning'
					});
					return false
				}

				if(tempItem.havePrep==null){
					this.$message({
						showClose: true,
						message: '所选项目中包含未上传可研报告的项目',
						type: 'warning'
					});
					return false
				}

				itemIdArr.push(tempItem.itemId);
			}
			this.showLoadingBtn = true
			let _thisItemIds = itemIdArr.toString()
			checkItemParent({'itemIds':_thisItemIds}).then(res => {
				if(res.data.successful){
					let resultVal = res.data.resultValue
					// this.showLoadingBtn = false
					if(!resultVal.result){
						this.showLoadingBtn = false
						_this.$message({
							showClose: true,
							message: "不能选择包含上级的项目",
							type: 'warning'
						});
						return false
					}

					let itemIdStr = itemIdArr.join(",")
					checkFeaDInvest({itemIds:itemIdStr}).then(res=>{
						
						if(res.data.successful){

							let flag = res.data.resultValue
							if(!flag){
								this.showLoadingBtn = false
								this.$message({
									showClose: true,
									message: '所选项目中包含未填写可研动态总投资的项目',
									type: 'warning'
								});
								return false
							}else{	
								getAuditClass({itemIds:itemIdStr}).then(res=>{
									
									if(res.data.successful){
										let flagT = res.data.resultValue.flag
										if(flagT){
											_this.showDialogSubmit()
										}else{
											this.showLoadingBtn = false
											let str = res.data.resultValue.msg
											this.$message({
												showClose: true,
												dangerouslyUseHTMLString: true,
												message: str,
												type: 'warning'
											});
											return false
										}
									}
								})
							}
						}else{
							this.showLoadingBtn = false
						}
					})
				}else{
					this.showLoadingBtn = false
				}
			})
			


		},
		// 点击提交
		showDialogSubmit(){
			let submitItems = {}
			let _thisItemIds = []
			if(this.$store.state.user.workflowtype==0) {
				//modify by liangwu 2020.6.25 调用流程中心流程
				let specRoleID = '';//专项角色ID
				let iscOrg = window.JSON.parse(sessionStorage.getItem("iscOrg"));//isc组织机构
				let specRoleMap = window.JSON.parse(sessionStorage.getItem("specRoleMap"));//isc专项role
				let purchaseRoleMap = window.JSON.parse(sessionStorage.getItem("purchaseRoleMap"));//isc零购专项role
				let electronicRoleMap = window.JSON.parse(sessionStorage.getItem("electronicRoleMap"));//isc电网基建专项role
				let userid = sessionStorage.getItem("userId");


				let params = {
					"user": userid,
					"orgIds": iscOrg.id,
					"roleIds": specRoleID,
					"processDefName": "feasibility_study_report_review",
					"activityDefID": 'review',
					"processInstName": '',
					"processInstDesc": '',
					"itemId":""
				}
				for(let i in this.bzkyArr){
					let item = this.bzkyArr[i];
					specRoleID = specRoleMap[item.specialityT];//根据专项类型
					params.roleIds = specRoleID;
					if (item.specialityT== '01') {//如果是电网基建角色从electronicRoleMap取
						specRoleID = electronicRoleMap[item.specDetailClassT];
						params.roleIds = specRoleID
					}
					if (item.specialityT == '08') {//如果是电网基建角色从purchaseRoleMap取
						specRoleID = purchaseRoleMap[item.specClassT];
						params.roleIds = specRoleID
					}
					params.itemId = item.itemId
					params.processInstName = item.itemName + '-项目可研编制审核'
					params.processInstDesc = item.itemName + '-项目可研编制审核'

					if(params.orgIds == undefined){
						params.orgIds = "";
					}

					if(params.roleIds == undefined){
						params.roleIds = "";
					}

					//调用流程中心启动
					createAndReassignAndfinishWorkItemWithOption(params).then(res => {
						if(res.data.status == 0)
						{
							let finishparams = {
								processInstID: res.data.data,
								userId: userid,
								itemId:item.itemId,
								type:1
							}
							// 将返回值信息及项目信息插入后端数据库
							createProcess(finishparams).then(finishres => {
								if (finishres.data.successful) {
									let saveWorkItemParams ={
										processInstID:res.processInstID,
										itemId:item.itemId,
										userId:userid,
										isAudit:'',
										workItemId:'123',
										auditDesc:'可研编制评审申请',
										itemPhaseDet:'25'
									}

									saveWorkItem(saveWorkItemParams).then(res=>{
											this.showLoadingBtn = false

										if (finishres.data.successful) {
											this.$refs.itemTable.clearSelection()
											this.$message.success("提交可研编制评审申请成功")
											this.getTable(this.currentPage, this.currentNum)
										}
										else {
											let error = finishres.data.resultHint;
											this.$message.error(item.itemName + error);
										}
									})
								}else {
									this.showLoadingBtn = false
									let error = finishres.data.resultHint;
									this.$message.error(item.itemName + error);
								}
							});
						}else {
							this.showLoadingBtn = false
							this.$message.error(item.itemName + res.data.message);
						}
					})

				}
			}else {
				for(let i in this.bzkyArr){
					_thisItemIds.push(this.bzkyArr[i].itemId)
					// if(this.bzkyArr[i].itemPhaseDetT != "4"){
					// 	this.$message({
					// 		showClose: true,
					// 		message: '所选项目中包含未上传可研评审文件的项目，请重新选择',
					// 		type: 'warning'
					// 	});
					// 	return false
					// }
				}
				_thisItemIds = _thisItemIds.join(",")
				submitItems.itemIds = _thisItemIds
				let formData = new FormData()
				formData.append("itemIds",_thisItemIds)
				console.log(submitItems)
				applyFeaPrep(formData).then(res=>{
					this.showLoadingBtn = false
					if(res.data.successful){
						this.$refs.itemTable.clearSelection()
						this.$message.success("提交可研报告成功")
						this.getTable(this.currentPage,this.currentNum)

					}
				})
			}
