		//点击提交可研报告按钮
		submitUpload() {
			let _this = this
			_this.submitBtn = true
			// this.$refs.upload.submit();
			let formData = {}
			// _this.feasibilityData
			let choseArr = _this.bzkyArr
			let _itemIds = [];
			for(let i in choseArr){
				_itemIds.push(choseArr[i].itemId)
				if(choseArr[i].children && choseArr[i].children.length >0){
					let childArr = choseArr[i].children
					for(let k in childArr){
						_itemIds.push(childArr[k].itemId)
					}
					_itemIds = [...new Set(_itemIds)]
				}
			}
			_this.itemIds = _itemIds.join(",")
			console.log(_this.itemIds)
			// 校验只选择子项目要提示
			

				formData.itemIds = _this.itemIds
				formData.repId = _this.reportFileId
				formData.repName = _this.reportFileName
				formData.fjIds = _this.fjIds
				if(formData.repName == "" ||formData.repName == undefined){
					this.$message({
						showClose: true,
						message: '请上传可研报告',
						type:'warning'
					})
					return false
				}
				console.log(formData)
				uploadReport(formData).then(response=>{
					_this.submitBtn = false
					if(response.data.successful){
						_this.tzkeVisible1 = false;
						_this.fileList = [];
						_this.fileList2 = [];
						_this.bzkyArr = []
						_this.fjIds = []
						_this.reportFileId = ''
						_this.reportFileName = ''
						this.$refs.itemTable.clearSelection()
						this.$message.success("保存可研报告成功")
						_this.getTable(_this.currentPage,_this.currentNum)
					}else{
						this.$message.error("保存可研报告失败")
					}
				})
			

