                  </span>
                   <span class="blue" v-if="scope.row.fileName !=''&&scope.row.fileName !=null && userId == scope.row.userNo" @click="delFile(scope.row)">删除附件</span>
                   <span style="color:gray;padding:0 10px;" v-if="scope.row.fileName !=''&&scope.row.fileName !=null && userId != scope.row.userNo">删除附件</span>
              </template>
            </el-table-column>

        </el-table>
        <div class="page">
          
          <el-pagination
            background
