            :page-size="getTableData.pageSize"
            layout="sizes, total, prev, pager, next, jumper"
            :total="totalNum">
          </el-pagination>
        </div>

      </div>
    </div>
    <!-- <right></right> -->
    <!-- 新增 -->
    <el-dialog :title="dialogTit" :close-on-click-modal = "false" @close="cancel('lableForm')"  width="900px" :visible.sync="dialogFormVisible">
       <!-- v-if="domain.showType == 1" -->
        <el-form :model="lableList" :inline="true" label-position="right" ref="lableForm" v-loading="dialogLoading">
          <el-form-item v-for="(domain, index) in lableList"  :label="domain.dispName" :label-width="formLabelWidth"
            :prop=" index + '.value'"
