            required: isRequired(domain.isMandatory), message: domain.dispName+'不能为空', trigger: 'blur'
          }">
            <!-- <el-input v-if="domain.colType!='DATE'" v-model="domain.value" autocomplete="off" size="mini"></el-input>
            
            <el-date-picker
              v-if="domain.colType=='DATE'"
              v-model="domain.value"
              type="date"
              placeholder="选择日期">
            </el-date-picker>
          </el-form-item> -->
          <el-input
              v-if="domain.dispType=='text'" 
