            >
            </el-input>
            <el-input v-if="domain.dispType=='string' " v-model="domain.value" autocomplete="off"></el-input>
            <el-select v-model="domain.value" placeholder="请选择" v-if="domain.dispType=='dropdown'">
              <el-option
                v-for="item in domain.codeList"
                :key="item.codeId"
