                :value="item.codeValue">
              </el-option>
            </el-select>

            <el-date-picker
              v-if="domain.dispType=='date'"
              v-model="domain.value"
              type="date"
              placeholder="选择日期">
            </el-date-picker>
          </el-form-item>

          <el-form-item :label-width="formLabelWidth" v-if="showDownload" label="下载附件">
            <a :href="GLOBALURL+'/prjStDmd/downloadAccessory?dmdId='+downloadId+'&token='+token">{{fileName}}</a>
          </el-form-item>
        </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel('lableForm')">取 消</el-button>
        <el-button type="primary" :loading="submitLoading" @click="submitForm('lableForm')">确 定</el-button>
      </div>
