<script>
// @ is an alias to /src
import SelectTree from "@/components/treeSelect.vue";
import {getPrjStDmd , getSaveTable, savePrjStDmd,deletePrjStDmd,exportDmd,getTreeDeptCode,getUserDept,delDmdTemplFile} from '@/api/data'
import { mapGetters } from 'vuex'
import querys from '@/components/querys'


export default {
  name: 'questions',
  data() {
  	return {
      delLoading:false,
      submitLoading:false,
      parentData:[],
      deptCode:"",
      fileName:'',
      isClearable:true,      // 可清空（可选）
			isAccordion:true,      // 可收起（可选）
			valueId:"",            // 初始ID（可选）
			props:{                // 配置项（必选）
				value: 'id',
				label: 'name',
				children: 'children',
				code: 'deptCode'
				// disabled:true
			},
      deptArr:[],
      isShowQuery:false,
      downloadId:'',
      showDownload:false,
      tableHeight:'',
      dialogLoading:false,
      loading:false,
      delData:{
        "ids": [],
      },
      lableList:{},
      totalNum:0,
      currentPage:1,
      currentNum:20,
      year: '',
      dialogFormVisible:false,
      dialogTit:'',
      form: {
        
      },
      
      options: [],
      value:'',
      formLabelWidth: '120px',
      input: '',
  		// 子组件的表格数据
  		tableData: [
  			// {id: 1, date: '2018-07-24', sales: 23.34, sale: 137597.76, const: 102203.71, profit: 35394.05},
  			// {id: 2, date: '2018-07-24', sales: 23.34, sale: 137597.76, const: 102203.71, profit: 35394.05},
  			// {id: 3, date: '2018-07-24', sales: 23.34, sale: 137597.76, const: 102203.71, profit: 35394.05},
  			// {id: 4, date: '2018-07-24', sales: 23.34, sale: 137597.76, const: 102203.71, profit: 35394.05},
  			
  		],
  		// 子组件的表头数据
  		tableLabel: [
              // {label: '', width: '40', prop: 'id'},
              // {label: '日期', width: '', prop: 'date'},
              // {label: '销售量', width: '', prop: 'sales'},
              // {label: '销售额', width: '', prop: 'sale'},
              // {label: '成本', width: '', prop: 'const'},
              // {label: '利润', width: '', prop: 'profit'}
          ],
      getTableData:{
        'pageIndex':1,
        'pageSize':20,
        'dmdName':'',
        'orgNo':'',
        'orgDept':'',
        'createDate':''
      },
      dictMap: new Map(),
      tempId:'',
      isShowFilter:false,
      //列表展示数据
      tableShowData:[],
      //字段类型字典
      columnTypeMap: new Map,
      param:{},
      token:'',
      userId:'',
      tempUserNo:''
  	}
  },
  computed: {
      ...mapGetters([
          'uid',
      ]),
  },
  components: {
    querys,
    SelectTree
  },
  watch:{

		deptCode(){
			this.getTableData.orgNo = this.deptCode
			this.getTable(this.currentPage,this.currentNum)
		}
	},
  created(){
    
    this.tableHeight=window.innerHeight-220;
  },
  mounted(){
console.log('配置地址',this.$store.state.app.workflowurl)

    console.log('isCollapse',this.$store.state.app.isCollapse)
    this.token = sessionStorage.getItem('token');
    this.userId = sessionStorage.getItem('userId');

    this.getTable(1,20)
    // this.getTreeDeptCode()
  },
  methods: {
    delFile(row){
      delDmdTemplFile({"dmdId":row.dmdId}).then(res=>{
          if(res.data.successful){
              this.$message.success("删除附件成功")
              this.getTable(this.currentPage,this.currentNum)
          }else{
            this.$message.error("删除附件失败")
          }
      })
    },
    uploadFileSubmit(row) {
      this.$refs.uploadFile.submit()
    },
    parentSave(){
      console.log(this.parentData)

      parent.getChildValue(this.parentData)
    },
    getValue(value){
			this.deptCode = value
			console.log(value);
		},
		//获取单位树形结构
		getTreeDeptCode(){
			let _this = this
			getUserDept().then(response=>{
				let valueId = response.data.resultValue
				getTreeDeptCode().then(response=>{
					console.log(response)
					if(response.data.successful){
						_this.valueId = valueId
						_this.deptArr = response.data.resultValue
					}
				})
			})
		},
    changeQuery(state,height){
      if(state){
        this.tableHeight = height
        this.isShowQuery = true
      }else{
        this.tableHeight = height
        this.isShowQuery = false

      }
    },
    dateFormat(row, column) {
        var moment = require('moment');
        var date = row[column.property];
        console.log('date',row)
        // return moment(date).format("YYYY-MM-DD")
        if(date){
          return moment(date).format("YYYY-MM-DD")
        }else{
          return ''
        }
    },
    cancel(formName){
      this.dialogFormVisible = false
      this.resetForm(formName)
    },
    resetForm(formName) {
        this.$refs[formName].resetFields();
      },
    getRowKeys(row){
      return row.dmdId;//返回唯一ID
    },
    
    isRequired(data){
      console.log(data)
      if(data && data==1){
        
        return true
      }else{
        return false
      }
    },
    switchFilter(){
      this.isShowFilter = !this.isShowFilter
    },
    isRequired(data){
      console.log(data)
      if(data && data==1){
        
        return true
      }else{
        return false
      }
    },
    //没用到
    changeProbName(val){
      
      if(!val){
        this.getTable()
      }
    },
    //这个方法重复了和下面
    uploadSuccess(response, file, fileList){
      let _this =  this
      if(response.successful){
        _this.$message({
          showClose: true,
          message: '导入成功',
          type: 'success'
        });
        _this.getTable(_this.currentPage,_this.currentNum)
      }else{
        _this.$message({
          showClose: true,
          dangerouslyUseHTMLString: true,
          message: response.resultHint,
          type: 'error'
        });
      }
      
    },
    handleSelectionChange(val){
      let _this = this
      _this.delData = {
        "ids": [],
      }
      _this.parentData=[]
      val.forEach(function(item,idx){
        _this.delData.ids.push(item.dmdId)
        _this.parentData.push({'dmdCode':item.dmdCode,'dmdId':item.dmdId})
      })
    },
    exportTmp(){
      let _this = this
      // if(_this.delData.ids.length){
        let exportData = _this.delData
        exportData.ids = exportData.ids.toString()
        window.location.href=_this.GLOBALURL+'/prjStDmd/export?ids='+exportData.ids+'&token='+_this.token
        
      // }else{
      //   _this.$message({
      //     showClose: true,
      //     message: '未选择任何数据',
      //   });
      // }
    },
    del(){
      let _this = this
      if(_this.delData.ids.length){
          this.$confirm('此操作将永久删除'+_this.delData.ids.length+'条数据, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
              _this.loading =true
              _this.delLoading = true
              deletePrjStDmd(_this.delData).then(response => {
                  console.log(response)
                  _this.delLoading = false
                  _this.$refs.demandTable.clearSelection();
                if(response.data.successful){
                  
                    this.$message({
                      showClose: true,
                      message: '删除成功',
                      type: 'success'
                    });
                    _this.getTable(_this.currentPage,_this.currentNum)

                }else{
                  this.$message({
                      showClose: true,
                      message: response.data.resultHint,
                      dangerouslyUseHTMLString: true,
                      type: 'error'
                    });
                }
                _this.loading =false

            })

          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除',
              type: 'warning'
            });          
          });
          
        }else{
          this.$message({
                  showClose: true,
                  message: '未选择任何数据',
                  type: 'warning'
                });
        }
      
    },
    submitForm(formName) {

      //校验问题创建人是否时本人
      let _this = this
      if(_this.tempUserNo!= "" &&_this.userId!=_this.tempUserNo){
          this.$message.warning('你不是创建者，没有权利修改此项')
          return false
      }
      console.log(_this.tempId)
      _this.$refs[formName].validate((valid) => {
        if (valid) {

          let subdata = {"items": []}
          

          console.log(_this.lableList)

          let arr={}

          if(_this.tempId.length){
            arr['dmdId']= _this.tempId
          }
          
          Object.keys(_this.lableList).forEach(function (key) {
            // console.log(key,_this.lableList[key])

            arr[_this.lableList[key].colDname] = _this.lableList[key].value
          })
          subdata.items.push(arr)
          console.log('subdata',subdata)
          _this.dialogFormVisible = false

          _this.loading =true
          _this.submitLoading = true
          savePrjStDmd(subdata).then(response => {
            console.log(response)
            _this.submitLoading = false
            if(response.data.successful){
              this.$message({
                showClose: true,
                message: '操作成功',
                type: 'success'
              });
              _this.getTable(_this.currentPage,_this.currentNum)
            }else{
              this.$message({
                showClose: true,
                message: '操作失败',
                type: 'error'
              });
            }
            _this.loading =false

          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    showDialog(data){
      let _this =this

      _this.dialogFormVisible = true
      _this.dialogLoading =true
      _this.showDownload = false
      if(data){
        _this.dialogTit='编辑需求'
        _this.tempId = data.dmdId
        _this.fileName = data.fileName

        if(data.tmplFileId){
          _this.showDownload = true
          _this.downloadId=data.dmdId
        }else{
          _this.showDownload = false
        }
        _this.tempUserNo = data.userNo

      }else{
        _this.dialogTit='新增需求'
        _this.tempId = ''
        _this.tempUserNo = ""
      }
      getSaveTable().then(response => {
        console.log(response)
        if(response.data.successful){
          response.data.resultValue.items.forEach(function (item, idx) { 
            item.value = ''
          })

          // _this.lableList = response.data.resultValue.items
          let common_table_info = {};//数组转对象，避免类型报错
          for(let i in response.data.resultValue.items){
            if(data){//修改问题 赋值
              // console.log(data[response.data.resultValue.items[i].colDname])
              _this.tableData.forEach((item,idx) => {//查询原始数据

                if(item.dmdId == data.dmdId){
                  console.log(item)
                  response.data.resultValue.items[i].value = item[response.data.resultValue.items[i].colDname]
                }
              })
              // response.data.resultValue.items[i].value = data[response.data.resultValue.items[i].colDname]
              console.log(data[response.data.resultValue.items[i].colDname])
            }
            common_table_info[i] = response.data.resultValue.items[i]
            
          }

          _this.lableList = common_table_info;

          
        }
        _this.dialogLoading =false

      })
    },
    //查询
		selectItem() {
			this.currentPage = 1;
			this.getTable(1, this.currentNum);
		},
    //获取当前页每页条数
    handleSizeChange(val) {
       this.currentNum = val
      this.getTable(this.currentPage,val)
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage = val
      this.getTable(val,this.currentNum)
    },
    getTable(page,size){
      let _this = this
      _this.loading = true
      _this.getTableData.pageIndex = page
      _this.getTableData.pageSize = size
      getPrjStDmd(_this.getTableData).then(response => {//{}写需要的参数
          console.log('response',response)//后端返回数据
          if(response.data.successful){
              _this.totalNum = response.data.resultValue.totalNum
              // _this.tableLabel = response.data.resultValue.tableLable
              // _this.tableData = response.data.resultValue.tableData
              //从表头中抽取出字典信息列表
                // _this.getDictArr(_this.tableLabel)
              _this.tableLabel = response.data.resultValue.tableLable
                let arr = response.data.resultValue.tableLable
              _this.tableLabel = _this.getLabelList(arr)
              _this.dictMap = _this.getDictArr(arr)
              console.log(_this.dictMap)
              _this.columnTypeMap = _this.getColumnTypeArr(arr)
              _this.tableData = response.data.resultValue.tableData
              _this.tableShowData = _this.getShowDateTable(_this.tableData,_this.dictMap,_this.columnTypeMap)
          }
          _this.loading = false
          
        }).catch(error => {
          console.log(error)//error
        })
        
    },
     //获取插入和修改的表头
    getLabelList(labelList){ 
      let _this =this
      labelList.forEach(function (item) {
        _this.$set(item,'value','');
      })
      let common_table_info = {};//数组转对象，避免类型报错
      for(let i in labelList){
        common_table_info[i] = labelList[i];
      }
      return common_table_info
    },
    //获取字典
    getDictArr(labelList){ 
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        if(temp.codeList!=null){
          map.set(temp.colDname,temp.codeList);
        }
      }
      return map
    },
     //获取字段类型字典
    getColumnTypeArr(labelList){
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        map.set(temp.colDname,temp.colType);
      }
      return map
    },
    //获取展示数据
    getShowDateTable(tableData,dictMap,columnTypeMap){
      let _this = this
      let arr = [];
      for(let i=0;i<tableData.length;i++){
        let tempObj = tableData[i];
        //拷贝对象
        let copyObj = new Object();
        //遍历获取的数据中的属性，如果属性名有和数据字典中的key相对应的，取出数据字典对应的值赋值过去
        for (let [key, value] of Object.entries(tempObj)) {
          // console.log(key + ':' + value);
          if(key=="feaStatus"){
               copyObj["feaStatusT"] = value;
          }
          if(dictMap.has(key)){
            let dictValueList = dictMap.get(key);
            for(let j=0;j<dictValueList.length;j++){
              let tempObj2 = dictValueList[j];
              if(tempObj2.codeValue==value){
                copyObj[key] = tempObj2.codeName;
              }
            }
            // if(copyObj[key]==''){
            //     copyObj[key] = value
            // }
          }else if(columnTypeMap.get(key)=="DATE"){
            console.log('columnTypeMap',key)
               copyObj[key+"Rel"] = value
               let timeStr = _this.formartDate(value,key)//区分创建日期/年份
               copyObj[key] = timeStr;
          }else{
            copyObj[key] = value;
          }
          if(key=="orgNo"){
            copyObj["orgNo"] = value
          }
          
        }
        arr.push(copyObj);
      }
      return arr;
    },
    //时间戳字符串的转化
    formartDate(param,key) {
      if(param){
        var time = new Date(param);
        var y = time.getFullYear();
        var m = time.getMonth()+1;
        var d = time.getDate();
        // var h = time.getHours();
        // var mm = time.getMinutes();
        // var s = time.getSeconds();
        // return y+'-'+this.add0(m)+'-'+this.add0(d)+' '+this.add0(h)+':'+this.add0(mm)+':'+this.add0(s);
        // return y+'-'+this.add0(m)+'-'+this.add0(d);
        if(key == 'year'){
          return y
        }else{
          return y+'-'+this.add0(m)+'-'+this.add0(d);
        }
        
      }else{
        return ''
      }
      
    },
    add0(m){
      return m<10?'0'+m:m;
    },
    beforeupload(){
      this.loading =  true
    },
    errorUpload(){

      this.loading = false
      this.$message({
            message: '上传失败',
            type:'error'
          });  
    },
    uploadSuccessAttachment(response, file, fileList){
      let _this = this
      console.log('uploadSuccessAttachment')
      _this.uploading =  false
      _this.fileList = fileList
      console.log(_this.fileList)
      if(response.successful){
          // _this.closePfDialog()
          _this.$message({
                showClose: true,
                message: "上传成功",
                type: 'success'
        });
        _this.getTable(_this.currentPage,_this.currentNum)
      }else{
        _this.$message({
                showClose: true,
                dangerouslyUseHTMLString: true,
                message: response.resultHint,
                type:'error'
        });
      }
    },
    exceed(){
      this.$message({
          showClose: true,
          message: '只能上传一个文件',
          type: 'warning'
        });
    },
  }
}
</script>
