    valiNum(rule, value, callback){//包含小数的数字
            let reg = /^[+-]?(0|([1-9]\d*))(\.\d+)?$/g;
        if (value === '') {
            callback(new Error('请输入内容'));
        } else if (!reg.test(value)) {
            callback(new Error('请输入数字'));
        } else {
            callback();
        }
    },
    validateNum (rule, value, callback){//正整数
            console.log('rule',rule,'value', value, 'callback',callback)
            let reg = /^[+]{0,1}(\d+)$/g;
            if (value === '') {
                callback(new Error('不能为空'));
            } else if (!reg.test(value)) {
                callback(new Error('请输入0及0以上的整数'));
            } else {
                callback();
            }
    },
    getSpecValue(spec,row){
      let map = this.dictMap.get('speciality')
      let specValue = ''
      map.forEach((item,value) =>{
        // console.log(item)
        if(item.codeName == spec){
          specValue = item.codeValue
        }
      })
      if(specValue == '01'){//电网基建需要验证电压
        if(row.voltLevel){
            if(row.voltLevel == '35kV' || row.voltLevel == '20kV' ){
              specValue = 21
            }else if(row.voltLevel == '110kV'){
              specValue = 22

            }else if(row.voltLevel == '10kV' || row.voltLevel == '6kV' || row.voltLevel == '6V' || row.voltLevel == '110V'|| row.voltLevel == '12V'|| row.voltLevel == '24V'|| row.voltLevel == '36V'|| row.voltLevel == '48V'|| row.voltLevel == '600V'|| row.voltLevel == '66kV'|| row.voltLevel == '660V'|| row.voltLevel == '750V'|| row.voltLevel == '1000V（含1140V）'|| row.voltLevel == '1500V'|| row.voltLevel == '220V'|| row.voltLevel == '2500V'|| row.voltLevel == '3000V'|| row.voltLevel == '380V(含400V)'){
              specValue = 20

            }else if(row.voltLevel == '220kV' || row.voltLevel == '330kV'){
              specValue = 23

            }else if(row.voltLevel == '500kV' || row.voltLevel == '750kV'|| row.voltLevel == '1000kV'){
              specValue = 24

            }
          }
        }
      return specValue
    },
