    submitOutbound(formName){
      let _this =this
      let outform =[]
      for(let i in _this.outboundForm){
        if(_this.outboundForm[i].money != ''){
          outform.push({'spec':_this.outboundForm[i].codeValue,'money':_this.outboundForm[i].money})

        }
      }
      
      getCKlist(outform).then(response => {
          if(response.data.successful){
            if(response.data.resultValue.list.length){
              _this.$refs['outboundForm'].resetFields();
              let speciality = _this.dictMap.get('speciality')
                
                for(let s in response.data.resultValue.list){
                    for(let i in speciality){
                      if(response.data.resultValue.list[s].speciality == speciality[i].codeValue+'' ){
                        response.data.resultValue.list[s].speciality = speciality[i].codeName+''
                      }
                    }
                }
                _this.ckData = response.data.resultValue.list
            }

            let errors = response.data.resultValue.errors
            let errorStr = ''
            errors.forEach(error => {
              errorStr += error.error + '<br>'
