    async batchBMAll(){
      let _this = this;
      _this.batchAllLoad = true
      let errorMag = '';
      for(let i=0;i<_this.parentItemInfo.length;i++){
            let tempData = _this.parentItemInfo[i];
            // console.log(tempData)
            try {
              await _this.showBm2(tempData);
            }catch(err){
              console.log(err);
              errorMag+=tempData.itemName +"编码失败<br/>"
            }
            
        }
      
        _this.batchAllLoad = false
        if(errorMag!=''){
          // _this.$message.error(errorMag)
          _this.$alert(errorMag, "提示", {
            confirmButtonText: '关闭',
            dangerouslyUseHTMLString: true,
            customClass:"submit-error",
          })
        }else{
          _this.$message.success("批量编码成功")
        }
        
        _this.getTable()
        _this.$refs.storagetable.clearSelection();
    },
    batchBM(){ //批量编码
         let _this = this;
        //检查所选项目
        if(_this.parentItemInfo.length>0){
          for(let i=0;i<_this.parentItemInfo.length;i++){
              let tempData = _this.parentItemInfo[i];
              if(tempData.code!=null&&tempData.code!=''&&tempData.code!=undefined){
                  _this.$message.warning("所选项目中已包含编码")
                  return false;
              }
          }

        }else{
           _this.$message.warning("请选择需要编码的项目")
        }
        _this.batchBMAll()
        
    },
    showBm2(data){
      let _this = this
      data.btnLoading = true
      let itemId = data.itemId
      // _this.loading = true
      //获取调用国电通编码器所使用的参数 add by gao.feng
      return new Promise((resolve, reject) => {
        doPostApplyItemCode({"itemId":itemId}).then(response=>{
          if(response.data.successful){
             let param = response.data.resultValue;
            //  resolve()
             
            //  console.log(2222)

            //  console.log(param)
             getBM(param).then(res=>{
                data.btnLoading = false
                console.log(res)
                let status = res.data.status
                if(status.indexOf("200")!=-1){//调用失败
                    // _this.$message.error("调用编码器失败,权限校验失败")
                    //_this.batchAllLoad = false
                    reject()
                    
                    console.log("调用编码器失败,权限校验失败")
                }else if(status.indexOf("100")!=-1){
                    let applyResultList = res.data.applyResult
                    let result = applyResultList[0]
                    if(result.status.indexOf("100")!=-1){//调用成功
                         let baseCode = result.baseCode
                         //更新项目编码
                         updateItemCode({"itemId":itemId,"code":baseCode}).then(res1=>{
                              if(res1.data.successful){
                                resolve()
                                  // _this.$message.success("编码成功")
                                  // _this.getTable()
                              }
                              
                         })

                    }else if(result.status.indexOf("200")!=-1){
                         let errormsg = ''
                         for(let i in result.applyResult){
                            errormsg = result.applyResult[i]
                         }
                        //  _this.$message.error(errormsg)
                        reject()
                        //_this.batchAllLoad = false
                         console.log(errormsg)
                    }
                }
             })
          }
        }).catch(error => {
          reject(error)
          
        })
      })
      
    },
