      if(this.isChildSelect){
        this.$message({
              showClose: true,
              message: '不能选择包含上级的项目',
              type: 'warning'
            });
      }else{
        if(this.delData.ids.length){
          this.pfDialog = true
        }else{
          this.$message({
              showClose: true,
              message: '请选择要批复的数据',
              type: 'warning'
            });
        }
      }
      
    },
    changeProbName(val){
      
      if(!val){
        this.getTable()
      }
    },
    handleNodeClick(data) {
        this.showForm = data.id
        console.log(this.showForm)
      },
    submitTable(){
      let _this = this
      // if(_this.submitData!=''){
        //{'itemIds':_this.submitData,'userId':_this.token}
        submitPrjStItemCheck().then(response => {
              console.log('response',response)
            if(response.data.successful){
              
                this.$message({
                  showClose: true,
                  message: "提交成功",
                  type: 'success'
                });
               
                _this.getTable()
                _this.$refs.storagetable.clearSelection();

            }else{
               this.$alert(response.data.resultHint, "提示", {
                  confirmButtonText: '关闭',
                  dangerouslyUseHTMLString: true,
                  customClass:"submit-error",

                })
            }
            _this.loading =false

        })
      // }else{
      //   this.$message({
      //     showClose: true,
      //     message: '请选择要提交的数据',
      //     type: 'error'
      //   });
      // }
    },
    getRowKeys(row){
      return row.itemId;//返回唯一ID
    },
    handleSelectionChange(val){
      console.log('handleSelectionChange',val)
      let _this = this
      
      _this.parentItemInfo = []
       _this.parentItemInfo = _this.parentItemInfo.concat(val);
      if(val){
         // _this.submitData= val[0].itemId
         //  console.log(_this.submitData)
      
        _this.scoreData=[]//评分itemids
        // _this.scoreRow=[]//电网基建
        _this.scoreSpecValue =[]//评分specvalue 最终如果都一样才能显示
        _this.submitData = []
        _this.delData = {
          "ids": [],
        }
        _this.exportData = {
          "ids": [],
        }
        _this.isChildSelect = false
        val.forEach(function(item,idx){
          // console.log(item)
          if(item.isChild){
            _this.isChildSelect =true
          }
          _this.scoreData.push(item.itemId)
          let tempSpecValue = _this.getSpecValue(item.speciality,item)
          if(_this.scoreSpecValue.indexOf(tempSpecValue) == -1){//如果类型一样就不记录
            _this.scoreSpecValue.push(_this.getSpecValue(item.speciality,item))//记录每条SpecValue最后比较 如果长度不是1 就是不同类型
          }
          if(_this.submitData.indexOf(item.itemId) == -1){
            _this.submitData.push(item.itemId)
            _this.delData.ids.push(item.itemId)
            _this.exportData.ids.push(item.itemId)
          }
          
        })
        // console.log('exportData',_this.exportData.ids,'delData',_this.delData.ids)
      }
    },
    del(){
      let _this = this
      if(_this.delData.ids.length){
        for(let i=0;i<_this.delData.length;i++){
          let item = _this.delData[i]
          if(item.createor != _this.userId){
						_this.$message({
							showClose: true,
							message: '所选项目中存在不是创建者创建的项目，请检查后再删除',
							type: 'warning'
						});
						return false;
					}
        }
          this.$confirm('此操作将永久删除'+_this.delData.ids.length+'条数据, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
              _this.loading =true
              deletePrjStItem(_this.delData).then(response => {
                  console.log('response',response)
                if(response.data.successful){
                  
                    this.$message({
                      showClose: true,
                      message: '删除成功',
                      type: 'success'
                    });
                    _this.getTable()

                }else{
                  this.$message({
                      showClose: true,
                      message: response.data.resultHint,
                      dangerouslyUseHTMLString: true,
                      type: 'error'
                    });
                }
                _this.loading =false
                _this.cannclSelection()//取消选择

            })

          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });          
          });
          
        }else{
          this.$message({
                  showClose: true,
                  message: '未选择任何数据',
                  type:'warning'
                });
        }
      
    },
    
    showDialog(data){
      let _this =this
      _this.dialogFormVisible = true
      // _this.dialogLoading =true
      // _this.itemId = data.itemId
      _this.tempItemId = data.itemId
      getBrowse({'itemId':data.itemId}).then(response => {
        console.log(response)
        if(response.data.successful){
          _this.browseLabel = response.data.resultValue.tableLable
          _this.browseTableData = response.data.resultValue.tableData
          _this.browseDictMap =  _this.getDictArr(_this.browseLabel)

          // _this.browseShowData = response.data.resultValue.tableData

          _this.browseShowData = _this.getShowDateTable(_this.browseTableData,_this.browseDictMap)
          console.log('browseShowData',_this.browseShowData)
         
        }
        _this.dialogLoading =false

      })
    },
    //获取当前表格条数
		handleSizeChange(val) {
			console.log(`每页 ${val} 条`);
      this.currentNum = val;
      this.getTableData.pageSize = val
      this.getTableData.pageIndex = this.currentPage
			this.getTable();
		},
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.getTableData.pageIndex = val
      this.getTableData.pageSize = this.currentNum
      this.getTable()
    },
    getTable(){
      let _this = this
      _this.loading = true
      console.log('currentPage',this.currentPage)
      console.log(_this.getTableData)
       prjStItemCheck(_this.getTableData).then(response => {//{}写需要的参数
          console.log('response',response)//后端返回数据
          _this.isChildSelect =false//清空批复限制
          if(response.data.successful){
              
              if(response.data.resultValue.submit == '1'){
                _this.isSubmit = false
              }else{
                _this.isSubmit = true

              }
              if(response.data.resultValue.paixu == '1'){
                _this.ispaixu = false
              }else{
                _this.ispaixu = true

              }
              
              _this.totalNum = response.data.resultValue.totalNum
              _this.tableLabel = response.data.resultValue.tableLable
              _this.tableData = response.data.resultValue.tableData
              //从表头中抽取出字典信息列表
                // _this.getDictArr(_this.tableLabel)
              _this.dictMap =  _this.getDictArr(_this.tableLabel)

              let outdata = _this.dictMap.get("speciality")//项目出库数据
             for(let i in outdata){
                // outdata[i].money = ''
                _this.$set(outdata[i],'money','')
                _this.outboundForm[i] = outdata[i]
              }
              console.log('outboundForm',_this.outboundForm)

              _this.storageLabelList = response.data.resultValue.tableLable
              _this.storageDictMap = _this.getDictArr(_this.storageLabelList)
              _this.storageTableData = response.data.resultValue.tableData
              _this.storageShowData = _this.getShowDateTable(_this.storageTableData,_this.storageDictMap)
              //解决子项没有记录选中
              
                _this.storageShowData.forEach(item => {
                  if(item.children){
                    let children = item.children
                    children.forEach((child,idx) => {
                      if(_this.submitData.indexOf(child.itemId) != -1){
                        _this.$refs.storagetable.toggleRowSelection(item.children[idx],true);
                      }
                    })
                  }
                })
             
              
              _this.$nextTick(() => {
                _this.$refs.storagetable.doLayout();
              });

          }
          _this.loading = false
          
        }).catch(error => {
          console.log(error)//error
        })
        
    },
    //获取展示数据
    getShowDateTable(tableData,dictMap){
      console.log(dictMap)
      const _this = this
      let arr = [];
      for(let i=0;i<tableData.length;i++){
        let tempObj = tableData[i];
		//处理有子级的
        if (typeof (tempObj.children) != 'undefined' && tempObj.children != null && tempObj.children.length > 0){
          let temp = []
          temp = tempObj.children
          tempObj.children = _this.getShowDateTable(temp,_this.storageDictMap)
        }
		//初始化编码按钮loading
        tempObj.btnLoading = false        //拷贝对象
        let copyObj = new Object();
        //遍历获取的数据中的属性，如果属性名有和数据字典中的key相对应的，取出数据字典对应的值赋值过去
        for (let [key, value] of Object.entries(tempObj)) {
          // console.log(key + ':' + value);
         
          if(dictMap.has(key)){
            let dictValueList = dictMap.get(key);
            for(let j=0;j<dictValueList.length;j++){
              let tempObj2 = dictValueList[j];
              if(tempObj2.codeValue==value){
                copyObj[key] = tempObj2.codeName;
              }
            }
          }else{
            copyObj[key] = value;
          }
        }
        arr.push(copyObj);
      }
      // console.log('arrarr',arr)
      return arr;
    },
    //获取数据字典
    getDictArr(labelList){ 
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        if(temp.codeList!=null){
          //add by gao.feng  过滤产业基建，产业大修，产业技改
          if(temp.colDname=="speciality"){
              let arrT = []
              for(let j=0;j<temp.codeList.length;j++){
                  let codeT = temp.codeList[j];
                  if(codeT.codeValue=='05'||codeT.codeValue=='07'||codeT.codeValue=='14'){
                      continue;
                  }else{
                    arrT.push(codeT)
                  }
              }
              temp.codeList = arrT
          }
          map.set(temp.colDname,temp.codeList);
        }
      }
      return map
    },
    pfFile(){
      if(!this.apprFileNo){
        this.$message({
          showClose: true,
          message: '请输入项目可研批复文号',
          type: 'warning'
        });
        return false
      }
      let pf = {
        "ids":this.delData.ids,
        "primarykey" :[this.fileId,this.apprFileNo]
      }
      
      approve(pf).then(response => {
        console.log(response)
        if(response.data.successful){
          this.$message({
                  showClose: true,
                  message: "批复成功",
                  type: 'success'
          });
          this.getTable()
          this.closePfDialog()
        }
      })
    },
    uploadSuccess(response, file, fileList){
      let _this = this
      console.log(response)
      _this.uploading =  false
      _this.fileList = fileList
      console.log(_this.fileList)
      if(response.successful){
          // _this.closePfDialog()
          _this.fileId = response.resultValue
          _this.$message({
                showClose: true,
                message: "上传成功",
                type: 'success'
        });
        
      }else{
        _this.$message({
                showClose: true,
                message: response.resultHint,
                dangerouslyUseHTMLString: true,
                type:'error'
        });
      }
    },
    beforeupload(){
      this.uploading =  true
    },
    errorUpload(){

      this.uploading = false
      this.$message({
            type: 'info',
            message: response.resultHint,
            type:'error',
            dangerouslyUseHTMLString: true,
          });  
    },
  }
}
</script>
