			newItemName: false,
			//项目专项
			specClass:"",
			//专项名称
			specClassName:"",
			// 子组件的表格数据
			tableData: [],
			// 子组件的表头数据
			tableLabel: {},
			dictMap: new Map,
			tempId:'',
			columnMap: new Map,
			title:"",
			totalLableArr:[],
			dialogLoading:false,
			formLabelWidth: '140px',
			//选择问题或需求列表
			innerVisible: false,
			//内层弹框标题
			questionTitle: '',
			//问题或需求列表表头
			questionLabelList: [],
			//问题或需求列表数据
			questionTableData: [],
			//问题或需求列表返回总页数
			questionListTotalNum: 0,
			//问题或需求字典
			questionDictMap: new Map(),
			//问题或需求展示数据
