		changeQuantity(val){
			let _this = this
			if(val>1){
				_this.setValue("lxItemType","1")
			}
		},
		//清空指定字段的值
		clearValue(key){
			let _this = this
			for (let i in _this.tableLabel) {
				let item = _this.tableLabel[i]
