		//保存&修改
		saveItem(formName){
			let _this = this;
			_this.$refs[formName].validate(valid => {
				if (valid) {
					let arr = {};
					let successMsg = '';
					let failMsg = '';
					if (_this.tempItemId !=null) {
						arr['itemId'] = _this.itemId;
						successMsg = '修改成功';
						failMsg = '修改失败';
					} else if (_this.parentItemId != null) {
						arr['parentId'] = _this.parentItemId;
						successMsg = '新增下级成功';
						failMsg = '新增下级失败';
					} else {
						successMsg = '添加成功';
						failMsg = '添加失败';
					}

					for (let i in _this.tableLabel) {
						 let item = _this.tableLabel[i]
						 arr[item.colDname] = item.value;
				    }
					// _this.tableLabel.forEach(function(item) {
					// 	arr[item.colDname] = item.value;
					// });
					//设置项目名称
					if(_this.tempItemId==null){
						if(arr['itemName']==''){
							arr['itemName'] = document.getElementById("ifremVal").value;
						}
					}
					
					//设置专业类别
					arr['speciality'] = _this.specClass;
					if(_this.tempItemId!=null){
						arr['itemId'] = _this.tempItemId
					}
					_this.dialogLoading = true
					if (_this.parentItemId == null) {
						saveBySpeciality(arr).then(response => {
							_this.dialogLoading = false
							console.log(response);
							if (response.data.successful) {
								this.$message({
									showClose: true,
									message: successMsg,
									type: 'success'
								});
								_this.itemId = ''
								_this.$router.push({path:_this.oldPath});  

							} else {
								this.$message({
									showClose: true,
									message: failMsg,
									type: 'error'
								});
							}
							_this.loading = false;
						});
					} else {
						//新增下级
						saveLower(arr).then(response => {
							_this.dialogLoading = false
							console.log(response);
							if (response.data.successful) {
								this.$message({
									showClose: true,
									message: successMsg,
									type: 'success'
								});
								_this.$router.push({path:_this.oldPath});  
							} else {
								this.$message({
									showClose: true,
									message: failMsg,
									type: 'error'
								});
							}
							_this.loading = false;
						});
					}
			} else {
					console.log('error submit!!');
					return false;
		    }
			
			})
		},
