								 	_this.tableLabel[i].value  = _this.formartDate(tableInfo[_this.tableLabel[i].colDname])
							 }else{
									_this.tableLabel[i].value = tableInfo[_this.tableLabel[i].colDname] + '';
							}
						}
						//获取一级分类下拉框
						_this.changeItemClass1()
						//获取固定资产下拉框
						_this.getCascadeLabel('1001')

						//子项目列表数据
						let childrenData = response.data.resultValue.tableDataList
						_this.childrenLable = arr
						if(typeof(childrenData) == "undefined"||childrenData.length==0){
							_this.showChildren = false
						}else{
							_this.showChildren = true
							//从表头中抽取出字典信息列表
							_this.childrenDictMap = _this.getDictArr(_this.childrenLable);
							//获取插入或修改时的表头
							// _this.getLabelList(_this.tableLabel);
							_this.childrenColumnMap = _this.getColumnTypeArr(_this.childrenLable)
					
							_this.childrenShowData = _this.getShowDateTable(childrenData, _this.childrenDictMap,_this.childrenColumnMap);
						}

						let conDept = _this.getValue("conDept")
						if(conDept!=''){
							_this.deptCodeT = conDept
							getDeptIdByDeptCode({'deptCode':conDept}).then(response=>{
								if(response.data.successful){
									let deptId = response.data.resultValue
									_this.deptName = deptId
								}else{
									this.$message.error("查询部门id失败")
								}
							})
						}
						_this.getTreeDeptCode()
						
					}
				})

			}else{ //新增
				// let formData = new FormData
				// formData.append("speciality",_this.specClass)
				let param = {}
				param.speciality = _this.specClass
				queryTitleBySpeciality(param).then(response=>{
					_this.dialogLoading = false
					if(response.data.successful){
						let arr = response.data.resultValue.tableLable
						//获取表头
						_this.tableLabel = _this.getLabelList(arr)
						//获取字典
						_this.dictMap = _this.getDictArr(arr)

						//获取一级分类下拉框
						_this.changeItemClass1()
						//获取固定资产下拉框
						_this.getCascadeLabel('1001')
						//给数据类型赋默认值
						for (let i in _this.tableLabel) {
							if(_this.tableLabel[i].dispType=='number'){
								_this.tableLabel[i].value = 0
							}
						}

						//零星购置，建管单位字段默认为用户所在单位						
						getUserDept().then(res=>{
							if(res.data.successful){
								let deptCode = res.data.resultValue
								_this.setValue("conDept",deptCode)
								getDeptIdByDeptCode({'deptCode':deptCode}).then(response=>{
								if(response.data.successful){
									let deptId = response.data.resultValue
