<template>
  <div class="querys_box">
      <div v-if="showSwitch" @click="switchQuerys" :class="['switch_btn',{[open]: isShowQuery}]"><span class="iconfont" >&#xe601;</span></div>
      <div :class="['querys',{[openQuery]: isShowQuery}]" >
        
        <slot name="queryTabs">
          
        </slot>
        
      </div>
      <div class="query_btns">
        <slot name="queryBtns">
          
        </slot>
      </div>
  </div>
</template>

<script>



export default {
  name: 'querys',
  props: ['showSwitch','tabHeight'],
  data() {
  	return {
      openQuery:'open_query',
      open:'open',
      isShowQuery:false,
  	}
  },
  
  mounted(){
   
  },
  methods: {
    switchQuerys(){
      this.isShowQuery = !this.isShowQuery
      let height = ''
      if(this.tabHeight){
        // height = this.viewportToPixels(this.tabHeight)
        height = this.tabHeight
        console.log(height)
        // window.innerHeight-220
      }else{
        // height = this.viewportToPixels('70vh')
        height = window.innerHeight-220
      }
       
      if(this.isShowQuery){
        height = height-42
      console.log(height)
      }else{
          // height = window.innerHeight-220

        if(this.tabHeight){
          console.log(this.tabHeight)

          height = this.tabHeight
        }else{
          height = window.innerHeight-220
        }
      }
      this.$emit('changeQuery',this.isShowQuery,height)

    },
    viewportToPixels(value) {
      var parts = value.match(/([0-9\.]+)(vh|vw)/)
      var q = Number(parts[1])
      var side = window[['innerHeight', 'innerWidth'][['vh', 'vw'].indexOf(parts[2])]]
      return side * (q/100)
    }

  }
}
</script>
