<template>
  <div class="container">
    
    <div class="main clearfix" :style="{'margin-left':$store.state.app.isCollapse ? '84px':'240px'}">
      
      <div class="main_container plan-review">
        <!-- <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item v-for="mat in $route.matched" ><span class="color_tip">{{mat.name}}</span></el-breadcrumb-item>
        </el-breadcrumb> -->
        <div class="btns query_btns">
            <el-button type="primary" size="small" @click="showReviewDia">评审计划审查意见</el-button>
        </div>

        <div class="template_main">
          <div class="template_left">
            <el-table 
              class="plan-table"
              :data="colTimeList"
              :height="tableHeight"
              border
              :header-cell-style="{background:'#359691',color:'#fff'}"
              row-class-name="row"
              cell-class-name="column"
              :highlight-current-row="true"
              @current-change="handleChangeColTimeList"
              v-loading="timeListloading"
              ref="tableLeft"
              >
              <el-table-column
                label="评审计划汇总列表"
                prop="jhps"
                >
              </el-table-column>
            </el-table>
          </div>
          <div class="template_right">
            <div class="table-item-box">
					    <div :class="showDrawer?'table-wrapper-run':'table-wrapper-init'">
                <el-table 
                  :data="tableDataShow"
                  :height="tableHeight"
                  border
                  :header-cell-style="{background:'#359691',color:'#fff'}"
                  @selection-change="handleSelectionChange"
                  row-class-name="row"
                  cell-class-name="column"
                  :highlight-current-row="true"
                  v-loading="tableDataLoading"
                  lazy
                  :load="tableLoad"
                  row-key="itemId"
                  :tree-props="{children: 'children', hasChildren: 'hasChildren'}"
                  >
                  <el-table-column
                          type="index"
                          width="50"
                          label="序号" >
                  </el-table-column>
                  <el-table-column
                          v-for="(item, index) in tableLabel"
                          v-if ="item.colDname != 'itemName'&&item.showType==1"
                          :key="index"
                          :prop="item.colDname"
                          :label="item.dispName" min-width="220"
                          :show-overflow-tooltip="true">
                  </el-table-column>
                  <el-table-column v-else-if="item.colDname == 'itemName'"
                                  :label="item.dispName" width="200" 
                                  :show-overflow-tooltip="true">
                    <template slot-scope="scope" >
                      <span class="blue" @click="showDetailDialog(scope.row)">{{scope.row.itemName}}</span>
                    </template>
                  </el-table-column>
                </el-table>
                <div class="page">
                  <el-pagination
                    background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-size="20"
                    :page-sizes="[10,20,30,50]"
                    layout="sizes, total, prev, pager, next, jumper"
                    :total="totalNum">
                  </el-pagination>
                </div>
              </div>
              <div class="planDrawer" :class="showDrawer?'item-add-wrapper-run':'item-wrapper-init'">
                <!-- 新增 修改 浏览 引itemaddcom -->
                <item-add-com 
                v-if="showDrawer" 
                :key="timer" 
                :showData="showData" 
                :showAddDialog="showDrawer" 
                :isShowQuery="showQuery"
                :tableHeightTab="tableHeight"
                @changeStatus="changeStatus">
                </item-add-com>
              </div>
            </div>
                
          </div>
          
        </div>
        
        
      </div>
    </div>
    <!-- <right></right> -->
    <el-dialog class="review-dialog" 
      :visible.sync="dialogReviewVisible" 
      title="可研计划审查意见" 
      :close-on-click-modal = "false" 
      custom-class="cus-dialog"
      :modal-append-to-body='false'>
      <div class="review">
          <el-form ref="form" :model="advice" label-width="0" size = "small" class="demo-form-inline" >
            <el-form-item >
              <el-radio-group v-model="advice.agreeType">
                <el-radio label="1" >同意</el-radio>
                <el-radio label="2" >不同意</el-radio>
              </el-radio-group>
               <el-upload
                class="upload"
                :action="GLOBALURL+'/prjStFeaAudit/uploadFile'"
                :headers="{'userId':userId,'token':token}"
                :on-success="uploadAdviceSuccess"
                :multiple='false'
                :show-file-list="true"
                :limit="1"
                :file-list="fileList"
                 accept=".doc,.docx">
                <el-button size="small" type="primary">上传评审计划审查意见</el-button>
              </el-upload>
            </el-form-item>
            <el-form-item >
              <el-input type="textarea" v-model="advice.checkAdvice"></el-input>
            </el-form-item>
            <el-form-item align="right">
              <el-button type="primary" @click="submitForm">保存</el-button>
              <el-button @click="dialogReviewVisible = false">取消</el-button>
            </el-form-item>
          </el-form>
        </div>
    </el-dialog>

  
    <!--项目详细信息-->
    <el-dialog :visible.sync="itemShow" :close-on-click-modal = "false" :modal-append-to-body='false'>
        <item-dialog :itemId="tempItemId"></item-dialog>
    </el-dialog>
  </div>
</template>

<script>
// @ is an alias to /src
import itemDialog from '@/views/project/item.vue'
import ItemAddCom from "@/views/project/itemAddCom"
import {getPlan,getColTimeList,submitCheckAdvice,getChildrenItem} from '@/api/data'
import { mapGetters } from 'vuex'

export default {
  name: 'planSummary',
  data() {
  	return {
      isShowQuery:false,
      showQuery:false,
      tableHeightTab:"",
      timer:"",
			showAddDialog:false,   //弹窗是否展示-新增 修改 浏览
			showData:{
				itemId:null, //项目id
				specClass: "",//专项类型
				specClassName:"",//专项类型名称
				oldPath:"",//原来路由
				tableLabelArr:"",//表头
				phash:"1",//阶段1=立项 2=可研 3=储备 0=浏览
				parentItemId:null //父项id-新增下级时用
			},
			showDrawer:false,
      dialogReviewVisible:false,
      dialogLoading:false,
      loading:false,
      delData:{
        "ids": [],
      },
      lableList:[{value: ''}],
      totalNum:0,
      currentPage:1,
      currentNum:20,
      year: '',
      dialogFormVisible:false,
      dialogFormVisible2:false,
      form: {
        time1:'',
        time2:''
      },
      options: [],
      value:'',
      formLabelWidth: '120px',
      input: '',
      //汇总时间列表
      colTimeList:[],
  		// 子组件的表格数据
  		tableData: [],
  		// 子组件的表头数据
      tableLabel: [],
      //表格展示数据列表
      tableDataShow:[],
      //选中的计划id
      selectPlanId:{},
      //获取字段类型字典，用于日期类型的展示
      columnTypeMap: new Map,
      //查询条件
      param:{
          "itemName":"",
          "deptCode":"",
          "auditPlanStatus":"",
          "specialClass":"",
          "itemType":"",
          "voltLevel":"",
          "filter":""
      },
      //当前选中的项目id
      tempItemId:"",
      //是否展示项目信息
      itemShow:false,
      //当前选中日期
      currentColTime:"",
      //当前项目名称
      curItemName:"",
      //是否展示编辑弹框
      showUpdatePlaneTime:false,
      //编辑框中的数据
      updateForm:{
        "planStartDate":"",
        "planEndDate":""
      },
      //评审计划id
      auditPlanId:"",
      timeListloading: false,
      tableDataLoading: false,
      //评审意见
      advice:{
          "agreeType":"2",
          "checkAdvice":""
      },
      fileList:[],
      tableHeight:'',
      userId:"",
      token:''
  	}
  },
  components: {
     "item-dialog":itemDialog,
     ItemAddCom
  },
  created(){
		//表格自动高度
    this.tableHeight=window.innerHeight-220;
		
	},
  mounted(){
    this.getColTimeList()
    
    this.userId = sessionStorage.getItem('userId');
    this.token = sessionStorage.getItem('token');
  },
  computed: {
		...mapGetters([
			// 'token',
		])
  },
  methods: {
    // 子组件向父组件传递参数
		changeStatus(status) {
			this.showDrawer = status
			console.log('status'+status)
    },
    // 懒加载table树形子数据
    tableLoad(row, treeNode, resolve) {
      let params = {
        'parentItemId':row.itemId
      }
      getChildrenItem(params).then(res => {
        if(res.data.successful){
          let childData =  this.getShowDateTable(res.data.resultValue, this.dictMap, this.columnTypeMap);
					resolve(childData)
        }
      })
    },
		//展示项目详细信息
		showDetailDialog(row) {
			this.showData.itemId = row.itemId
			this.showData.phash = 0
			this.showDrawer = true;
			this.timer = new Date().getTime()
		},
    showReviewDia(){
      let _this = this
        //检查参数
        if(_this.currentColTime==""||_this.currentColTime.length==0){
          this.$message({
              showClose: true,
              message: '请选择提交批次',
              type: 'warning'
            });
        }
        // else if(_this.advice.checkAdvice.agreeType=='' || _this.advice.checkAdvice.agreeType==undefined){
        //   this.$message({
        //       showClose: true,
        //       message: '请选择评审意见',
        //       type: 'warning'
        //     });
        // }
        else{
          _this.dialogReviewVisible = true
        }
        
      
    },
    //上传意见成功
    uploadAdviceSuccess(response){
        let _this = this
        if(response.successful){
            _this.advice.auditFileId = response.resultValue.auditFileId 
            this.$message({
              showClose: true,
              message: '上传成功',
              type: 'success'
            });
        }else{
            let str = response.resultHint
            this.$message({
              showClose: true,
              message: '上传失败 '+str,
              dangerouslyUseHTMLString: true,
              type: 'error'
            });
        }     
    },
    //提交
    submitForm(){
        let _this = this
        //检查参数
        this.$confirm('是否确定提交意见?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
			  }).then(() => {
            let _fileId = _this.advice.auditFileId
            if(_fileId == "" || _fileId == undefined || _fileId == null){
              _this.advice.auditFileId = ""
            }
            let formData = new FormData
            formData.append("cTotalTime",_this.currentColTime)
            formData.append("agreeType",_this.advice.agreeType)
            formData.append("checkAdvice",_this.advice.checkAdvice)
            formData.append("auditFileId",_this.advice.auditFileId)
            submitCheckAdvice(formData).then(response=>{
                if(response.data.successful){
                    this.$message({
                      showClose: true,
                      message: '保存意见成功',
                      type: 'success'
                    });
                    _this.getColTimeList()
                    _this.advice = {"agreeType":"","checkAdvice":""}
                    _this.fileList = []
                    _this.dialogReviewVisible = false
                }else{
                  this.$message({
                      showClose: true,
                      message: '保存意见失败',
                      type: 'error'
                    });
                }
            })
				}).catch(() => {});

    },
    //时间戳字符串的转化
    formartDate(param) {
      var time = new Date(param);
      var y = time.getFullYear();
      var m = time.getMonth()+1;
      var d = time.getDate();
      // var h = time.getHours();
      // var mm = time.getMinutes();
      // var s = time.getSeconds();
      // return y+'-'+this.add0(m)+'-'+this.add0(d)+' '+this.add0(h)+':'+this.add0(mm)+':'+this.add0(s);
      return y+'-'+this.add0(m)+'-'+this.add0(d);
    },
    add0(m){
      return m<10?'0'+m:m;
    },
    //当点击不同的汇总日期时
    handleChangeColTimeList(val){
        let _this = this
        if(_this.currentColTime!=val.jhps){
            _this.currentColTime = val.jhps
            _this.getTable(_this.currentPage,_this.currentNum)
        }
    },
    //获取评审计划信息列表
    getColTimeList(){
      let _this = this
      _this.timeListloading = true
      getColTimeList("6").then(response=>{
          console.log(response)
          _this.timeListloading = false
          if(response.data.successful){
              let arr = response.data.resultValue
              _this.colTimeList = []
              arr.forEach(function(item){
                  let tempObj = {}
                  tempObj.jhps = item
                  _this.colTimeList.push(tempObj)
              })
              if(arr.length>0){
                _this.currentColTime = arr[0]
              }
              _this.getTable(1,_this.currentNum)
          }else{
            this.$message({
              showClose: true,
              message: '获取评审计划信息列表失败',
              type: 'error'
            });
          }
      })
    },
    //展示项目详细信息
    showItemInfo(data){
     let _this = this;
			_this.tempItemId = data.itemId;
			this.$router.push({path:'/itemInfo',query:{itemId:_this.tempItemId ,oldPath:_this.$route.path}});
    },
    //监听表单选中事件
    handleSelectionChange(val){
      let _this = this
      _this.selectPlanId = {
        "ids": [],
      }
      val.forEach(function(item){
        _this.selectPlanId.ids.push(item.auditPlanId)
      })
    },
    //获取当前每页条数
    handleSizeChange(val) {
      this.currentNum = val;
      this.getTable(this.currentPage,val)
    },
    //分页查询
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage = val;
      this.getTable(val,this.currentNum)
    },
    //获取数据列表
    getTable(page,size){
        let _this = this
        _this.tableDataLoading = true
        let filter = 'cTotalTimeStr=1&itemPhaseDet=6'
        if(_this.currentColTime!=""){
            filter +='&cTotalTime='+_this.currentColTime
        }
        //拼接查询条件
        for(let [key, value] of Object.entries(_this.param)) {
            if(key=="pageIndex"||key=="pageSize"||value==''){
                continue
            }
            console.log(key + ':' + value);
            filter+='&'+key+'='+value
        }
        let searchParam = {}
        searchParam.pageIndex = page
        searchParam.pageSize = size
        searchParam.filter = filter      
        
        getPlan(searchParam).then(response => {//{}写需要的参数
            _this.tableDataLoading = false
            console.log('response',response)//后端返回数据
            if(response.data.successful){
                _this.totalNum = response.data.resultValue.totalNum
                let labelArr = response.data.resultValue.tableLable
                _this.tableLabel = _this.getLabelList(labelArr)
                _this.dictMap = _this.getDictArr(labelArr)
                _this.columnTypeMap = _this.getColumnTypeArr(labelArr)
                _this.tableData = response.data.resultValue.tableData
                _this.tableDataShow = _this.getShowDateTable(_this.tableData,_this.dictMap,_this.columnTypeMap)
            }
            _this.loading = false
            
          }).catch(error => {
            console.log(error)//error
          })
        
    },
     //获取字典
    getDictArr(labelList){ 
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        if(temp.codeList!=null){
          map.set(temp.colDname,temp.codeList);
        }
      }
      return map
    },
    //获取字段类型字典
    getColumnTypeArr(labelList){
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        map.set(temp.colDname,temp.colType);
      }
      return map
    },
    //获取插入和修改的表头
    getLabelList(labelList){ 
      let _this =this
      labelList.forEach(function (item) {
        _this.$set(item,'value','');
      })
      let common_table_info = {};//数组转对象，避免类型报错
      for(let i in labelList){
        common_table_info[i] = labelList[i];
      }
      return common_table_info
    },
    //获取展示数据
    getShowDateTable(tableData,dictMap,columnTypeMap){
      let _this = this
      let arr = [];
      for(let i=0;i<tableData.length;i++){
        let tempObj = tableData[i];
        //拷贝对象
        let copyObj = new Object();
        //遍历获取的数据中的属性，如果属性名有和数据字典中的key相对应的，取出数据字典对应的值赋值过去
        for (let [key, value] of Object.entries(tempObj)) {
          console.log(key + ':' + value);
          if(dictMap.has(key)){
            let dictValueList = dictMap.get(key);
            for(let j=0;j<dictValueList.length;j++){
              let tempObj2 = dictValueList[j];
              if(tempObj2.codeValue==value){
                copyObj[key] = tempObj2.codeName;
              }
            }
          }else if(columnTypeMap.get(key)=="DATE"){
               copyObj[key+"Rel"] = value
               let timeStr = _this.formartDate(value)
               copyObj[key] = timeStr;
          }else{
              copyObj[key] = value;
          }
        }
        arr.push(copyObj);
      }
      return arr;
    },
    beforeUploadReport(file) {
			if (file.name) {
				let pex = file.name.split('.')[1];
				if (pex == 'doc' || pex == 'docx') {
					return true;
				} else {
					this.$message.warning('文件格式不正确,只能上传doc,docx文件!');
					return false;
				}
			} else {
				this.$message.warning('文件格式不正确,只能上传doc,docx文件!');
				return false;
			}
		},
  }
}
</script>
