  .review .el-upload-list{
    display: inline-block;
    vertical-align: -8px;
    
  }
  /* .plan-review .el-table .el-table__body-wrapper {
    height: 89% !important;
  } */

  .review-dialog .el-dialog .el-textarea__inner {
    width: 100% !important;
  }
  
</style>
