			let _this = this;
			if(response.successful){
				let fileParam = {}
				fileParam.fileId = response.resultValue.fileId
				fileParam.fileName = response.resultValue.fileName
				_this.fjIds.push(fileParam)
			}else{
				this.$message({
					showClose: true,
					message: '上传失败',
					dangerouslyUseHTMLString: true,
					type: 'error'
				});
			}
		},

     //删除可研
    deleteReport(){
      let _this = this
      let formData = new FormData();
      if(_this.fileList.length==0){
        this.$message({
              showClose: true,
              message: "可研报告已删除",
              type: 'warning'
        });
         return false
      }
      _this.$confirm('确认删除可研报告？','提示',{
        confirmButtonText:'确定',
        cancelButtonText:'取消',
        type:'warning'
      }).then(() => {
        formData.append("itemId", _this.uploadReportItemId);
        deleteReport(formData).then(res =>{
            if(res.data.successful){
                  _this.fileList = []
                  this.$message({
                    showClose: true,
                    message: "删除可研报告成功",
                    type: 'success'
                  });
