<template>
  <div class="container">
    
    <div class="main clearfix" :style="{'margin-left':$store.state.app.isCollapse ? '84px':'240px'}">
      
      <div class="main_container">
        <!-- <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item v-for="mat in $route.matched" ><span class="color_tip">{{mat.name}}</span></el-breadcrumb-item>
        </el-breadcrumb> -->

       <querys @changeQuery="changeQuery" :showSwitch = "false">
          <div slot="queryTabs">
            <div class="query_tab">
              <div class="query_label">项目名称</div>
              <div class="query_select">
                <el-input v-model="param.itemName"  clearable size="small" @change="selectItem" placeholder="请输入内容"
            ></el-input>
              </div>
            </div>

            <div class="query_tab">
              <div class="query_label">所属单位</div>
              <div class="query_select">
                  <select-tree @getValue="getValue"></select-tree>
              </div>
            </div>

            <div class="query_tab" v-if="param.isPassed!=0">
              <div class="query_label"><sapn >可研状态</sapn></div>
              <div class="query_select">
                <el-select v-model="param.feaStatus" 
                             v-if="selectParamDict != undefined"
                             placeholder="请选择" size="small" clearable
                             @change="selectItem">
                    <el-option
                      v-for="item in selectParamDict.get('itemPhaseDet')"
                      :key="item.codeValue"
                      :label="item.codeName"
                      :value="item.codeValue">
                    </el-option>
                  </el-select>
              </div>
            </div>

            <div class="query_tab">
              <div class="query_label">项目层级</div>
              <div class="query_select">
                <el-select v-model="param.itemLevel" @change="selectItem" v-if="selectParamDict != undefined"
                      placeholder="请选择" size="small" clearable>
                  <el-option v-for="item in selectParamDict.get('itemLevel')" :key="item.codeValue"
                        :label="item.codeName" :value="item.codeValue"></el-option>
                </el-select>
              </div>
            </div>

          </div>
          <div slot="queryBtns">
            <el-button type="primary" size="small" @click="showBatchCheck">批量审查</el-button>
          </div>
        </querys>
    
        <div class="table-item-box">
          <div :class="showDrawer?'table-wrapper-run':'table-wrapper-init'">
            <el-table 
              :data="tableShowData"
              border
              :header-cell-style="{background:'#359691',color:'#fff'}"
              row-class-name="row"
              cell-class-name="column"
              :highlight-current-row="true"
              :v-loading="loading"
              :height="tableHeight"
              @selection-change="handleSelectionChange"
              lazy
              :load="tableLoad"
              row-key="itemId"
              :tree-props="{children: 'children', hasChildren: 'hasChildren'}"
              >
              <el-table-column
                      type="selection"
                      width="45">
              </el-table-column>
              <el-table-column
                      type="index"
                      width="50"
                      label="序号">
              </el-table-column>
              <el-table-column
                      v-for="(item, index) in tableLabel"
                      v-if ="item.colDname != 'itemName'&&item.showType==1"
                      :key="index"
                      :prop="item.colDname"
                      :label="item.dispName"
                      :show-overflow-tooltip="true">
              </el-table-column>
              <el-table-column v-else-if="item.showType==3"
                                :prop="item.colDname"
                              :label="item.dispName"
                              :show-overflow-tooltip="true">
              </el-table-column>
              <el-table-column v-else-if="item.colDname == 'itemName'"
                              :label="item.dispName"
                              :show-overflow-tooltip="true">
                <template slot-scope="scope" >
                  <span class="blue" @click="showItemInfo(scope.row)">{{scope.row.itemName}}</span>
                </template>
              </el-table-column>
            </el-table>
            <div class="page">
              <el-pagination
                background
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[10,20,30,50]"
                :page-size="20"
                layout="sizes, total, prev, pager, next, jumper"
                :total="totalNum">
              </el-pagination>
            </div>
          </div>
          <div :class="showDrawer?'item-add-wrapper-run':'item-wrapper-init'">
						<!-- 新增 修改 浏览 引itemaddcom -->
						<item-add-com 
						v-if="showDrawer" 
						:key="timer" 
						:showData="showData" 
						:showAddDialog="showDrawer" 
            :isShowQuery="showQuery"
            :tableHeightTab="tableHeight"
						@changeStatus="changeStatus">
						</item-add-com>
					</div>
        </div>
      </div>
    </div>

    <!-- 批量审查弹窗 -->
    <el-dialog :visible.sync="batchCheckShow" title="批量审查" :close-on-click-modal = "false" 
    :modal-append-to-body='false'>
         <el-form ref="form" :model="advice" label-width="0" size = "small" class="demo-form-inline" >
            <el-form-item >
            <el-radio-group v-model="advice.agreeType">
              <el-radio label="1" >同意</el-radio>
              <el-radio label="2" >不同意</el-radio>
            </el-radio-group>
                <el-upload
                  class="upload"
                  :action="GLOBALURL+'/prjStFeaAudit/uploadFile'"
                  :headers="{'userId':userId,'token':token}"
                  :on-success="uploadAdviceSuccess"
                  :multiple='false'
                  :show-file-list="true"
                  :limit="1"
                  :file-list="fileList"
                    accept=".doc,.docx">
                <el-button size="small" type="primary">上传审查意见</el-button>
              </el-upload>
            </el-form-item>
            <el-form-item >
              <el-input type="textarea" v-model="advice.checkAdvice"></el-input>
            </el-form-item>
            <el-form-item align="right">
              <el-button type="primary" :loading="checkLoading" @click="batchCheck">保存</el-button>
              <el-button @click="batchCheckShow = false">取消</el-button>
            </el-form-item>
        </el-form> 
    </el-dialog>
    
    <el-dialog :visible.sync="itemShow" :close-on-click-modal = "false" :modal-append-to-body='false'>
          <item-dialog :itemId="tempItemId"></item-dialog>
    </el-dialog>
  </div>
</template>

<script>
// @ is an alias to /src
import { mapGetters } from 'vuex'
import itemDialog from '@/views/project/item.vue'
import SelectTree from "@/components/treeSelect.vue";
import {getItemLabelInfo,getFragmentary,updateFragmentaryAudit,getTreeDeptCode,getUserDept,queryFilterParam,getChildrenItem} from '@/api/data'
import querys from '@/components/querys'
import ItemAddCom from "@/views/project/itemAddCom"

export default {
  name: 'questions',
  data() {
  	return {
      checkLoading:false,
      isShowQuery:false,
      showQuery:false,
      tableHeightTab:"",
      timer:"",
			showAddDialog:false,   //弹窗是否展示-新增 修改 浏览
			showData:{
				itemId:null, //项目id
				specClass: "",//专项类型
				specClassName:"",//专项类型名称
				oldPath:"",//原来路由
				tableLabelArr:"",//表头
				phash:"1",//阶段1=立项 2=可研 3=储备 0=浏览
				parentItemId:null //父项id-新增下级时用
			},
			showDrawer:false,
      deptCode:"",
			isClearable:true,      // 可清空（可选）
			isAccordion:true,      // 可收起（可选）
			valueId:"",            // 初始ID（可选）
			props:{                // 配置项（必选）
				value: 'id',
				label: 'name',
				children: 'children',
				code: 'deptCode'
				// disabled:true
			}, 
      deptArr:[],
      //查询条件
      param:{
        "pageIndex": 1,
        "pageSize": 10,
        "isPassed":0,
        "deptCode":"",
        "itemType": "",
        "speciality": "",
        "feaStatus":""
      },
      tableHeight:'',
      dialogVisibleL:false,
      textarea:'',
      nolabel:'',
      radio:'',
      dialogLoading:false,
      loading:false,
      dialogTitle:'',
      tableTest:[],
      delData:{
        "ids": [],
      },
      lableList:[{value: ''}],
      totalNum:0,
      currentPage:1,
      currentNum:20,
      year: '',
      dialogFormVisible:false,
      form: {
        
      },
      resource:'',
      options: [],
      value:'',
      formLabelWidth: '120px',
      input: '',
   		// 子组件的表格数据
  		tableData: [],
  		// 子组件的表头数据
      tableLabel: [],
      //列表展示数据
      tableShowData:[],
      //数据字典
      dictMap: new Map,
       //查询条件
      selectParamDict: new Map(),
      //字段类型字典
      columnTypeMap: new Map,
      //当前项目id
      tempItemId:"",
      //当前可研评审id
      tempFeaAuditid:"",
      itemShow: false,
      dialogShowType:"",
      //提交现场评审意见所需保存的数据
      advice:{
         "agreeType":"",
         "checkAdvice":"",
         "fileId":""
      },
      isShowFilter:false,
      //项目字典
      itemMap:new Map,
      //展示批量审核弹框
      batchCheckShow: false,
      //批量审核保存参数
      paramSave:[],
      fileList:[],
      userId:'',
      token:''
  	}
  },
  components: {
     "item-dialog":itemDialog,
     'querys':querys,
     SelectTree,
     ItemAddCom
  },
  created(){
    this.tableHeight=window.innerHeight-220;
  },
  mounted(){
    this.getItemLabelSearch()
    this.getItemLabel()
    this.getTable(1,20)
    this.userId = sessionStorage.getItem('userId');
    this.token = sessionStorage.getItem('token');
    // this.getTreeDeptCode()
  },
  computed: {
		...mapGetters([
			// 'token',
		])
  },
  watch:{
		deptCode(){
      this.param.deptCode = this.deptCode
      this.currentPage = 1
			this.getTable(1,this.currentNum)
		}
	},
  methods: {
    getValue(value){
			this.deptCode = value
    },
    // 懒加载table树形子数据
    tableLoad(row, treeNode, resolve) {
      let params = {
        'parentItemId':row.itemId
      }
      getChildrenItem(params).then(res => {
        if(res.data.successful){
          let childData =  this.getShowDateTable(res.data.resultValue, this.dictMap, this.columnTypeMap);
					resolve(childData)
        }
      })
    },
    //获取查询条件下拉框
		getItemLabelSearch() {
			let _this = this
			queryFilterParam().then(response => {
				console.log('response', response)//后端返回数据
				if (response.data.successful) {
					let labelList = response.data.resultValue.tableLable
					_this.selectParamDict.set('voltLevel', response.data.resultValue.voltCodeList)
					_this.selectParamDict.set('itemLevel', response.data.resultValue.itemLevelList)
          _this.selectParamDict.set('itemSource', response.data.resultValue.itemSourceList)
          _this.selectParamDict.set('itemPhaseDet', response.data.resultValue.itemPhaseDet)
          _this.$forceUpdate()
				}
			})
		},
		//获取单位树形结构
		getTreeDeptCode(){
			let _this = this
			getUserDept().then(response=>{
				let valueId = response.data.resultValue
				getTreeDeptCode().then(response=>{
					console.log(response)
					if(response.data.successful){
						_this.valueId = valueId
						_this.deptArr = response.data.resultValue
					}
				})
			})
		},
    changeQuery(state,height){
      if(state){
        this.tableHeight = height
        this.isShowQuery = true
        this.showQuery = true
      }else{
        this.tableHeight = height
        this.isShowQuery = false
        this.showQuery = false

      }
    },
    //上传文件成功
    uploadAdviceSuccess(response){
        let _this = this
        if(response.successful){
            _this.advice.fileId = response.resultValue.auditFileId 
            this.$message({
              showClose: true,
              message: '上传成功',
              type: 'success'
            });
        }else{
            let str = response.resultHint
            this.$message({
              showClose: true,
              message: '上传失败 '+str,
              dangerouslyUseHTMLString: true,
              type: 'error'
            });
        }
    },
    //批量审核
    batchCheck(){
        let _this = this
        
        let saveArr = []
        _this.paramSave.forEach(function(item){
            let param = {}
            param.auditType = _this.advice.agreeType
            param.checkAdvice = _this.advice.checkAdvice
            param.itemId = item.itemId
            param.auditPlanId = item.auditPlanId
            param.planFileId = _this.advice.fileId
            saveArr.push(param)
        })
        _this.checkLoading = true
        updateFragmentaryAudit(saveArr).then(response=>{
           _this.checkLoading = false
              if(response.data.successful){
                  this.$message.success("批量修改成功")  
                  _this.batchCheckShow = false
                  _this.getTable(_this.currentPage,_this.currentNum)
                  _this.paramSave = []
                  
              }else{
                  this.$message.error("批量修改失败")
              }
        })
    },
    getRowKeys(row){
      return row.itemId;//返回唯一ID
    },
    // 打开批量审查弹窗
    showBatchCheck(){
        let _this = this
        //当没有勾选时先提示
        if(_this.paramSave.length==0){
             this.$message({
              showClose: true,
              message: '请选择审核项目',
              type: 'error'
            });
        }else{
          _this.batchCheckShow = true
        }
        
    },
    selectItem(){
      this.currentPage =1;
      this.getTable(1,this.currentNum)
    },
    //重置查询
		resetItem() {
			let _this = this;
			_this.param = {
				"pageIndex": 1,
        "pageSize": 20,
        "deptCode":"",
        "itemType": "",
        "isPassed":0,
        "speciality": "",
        "feaStatus":""
			};
			this.getTable(1, 20);
		},
    getItemLabel(){
      let _this = this
      getItemLabelInfo().then(response =>{
          console.log('response',response)//后端返回数据
          if(response.data.successful){
            let labelList = response.data.resultValue.tableLable
            //获取字典
            _this.itemMap = _this.getDictArr(labelList);

          }
      })
    },
    switchFilter(){
      this.isShowFilter = !this.isShowFilter
    },
    // 子组件向父组件传递参数
    changeStatus(status) {
			this.showDrawer = status
			console.log('status'+status)
		},
    showItemInfo(data){
      //   			let _this = this;
			// _this.tempItemId = data.itemId;
      // this.$router.push({path:'/itemInfo',query:{itemId:_this.tempItemId ,oldPath:_this.$route.path}});
      this.showData.itemId = data.itemId
			this.showData.phash = 0
			this.showDrawer = true;
			this.timer = new Date().getTime()
    },
    
    handleSelectionChange(val){
      let _this = this
      _this.paramSave = []
      val.forEach(function(item){
          let obj = {}
          obj.auditPlanId = item.auditPlanId
          obj.itemId = item.itemId
          _this.paramSave.push(obj)
      })
    },
    //获取当前每页条数
    handleSizeChange(val) {
      this.currentNum = val;
      this.getTable(this.currentPage,val)
    },
    //获取当前页码
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage = val;
      this.getTable(val,this.currentNum)
    },
    getTable(page,size){
      let _this = this
      _this.loading = true
      let paramSelect = {}
      paramSelect.pageIndex = page
      paramSelect.pageSize = size
      if(_this.param.isPassed==0){
          paramSelect.feaStatus = '5,11,14'
      }else{
          paramSelect.feaStatus = _this.param.feaStatus
      }

      paramSelect.itemName = _this.param.itemName
      paramSelect.deptCode = _this.param.deptCode
      paramSelect.itemType = _this.param.itemType
      paramSelect.speciality = _this.param.speciality
      paramSelect.itemClass = "3"

       getFragmentary(paramSelect).then(response => {//{}写需要的参数
          console.log('response',response)//后端返回数据
          if(response.data.successful){
              _this.totalNum = response.data.resultValue.totalNum
              _this.tableLabel = response.data.resultValue.tableLable
              let arr = response.data.resultValue.tableLable
              _this.tableLabel = _this.getLabelList(arr)
              _this.dictMap = _this.getDictArr(arr)
              _this.columnTypeMap = _this.getColumnTypeArr(arr)
              _this.tableData = response.data.resultValue.tableData
              _this.tableShowData = _this.getShowDateTable(_this.tableData,_this.dictMap,_this.columnTypeMap)

          }
          _this.loading = false
          
        }).catch(error => {
          console.log(error)//error
        })
        
    },
    //获取展示数据
    getShowDateTable(tableData,dictMap,columnTypeMap){
      let _this = this
      let arr = [];
      for(let i=0;i<tableData.length;i++){
        let tempObj = tableData[i];
        //拷贝对象
        let copyObj = new Object();
        //遍历获取的数据中的属性，如果属性名有和数据字典中的key相对应的，取出数据字典对应的值赋值过去
        for (let [key, value] of Object.entries(tempObj)) {
          console.log(key + ':' + value);
          if(key=="feaStatus"){
               copyObj["feaStatusT"] = value;
          }
          if(dictMap.has(key)){
            let dictValueList = dictMap.get(key);
            for(let j=0;j<dictValueList.length;j++){
              let tempObj2 = dictValueList[j];
              if(tempObj2.codeValue==value){
                copyObj[key] = tempObj2.codeName;
              }
            }
          }else if(columnTypeMap.get(key)=="DATE"){
               copyObj[key+"Rel"] = value
               let timeStr = _this.formartDate(value)
               copyObj[key] = timeStr;
          }else{
            copyObj[key] = value;
          }
        }
        arr.push(copyObj);
      }
      return arr;
    },
    //获取字典
    getDictArr(labelList){ 
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        if(temp.codeList!=null){
          map.set(temp.colDname,temp.codeList);
        }
      }
      return map
    },
     //获取字段类型字典
    getColumnTypeArr(labelList){
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        map.set(temp.colDname,temp.colType);
      }
      return map
    },
    //获取插入和修改的表头
    getLabelList(labelList){ 
      let _this =this
      labelList.forEach(function (item) {
        _this.$set(item,'value','');
      })
      let common_table_info = {};//数组转对象，避免类型报错
      for(let i in labelList){
        common_table_info[i] = labelList[i];
      }
      return common_table_info
    },
     //时间戳字符串的转化
    formartDate(param) {
      var time = new Date(param);
      var y = time.getFullYear();
      var m = time.getMonth()+1;
      var d = time.getDate();
      // var h = time.getHours();
      // var mm = time.getMinutes();
      // var s = time.getSeconds();
      // return y+'-'+this.add0(m)+'-'+this.add0(d)+' '+this.add0(h)+':'+this.add0(mm)+':'+this.add0(s);
      return y+'-'+this.add0(m)+'-'+this.add0(d);
    },
    add0(m){
      return m<10?'0'+m:m;
    },
  }
}
</script>
