<template>
    <div class="menu" :style="{'width':isCollapse ? '64px':'220px'}">
        <el-menu ref ="elMenu" :default-active="activeIndex == $route.path?activeIndex:$route.path" class="el-menu-demo" @select="handleSelect"
                 background-color="#fff"
                 :unique-opened="true"
                 text-color="#333"
                 :collapse="isCollapse"
                 
                 active-text-color="#369692"><!-- :router="true" -->
            <span v-for="route in permission_routes" >
                <!-- 没有子菜单的 -->
                <el-menu-item  :index="route.path" v-if="!route.hidden&&!route.isOpen && notHttp(route.path)">
                    <template v-for="children in route.children">
                        <i class="iconfont" v-if="children.name == '储备中心概览'">&#xe620;</i>
