                        </span>
                    </template>
                    
                </el-menu-item><!-- 外网连接处理 -->
                <a class="el-menu-item" :href="route.path" target="_blank" v-else-if="!route.hidden&&!route.isOpen && !notHttp(route.path)">
                    <span v-for="children in route.children">
                      {{children.name}}
                    </span>
                </a>

                <el-submenu  :index="route.path" v-else-if="!route.hidden&&route.isOpen">

                <!-- 一级菜单项的文字 -->
                <template slot="title"> 
                    <i class="iconfont"> 
                        <i v-if="route.name == '项目需求管理'">&#xe60a;</i>
                        <i v-else-if="route.name == '项目立项管理'">&#xe605;</i>
                        <i v-else-if="route.name == '可研评审管理'">&#xe60c;</i>
                        <i v-else-if="route.name == '项目储备管理'">&#xe609;</i>
                        <i v-else-if="route.name == '模版配置'">&#xe60d;</i>

                    </i> 
                    <span slot="title">{{route.name}}</span>
                    </template>

                    <span v-for="children in route.children">
                        <!-- 二级菜单 -->
                        <el-submenu v-if="children.children" :index="children.path">
                            <!-- 二级菜单项的文字 -->
                            <template slot="title">
                                <span style="padding-left: 0px;">
                                    <i class="iconfont"></i> {{children.name}}
                                </span>
                            </template>

                            <div :index="children.path" v-if='children.isOpen'>
                                <!-- 三级菜单项的文字 -->
                                <el-menu-item v-for="subchildren in children.children" v-if="notHttp(subchildren.path)" :index="subchildren.path">
                                    <span style="padding-left: 15px;">
                                        <i class="iconfont"></i>{{subchildren.name}}
                                    </span>
                                </el-menu-item><!-- 外网连接处理 -->
                                <a class="el-menu-item"  target="_blank" v-else :href="subchildren.path">
                                    <i class="iconfont"></i>{{subchildren.name}}
                                </a>
                                
                            </div>

                        </el-submenu>
                        <el-menu-item v-else-if="!children.children && notHttp(children.path)" :index="children.path">
                            <!-- 二级菜单项的文字 -->
                            <template slot="title">
                                 <span style="padding-left: 4px;">
                                <i class="iconfont"></i>{{children.name}}
                                 </span>
                            </template>

                        </el-menu-item><!-- 外网连接处理 -->
                        <a class="el-menu-item"  target="_blank" v-else-if="!children.children && !notHttp(children.path)" :href="children.path">
                            <i class="iconfont"></i>{{children.name}}
                        </a>
                    </span>
                    


                    

                </el-submenu>
