<script>
    import {mapGetters,mapState} from 'vuex'
    import { getUrlKey } from '@/utils/auth'

    export default {
        name: 'naver',

        data() {
            return {
                isCollapse:false,
                activeIndex:"",
                ischange:true
            };
        },

        watch: {
            // routes(newval,oldval) {
            //     console.log('newval',newval)
            //    this.activeIndex = newval.path
            
            // },
            // $route: {
            //     handler: function (to, from) {
            //        console.log('路由变化nav',to.path,this.ischange)
            //        if(this.ischange){
            //             console.log('执行变化')
            //             this.activeIndex = to.path
            //             this.ischange = false
            //        }
            //     },
            //     immediate: false
            // },
            
        },
        computed: {
            ...mapGetters([
                'permission_routes',
            ]),
           ...mapState({
                pathChangeActive: state => state.itemadd.pathChangeActive
            }),
            routes() {
              return global.antRouter
            }
        },
        
        mounted() {
            if(getUrlKey('path',window.location.href)){
                console.log("隐藏菜单")
                this.$store.dispatch('app/setHideMenu', true)
                this.$store.dispatch('app/setIscollapse', true)//隐藏菜单 更新布局

            }else{
                this.$store.dispatch('app/setHideMenu', false)
                this.$store.dispatch('app/setIscollapse', false)//隐藏菜单 更新布局

            }
            console.log('this.routes',this.permission_routes)
            let _this = this

        },
        methods: {
            notHttp(str){
                if(str.substring(0, 4) == 'http'){
                    return false
                }else{
                    return true
                }
            },
            toggleIsCollapse(){
                this.isCollapse = !this.isCollapse
                this.$store.dispatch('app/setIscollapse', this.isCollapse)
            },
            setPath(key){
                let _this =  this
                if(key == '/templateConfiguration/1/1' ){
                    _this.$router.push({
                        path: key,
                        query: { tmplTypeId: '1' },
                      })
                    }else if(key == '/templateConfiguration/1/2'){
                        _this.$router.push({
                            path: key,
                            query: { tmplTypeId: '2' },
                          })
                    }else if(key == '/templateConfiguration/1/3'){
                        _this.$router.push({
                            path: key,
                            query: { tmplTypeId: '3' },
                          })
                    }else if(key == '/templateConfiguration/2/1'){
                        _this.$router.push({
                            path: key,
                            query: { tmplTypeId: '4' },
                          })
                    }else if(key == '/templateConfiguration/2/2'){
                        _this.$router.push({
                            path: key,
                            query: { tmplTypeId: '5' },
                          })
                    }else if(key == '/templateConfiguration/3/1'){
                        _this.$router.push({
                            path: key,
                            query: { tmplTypeId: '6' },
                          })
                    }else if(key == '/templateConfiguration/3/2'){
                        _this.$router.push({
                            path: key,
                            query: { tmplTypeId: '7' },
                          })
                    }else if(key == '/templateConfiguration/3/3'){
                        _this.$router.push({
                            path: key,
                            query: { tmplTypeId: '8' },
                          })
                    }else if(key == '/templateConfiguration/3/4'){
                        _this.$router.push({
                            path: key,
                            query: { tmplTypeId: '9' },
                          })
                    }else if(key == '/templateConfiguration/4/1'){
                        _this.$router.push({
                            path: key,
                            query: { tmplTypeId: '11' },
                          })
                    }else if(key == '/templateConfiguration/5/1'){
                        _this.$router.push({
                            path: key,
                            query: { tmplTypeId: '10' },
                          })
                    }
                    else{
                        _this.$router.push({
                            path: key,
                        })
                        
                          console.log("key"+key)
                    }
            },
            handleSelect(key, keyPath) {
                
                let _this = this
                console.log(_this.$refs.elMenu)
                
                // if(!_this.pathChangeActive){
                    _this.setPath(key)
                // }else{
                //     _this.$confirm('您有未保存的数据，是否继续', '提示', {
                //             confirmButtonText: '确定',
                //             cancelButtonText: '取消',
                //             type: 'warning'
                //         }).then(() => {
                //            _this.setPath(key)

                //         }).catch(() => {
                //             _this.$refs.elMenu.activeIndex = _this.$route.path
                //             // _this.activeIndex = _this.$route.path
                           
                            

                //         })
                   
                // }
                
               

                

            },
            oneChild(children) {
                if (children) {
                    const leng = Object.keys(children);
                    if (leng.length == 1) {
                        return true
                    } else {
                        return false
                    }
                }

            },
            hasOneShowingChild(children = [], parent) {
                const showingChildren = children.filter(item => {
                    if (item.hidden) {
                        return false
                    } else {
                        // Temp set(will be used if only has one showing child)
                        this.onlyOneChild = item
                        return true
                    }
                })

                // When there is only one child router, the child router is displayed by default
                if (showingChildren.length === 1) {
                    return true
                }

                // Show parent if there are no child router to display
                if (showingChildren.length === 0) {
                    this.onlyOneChild = {...parent, path: '', noShowingChildren: true}
                    return true
                }

                return false
            },
            // handleSelect(key, keyPath) {
            //     console.log(key, keyPath);

            // }
        }
    }
</script>
