<template>
  <div>
    <!-- 评审计划编制待办 -->
    <div class="table-item-box">
      <div :class="showDrawer?'table-wrapper-run':'table-wrapper-init'">
        <el-table
          class="project"
          :data="tableDataShow"
          v-loading="loading"
          :height="tableHeight"
          border
          :header-cell-style="{background:'#359691',color:'#fff'}"
          cell-class-name="column"
          :highlight-current-row="true"
          row-key="itemId"
          ref="itemTable"
          :tree-props="{children: 'children'}"
        >
          <el-table-column type="selection" width="45"></el-table-column>
          <el-table-column type="index" width="50" label="序号"></el-table-column>
          <el-table-column
            v-for="(item, index) in tableLabel"
            v-if="item.colDname != 'itemName' && item.showType == 1"
            :key="index"
            :prop="item.colDname"
            :label="item.dispName"
            :show-overflow-tooltip="true"
            min-width="200"
          ></el-table-column>
          <el-table-column
            align="left"
            :show-overflow-tooltip="true"
            v-else-if="item.colDname == 'itemName'"
            :label="item.dispName"
            min-width="200"
          >
            <template slot-scope="scope">
              <span v-if="scope.row.isBatchPackage ==1" >{{ scope.row.itemName }}</span>
              <span class="blue" v-else @click="showDetailDialog(scope.row)">{{ scope.row.itemName }}</span>
              
            </template>
          </el-table-column>
        </el-table>
        <div class="table-item-box" style="height: 50% !important;">
          <div class="table-box" :class="showDrawer?'table-wrapper-run':'table-wrapper-init'">
            <!-- 留言原因 -->
            <div class="plan-assess">
              <el-form :model="form" class="demo-form-inline reason-form" label-width="10%">
                <el-form-item label="意见:">
                  <el-input class="elinput reason" type="textarea" v-model="auditItemParam.auditAdvice" placeholder></el-input>
                </el-form-item>
              </el-form>
            </div>
            
            <!-- 确定 取消 -->
            <div slot="footer" class="dialog-footer option">
              <div class="div">
                <el-button @click="backWorkFlow">退回</el-button>
                <el-button type="primary" @click="saveAudtiInfo" :loading="loadingBtn1">同意</el-button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div :class="showDrawer?'item-add-wrapper-run':'item-wrapper-init'">
        <!-- 新增 修改 浏览 引itemaddcom -->
        <item-add-com
          v-if="showDrawer"
          :key="timer"
          :showData="showData"
          :showAddDialog="showDrawer"
          :isShowQuery="showQuery"
          :tableHeightTab="tableHeight"
          @changeStatus="changeStatus"
        ></item-add-com>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import { mapGetters } from "vuex";
import SelectTree from "@/components/treeSelect.vue";
import itemDialog from "@/views/project/item.vue";
import ItemAddCom from "@/views/project/itemAddCom";

import {
  auditBySpeciality,
  getTreeDeptCode,
  getUserDept,
  queryItemTitle,
  queryAll,
  saveWorkItem,
  getProcessStatus,
  updateProcessFinished,
  getItemInfoByProcessInstId,
  queryTitle,
  getItemInfoByProcessInstIdWithBatchGroup,
  checkPlan
} from "@/api/data";

import { queryBySpeciality, getSpecialityIds } from "@/api/data";
import querys from "@/components/querys";

import { finishWorkItem, backActivity ,noticeAdd} from "@/api/workflow";

export default {
  name: "questions",
  data() {
    return {
      itemId:"",
      showQuery:false,
      tableHeightTab:"",
      timer: "",
      showAddDialog: false, //弹窗是否展示-新增 修改 浏览
      showData: {
        itemId: null, //项目id
        specClass: "", //专项类型
        specClassName: "", //专项类型名称
        oldPath: "", //原来路由
        tableLabelArr: "", //表头
        phash: "1", //阶段1=立项 2=可研 3=储备 0=浏览
        parentItemId: null //父项id-新增下级时用
      },
      showDrawer: false,
      deptCode: "",
      isClearable: true, // 可清空（可选）
      isAccordion: true, // 可收起（可选）
      valueId: "", // 初始ID（可选）
      props: {
        // 配置项（必选）
        value: "id",
        label: "name",
        children: "children",
        code: "deptCode"
        // disabled:true
      },
      deptArr: [],
      //加载保存审核意见
      loadingBtn1: false,
      //评审意见参数
      auditItemParam: {
        audited: "1",
        auditAdvice: "",
        itemIds: "",
          itemPhaseDet:""
      },
      isShowQuery: false,
      dialogVisibleL: false,
      dialogLoading: false,
      loading: false,
      delData: {
        ids: []
      },
      lableList: [{ value: "" }],
      totalNum: 0,
      currentPage: 1,
      currentNum: 10,
      year: "",
      dialogFormVisible: false,
      form: {},
      options: [],
      value: "",
      formLabelWidth: "140px",
      input: "",
      // 子组件的表格数据
      tableData: [],
      // 子组件的表头数据
      tableLabel: [],

      //查询条件
      param: {
        createOrg: "", //单位编码
        itemName: "", //项目名称
        itemType: "", //项目类型
        pageSize: 10,
        speciality: "", //专业类型
        voltLevel: "", //电压等级
        // feaStatus: 0,  //可研状态
        ghYear: "", //规划年份
        itemSource: ""
      },
      // dictMap: Object,
      dictMap: new Map(),
      tableDataShow: [],
      saveData: {
        kssj: ""
      },
      //提交评审计划弹框中的信息
      pleanTableLabel: [],
      planLabel: {},
      //项目id
      tempItemId: "",
      //项目名称
      tempItemName: "",
      //是否展示项目详情弹框
      itemShow: false,
      saveLabel: {},
      //项目可研报告文件名
      reportFileName: "",
      //获取字段类型字典，用于日期类型的展示
      columnTypeMap: new Map(),
      isShowFilter: false,
      //自动表格高度

      tableHeight: "65vh",
      minDate: "",
      maxDate: "",
      pickerOptionsStart: {
        disabledDate: time => {
          let maxDate = new Date(this.maxDate);
          let minDate = Date.now() - 8.64e7;
          return time.getTime() <= minDate || time.getTime() > maxDate;
        }
      },
      pickerOptionsEnd: {
        disabledDate: time => {
          let minDate = new Date(this.minDate);
          let maxDate = Date.now() - 8.64e7;
          return time.getTime() <= maxDate || time.getTime() <= minDate;
        }
      },

      auditPlanId: "",
      //点击提交传入的参数
      saveParams: [],
      //用户所属专项类型值
      userSpecialityArr: []
    };
  },
  components: {
    querys,
    SelectTree,
    ItemAddCom
  },
  watch: {
    deptCode() {
      this.param.createOrg = this.deptCode;
      this.getTable(this.currentPage, this.currentNum);
    }
  },
  created() {
    //表格自动高度
    this.tableHeight=window.innerHeight-300;
  },


  mounted() {
    this.getSpecialityIds();
    this.getTableWithTitle(1, 10);
    // this.getTreeDeptCode()
  },
  computed: {
    ...mapGetters([
      // 'token',
    ])
  },
  methods: {
    cancel() {
        top.opener=null;
        top.close();     //注意,   不可跨域
        // parent.window.close();
        // var index = parent.layer.getFrameIndex(window.name);
        // parent.layer.close(index);
    },
    getValue(value) {
      this.deptCode = value;
    },
    // 子组件向父组件传递参数
    changeStatus(status) {
      this.showDrawer = status;
      console.log("status" + status);
    },
    //展示项目详细信息
    showDetailDialog(row) {
      this.showData.itemId = row.itemId;
      this.showData.phash = 0;
      this.showDrawer = true;
      this.timer = new Date().getTime();
    },
    //获取单位树形结构
    getTreeDeptCode() {
      let _this = this;
      getUserDept().then(response => {
        let valueId = response.data.resultValue;
        getTreeDeptCode().then(response => {
          console.log(response);
          if (response.data.successful) {
            _this.valueId = valueId;
            _this.deptArr = response.data.resultValue;
          }
        });
      });
    },

    //保存审核意见
    saveAudtiInfo() {
        let params = {
            "user": this.$route.query.iscUserId,
            "workItemID": this.$route.query.workItemID
        }
        // let params = {
        //   "user": 'ff8080817147f21001735d460fa438c7',
        //   "workItemID": 10113
        // }
        this.loadingBtn1 = true
        finishWorkItem(params).then(res => {
            if(res.data.status == '0'){
                let params1 = {
                    processInstID:  this.$route.query.processInstID,
                    itemId:  this.auditItemParam.itemIds,
                    userId:  this.$route.query.iscUserId,
                    isAudit: 1,
                    workItemId:  this.$route.query.workItemID,
                    auditDesc: this.auditItemParam.auditAdvice,
                    itemPhaseDet:9
                }
                if(this.tableDataShow[0].hasPackage ==1){
                  params1.isBatchPackage = true
                }else{
                  params1.isBatchPackage = false
                }
                saveWorkItem(params1).then(result => {
                  this.loadingBtn1 = false
                  let isHasPackage = this.tableDataShow[0].hasPackage  
                  if (result.data.successful){
                    let param ={
                      itemId:this.auditItemParam.itemIds,
                      checkType:1,
                      checkAdvice:this.auditItemParam.auditAdvice,
                      hasPackage:isHasPackage
                    }
                    let paramArr = []
                    paramArr.push(param)
                    checkPlan(paramArr).then(res => {
                      if(res.data.successful){
                        let resVal = res.data.resultValue
                        if(resVal.length >0 ){
                            
                            resVal.forEach(function(item){  
                              let noticeArr = []
                              let noticeData = {}
                              noticeData.content = item.itemName + '项目，评审计划已经下达，评审时间 '+ item.startDate+' —— '+item.endDate
                              noticeData.noticeUser = item.createor;
                              noticeArr.push(noticeData)//流程通知所需参数
                              noticeAdd(noticeArr).then(response =>{
                                  console.log(response)
                              })
                            })
                        }
                        this.$alert('操作成功', '提示', {
                          confirmButtonText: '确定',
                          callback: () => {
                            parent.GetChildValue("true");
                          }
                        });
                      } 
                    })  
                  }else {
                      let error = result.data.resultHint;
                      this.$message.error(error);
                  }
                });
                
                
            }else {
              this.$message.error(res.data.message);
            }

        })
    },

    backWorkFlow() {
        let params = {
            "user": this.$route.query.iscUserId,
            "currentActInstID": this.$route.query.activityInstID,
            "destActDefID": "TJKYPSJH",
            "rollBackStrategy": "path"
        }
        // let params = {
        //     "user": 'ff8080817147f21001735d460fa438c7',
        //     "currentActInstID":11154,// this.$route.query.activityInstID,
        //     "destActDefID": "TJKYPSJH",
        //     "rollBackStrategy": "path"
        // }
        backActivity(params).then(res => {
            if(res.data.status == 0) {
                let params1 = {
                    processInstID: this.$route.query.processInstID,
                    itemId:  this.auditItemParam.itemIds,
                    userId:  this.$route.query.iscUserId,
                    isAudit: 2,
                    workItemId:  this.$route.query.workItemID,
                    auditDesc: this.auditItemParam.auditAdvice,
                    itemPhaseDet:8
                }
                
                // if(toback){
                  saveWorkItem(params1).then(result => {
                    if (result.data.successful){
                      let isHasPackage = this.tableDataShow[0].hasPackage  
                      let param ={
                        itemId:this.auditItemParam.itemIds,
                        checkType:0,
                        checkAdvice:this.auditItemParam.auditAdvice,
                        hasPackage:isHasPackage
                      }
                      let paramArr = []
                      paramArr.push(param)
                      checkPlan(paramArr).then(res => {
                        if(res.data.successful){
                          this.$alert('操作成功', '提示', {
                            confirmButtonText: '确定',
                            callback: () => {
                              parent.GetChildValue("true");
                            }
                          });
                        } 
                      })
                    }
                    else {
                        let error = result.data.resultHint;
                        this.$message.error(error);
                    }
                  });
                // }
                
            }else {
                this.$message.error(res.data.message);
            }

        })
    },

    getSpecialityIds() {
      let _this = this;
      getSpecialityIds().then(response => {
        if (response.data.successful) {
          let str = response.data.resultValue;
          if (str != "") {
            _this.userSpecialityArr = str.split(",");
          }
          console.log(response);
        }
      });
    },
    //时间戳字符串的转化
    formartDate(param) {
      var time = new Date(param);
      var y = time.getFullYear();
      var m = time.getMonth() + 1;
      var d = time.getDate();
      // var h = time.getHours();
      // var mm = time.getMinutes();
      // var s = time.getSeconds();
      // return y+'-'+this.add0(m)+'-'+this.add0(d)+' '+this.add0(h)+':'+this.add0(mm)+':'+this.add0(s);
      return y + "-" + this.add0(m) + "-" + this.add0(d);
    },
    add0(m) {
      return m < 10 ? "0" + m : m;
    },
    switchFilter() {
      this.isShowFilter = !this.isShowFilter;
    },
    //获取字段类型字典
    getColumnTypeArr(labelList) {
      let map = new Map();
      for (let i = 0; i < labelList.length; i++) {
        let temp = labelList[i];
        map.set(temp.colDname, temp.colType);
      }
      return map;
    },

    //查询表头以及列表数据
    getTableWithTitle(size) {
      //（page,size）
      let _this = this;
      _this.loading = true;
      queryTitle().then(response => {
        _this.loading = false;
        if (response.data.successful) {
          //获取表头
          _this.tableLabel = response.data.resultValue
          //从表头中抽取出字典信息列表
          _this.dictMap = _this.getDictArr(_this.tableLabel);
          //获取字段类型对应关系
          _this.columnTypeMapDict = _this.getColumnTypeArr(_this.tableLabel);
          //从路由中获取流程实例id
          let processInstID = _this.$route.query.processInstID;
          //获取列表数据
          let param = {
            'processInstID':processInstID
          }
          console.log('processInstID'+_this.$route.query.processInstID)
          //根据流程实例id查询项目id
          getItemInfoByProcessInstIdWithBatchGroup(param).then(
            res => {
              if (res.data.successful) {
                console.log(res);
                let itemInfo = res.data.resultValue;
                // 保存或者退回需要用到的参数
                _this.auditItemParam.itemIds = itemInfo.itemId;
                _this.auditItemParam.itemPhaseDet = itemInfo.itemPhaseDet;
                let arr = [];
                if (itemInfo != null) {
                  arr.push(itemInfo);
                  
                }
                _this.tableDataShow = _this.getShowDateTable(arr, _this.dictMap, _this.columnTypeMapDict);
              }
            }
          );
        } else {
          this.$message.error("查询表头信息出错");
        }
      });
    },
    getDictArr(labelList) {
      //根据当前用户id查询用户所属的单位
      //获取数据字典
      let _this = this;
      let map = new Map();
      for (let i = 0; i < labelList.length; i++) {
        let temp = labelList[i];
        if (temp.codeList != null) {
          map.set(temp.colDname, temp.codeList);
        }
      }
      return map;
    },
    //获取展示数据
      getShowDateTable(tableData, dictMap, columnDictMap) {
          let _this = this
          let arr = [];
          for (let i = 0; i < tableData.length; i++) {
              let tempObj = tableData[i];
              //拷贝对象
              let copyObj = new Object();
              //遍历获取的数据中的属性，如果属性名有和数据字典中的key相对应的，取出数据字典对应的值赋值过去
              for (let [key, value] of Object.entries(tempObj)) {
                  //console.log(key + ':' + value);
                  let lowerKey = key.toLowerCase()
                  if (dictMap.has(key)) {
                      let dictValueList = dictMap.get(key);
                      for (let j = 0; j < dictValueList.length; j++) {
                          let tempObj2 = dictValueList[j];
                          if (tempObj2.codeValue == value) {
                              copyObj[key] = tempObj2.codeName;
                          }
                      }
                  } else if (dictMap.has(lowerKey)) {
                      let dictValueList = dictMap.get(key.toLowerCase);
                      for (let j = 0; j < dictValueList.length; j++) {
                          let tempObj2 = dictValueList[j];
                          if (tempObj2.codeValue == value) {
                              copyObj[key] = tempObj2.codeName;
                          }
                      }
                  } else if (columnDictMap.get(key) == "date" || columnDictMap.get(key) == "month"
                      || columnDictMap.get(key) == "year") {
                      if(value!=null&&value!=''){
                          copyObj[key] = _this.formartDate(value, columnDictMap.get(key));
                      }  
                  } else {
                      copyObj[key] = value;
                  }

                  if (key == "itemClass") {
                      copyObj["itemClassT"] = value
                  }

                  if (key == "conDept") {
                      copyObj["conDept"] = value
                  }
                  if (key == "itemLevel") {
                      copyObj["itemLevelT"] = value
                  }

                  //add by liangwu
                  if (key == "specDetailClass") {
                      copyObj["specDetailClassT"] = value
                  }

                  if (key == "specClass") {
                      copyObj["specClassT"] = value
                  }
                  //add end
              }
              if (typeof (tempObj.children) != 'undefined' && tempObj.children != null && tempObj.children.length > 0) {
                  let tempArr = _this.getShowDateTable(tempObj.children, dictMap, columnDictMap)
                  copyObj.children = tempArr
              }
              arr.push(copyObj);
          }
          return arr;
      },
  }
};
</script>
