<template>
  <el-select :value="valueTitle" ref="myselect" clearable :size="size" :class="{'size_large' : size == 'large'}" @clear="clearHandle" :disabled="disabled">
    <el-option :value="valueTitle" :label="valueTitle" class="options" v-show="reFresh" @dblclick.native="double">
      <el-tree  
        id="tree-option"
        ref="selectTree"
        :accordion="accordion"
        :data="options"
        :value="valueId"
        :props="props"
        node-key="id"    
        :default-expanded-keys="defaultExpandedKey"
        @node-click="handleNodeClick"
        onselectstart='return false'
        >
      </el-tree>
    </el-option>
  </el-select>
</template>

<script>
import { mapState,mapActions } from 'vuex'
import {getTreeDeptCode ,getUserDept} from '@/api/data'
import { treeList } from '../store/modules/treeList'

export default {
  name: "el-tree-select",
  props:{
    disabled:{default:false},
    size:{type: String,default:'small'},
    
    
    // 选项列表数据(树形结构的对象数组)
    // options:{ type: Array, default:new Array},
    
    // 初始值
    //  value:{ type: String},

    deptCodeT:{type: String,default:''},

    deptName:{type: String,default:''},
    
    // 可清空选项
    clearable:{ type:Boolean},
    
    // 自动收起
    accordion:{ type:Boolean }
  },
  data() {
    return {
      valueId: null,
      options:[],
      valueTitle:'',
      defaultExpandedKey:[],
      reFresh: false,
      deptCode:"",
      
      // 配置项
      props:{
        label: 'name',
        children: 'children',
        code: 'deptCode',
        value: 'id'
      }
    }
  },
  created(){
    this.$store.dispatch("treeList/set_treeInfo")
    console.log('created')
  },
  store:treeList,
  mounted(){
    this.valueId = this.value,    // 初始值
    console.log(this.options)
    console.log(this.props)
    this.initHandle()
    console.log(this.$store.state.treeListInfo)
    // 判断如果全局字段treelistinfo存在 就读全局字段 如果不存在就请求接口
    //刷新页面会重新请求
    if(this.$store.state.treeListInfo){
      this.options = this.$store.state.treeListInfo
      this.reFresh = true;
      console.log('走缓存')
    }else{
      this.getTreeDeptCode()
       console.log('走接口')
    }
    
    // console.log(this.options)
  },
  computed: {
		
		...mapState({
             treeListInfo:state=>state.treeListInfo
        })
    },
  methods: {
      //双击事件--关闭select-tree
    double() {
      this.$refs.myselect.blur()
    },

    // getValue(value){
		// 	this.deptCode = value
		// },
    //获取单位树形结构
		getTreeDeptCode(){
			let _this = this
			getUserDept().then(response=>{
				let valueId = response.data.resultValue
				getTreeDeptCode().then(response=>{
					if(response.data.successful){
            // _this.$emit('getTreeList',response.data.resultValue)
						_this.valueId = valueId
            _this.reFresh = true;
            _this.$store.state.treeListInfo = response.data.resultValue
            _this.options = response.data.resultValue
            
            console.log(_this.$store.state.treeListInfo)
					}
				})
			})
		},
    // 初始化值
    initHandle(){
      let _this = this
      if(_this.deptName!=''){
        _this.valueTitle = _this.deptName
      }
      // console.log(_this.$refs.selectTree)
      // if(typeof(_this.$refs.selectTree)!='undefined'
      //               &&this.options.length>0&&this.deptCodeT!=''){
      //     let temp1 = this.$refs.selectTree.getNode(this.deptCodeT)
      //     this.valueTitle = 'AAA'
      //     if(temp1!=null){
                
      //           // this.valueTitle = this.$refs.selectTree.getNode(this.deptCodeT).data[this.props.label]     // 初始化显示
      //           // this.$refs.selectTree.setCurrentKey(this.deptCodeT)       // 设置默认选中
      //           // this.defaultExpandedKey = [this.deptCodeT]   
      //     }
          
      // }
      
      this.initScroll()
    },
    // 初始化滚动条
    initScroll(){

      this.$nextTick(()=>{
        let scrollWrap = document.querySelectorAll('.el-scrollbar .el-select-dropdown__wrap')[0]
        let scrollBar = document.querySelectorAll('.el-scrollbar .el-scrollbar__bar')
        scrollWrap.style.cssText = 'margin: 0px; max-height: none; overflow: hidden;'
        scrollBar.forEach(ele => ele.style.width = 0)
      })
    },
    // 切换选项
    handleNodeClick(node){
      this.valueTitle = node[this.props.label]
      this.valueId = node[this.props.value]
      this.deptCode = node[this.props.code]
      this.$emit('getValue',this.deptCode)
      console.log(this.deptCode)
      this.defaultExpandedKey = []
      
    },
    // closeSelectTree(node){
    //   console.log(111)
    //   this.reFresh = false
      
    // },
    // 清除选中
    clearHandle(){
      this.valueTitle = ''
      this.valueId = null
      this.defaultExpandedKey = []
      this.clearSelected()
      this.$emit('getValue',null)
    },
    // 清空选中样式
    clearSelected(){
      let allNode = document.querySelectorAll('#tree-option .el-tree-node')
      allNode.forEach((element)=>element.classList.remove('is-current'))
    }
  },
  watch: {
    // value(){
    //   this.valueId = this.value
    //   this.reFresh = true
    //   this.initHandle()
    // }
    deptName(){

      if(this.deptName!=''){
        this.valueTitle = this.deptName
      }
    }
    
  },
}
</script>

<style scoped>
  .size_large{
    width: 300px
  }
  .el-scrollbar .el-scrollbar__view .el-select-dropdown__item{
    height: auto;
    max-height: 274px;
    padding: 0;
    overflow: hidden;
    overflow-y: auto;
  }
  .el-select-dropdown__item.selected{
    font-weight: normal;
  }
  ul li >>>.el-tree .el-tree-node__content{
    height:auto;
    padding: 0 20px;
  }
  .el-tree-node__label{
    font-weight: normal;
  }
  .el-tree >>>.is-current .el-tree-node__label{
    color: #409EFF;
    font-weight: 700;
  }
  .el-tree >>>.is-current .el-tree-node__children .el-tree-node__label{
    color:#606266;
    font-weight: normal;
  }
</style>