<template>
  <div class="container">
    
    <div class="main clearfix" :style="{'margin-left':$store.state.app.isCollapse ? '84px':'240px'}">
     
      <div class="main_container">
        <div class="order_btn">
          <!-- <el-button type="primary" size="small"  @click="submitTable" class="margin_right">提交</el-button> -->
          <el-button type="primary" size="small"><router-link  to="/storageTable">项目分级排序标准</router-link></el-button>
        </div>
         
        <el-tabs v-model="activeName" @tab-click="handleClick">
          <el-tab-pane  v-for="(item,index) in tabList" :key="'lay1'+index" :label="item.name" :name="item.name">
                <!-- {{item.name}} -->
                
                <el-tabs v-model="activeName2" @tab-click="handleClick2" v-if="item.name == '零星购置'">
                  <el-tab-pane  v-for="(itemli,indexli) in item.list" :key="'lay2'+indexli" :label="itemli.name" :name="itemli.name"></el-tab-pane>
                </el-tabs>
          </el-tab-pane>
          
        </el-tabs>
        <orderComponent :tableHeight="tableHeight" :orderData="orderData" :acitve="activeName" ></orderComponent>
      </div>
      
    </div>
    
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import orderComponent from './orderComponent'

import {getFs,saveNum,submitPrjStItemCheck} from '@/api/data'
export default {
  name: 'storageOrder',
  data() {
    return {
      lxData:[],
      activeName2:'',
      orderData:[],
      tabList:{},
      activeName:'',
      tableHeight:''
      
    }
  },
  components: {
    orderComponent
  },
  computed: {
    ...mapGetters([
      'roles'
    ]),
    
  },
  
  mounted(){
    const _this = this
    _this.tableHeight=window.innerHeight-280;
    _this.getTable()

  },
  created(){
    
  },
  methods: {
    submitTable(){
      let _this = this
      // if(_this.submitData!=''){
        
        
        //{'itemIds':_this.submitData,'userId':_this.token}
        submitPrjStItemCheck().then(response => {
              console.log('response',response)
            if(response.data.successful){
              
                this.$message({
                  showClose: true,
                  message: '提交成功',
                  type: 'success'
                });
                _this.getTable()
                _this.$refs.storagetable.clearSelection();

            }else{
              this.$message({
                  showClose: true,
                  message: response.data.resultHint,
                  type: 'error'
                });
            }
            _this.loading =false

        })
      // }else{
      //   this.$message({
      //     showClose: true,
      //     message: '请选择要提交的数据',
      //     type: 'error'
      //   });
      // }
    },
    handleClick(tab){
      console.log(tab)
      if(this.activeName == '零星购置'){//设置零星购置初始化的data
        this.activeName2 = this.tabList[tab.index].list[0].name
        this.orderData = this.tabList[tab.index].list[0].list
        this.tableHeight=window.innerHeight-320;
      }else{
        this.orderData = this.tabList[tab.index].list
        this.tableHeight=window.innerHeight-280;
      }
    },
    handleClick2(tab){
      this.orderData = this.lxData[tab.index].list
      this.activeName2 = this.lxData[tab.index].name
        // this.orderData = this.tabList[tab.index].list[0].list
        this.tableHeight=window.innerHeight-320;

    },
    handleCurrentChange(val) {
      console.log('触发选择了',val.localCode)
      console.log(val)
      this.selectCode = val.localCode
      // this.changeOrder(val,index)
    },
   
    getTable(){
      let _this = this
      _this.loading = true
       getFs().then(response => {//{}写需要的参数
        _this.loading = false
        if(response.data.successful){
          let arr = []
          response.data.resultValue.forEach((item,idx) => {
            if(item.list.length){
              arr.push(item)
              if(item.name == '零星购置'){
                _this.lxData = item.list
                console.log('lxData',_this.lxData)
              }

            }
          })
          _this.tabList = arr
          console.log('arrarr',arr)
          _this.orderData = _this.tabList[0].list//设置当前数据
          console.log( _this.tabList)
          _this.activeName = _this.tabList[0].name//设置选中当前名字

          
        }
     })
    },
    
   
  }
}
</script>
