      valueId: "", // 初始ID（可选）
      props: {
        // 配置项（必选）
        value: "id",
        label: "name",
        children: "children",
        code: "deptCode"
        // disabled:true
      },
      deptArr: [],
      //加载保存审核意见
      loadingBtn1: false,
      //评审意见参数
      auditItemParam: {
        audited: "1",
        auditAdvice: "",
        itemIds: "",
        speciality: "",//专项类型
        specDetailClass : "",//电网基建专项类型细分
        specClass: "",//零购专项类型细分
        itemPhaseDet:""
      },
      isShowQuery: false,
      dialogVisibleL: false,
      dialogLoading: false,
      loading: false,
      delData: {
        ids: []
      },
      lableList: [{ value: "" }],
      totalNum: 0,
      currentPage: 1,
      currentNum: 10,
      year: "",
      dialogFormVisible: false,
      form: {},
      options: [],
      value: "",
      formLabelWidth: "140px",
      input: "",
      // 子组件的表格数据
      tableData: [],
      // 子组件的表头数据
