      //查询条件
      param: {
        createOrg: "", //单位编码
        itemName: "", //项目名称
        itemType: "", //项目类型
        pageSize: 10,
        speciality: "", //专业类型
        voltLevel: "", //电压等级
        // feaStatus: 0,  //可研状态
        ghYear: "", //规划年份
        itemSource: ""
      },
      // dictMap: Object,
      dictMap: new Map(),
      tableDataShow: [],
      saveData: {
        kssj: ""
      },
      //提交评审计划弹框中的信息
      pleanTableLabel: [],
      planLabel: {},
      //项目id
      tempItemId: "",
      //项目名称
      tempItemName: "",
      //是否展示项目详情弹框
      itemShow: false,
      saveLabel: {},
      //项目可研报告文件名
      reportFileName: "",
      //获取字段类型字典，用于日期类型的展示
      columnTypeMap: new Map(),
      isShowFilter: false,
      //自动表格高度

      tableHeight: "65vh",
      minDate: "",
      maxDate: "",
      pickerOptionsStart: {
        disabledDate: time => {
          let maxDate = new Date(this.maxDate);
          let minDate = Date.now() - 8.64e7;
          return time.getTime() <= minDate || time.getTime() > maxDate;
        }
      },
      pickerOptionsEnd: {
        disabledDate: time => {
          let minDate = new Date(this.minDate);
          let maxDate = Date.now() - 8.64e7;
          return time.getTime() <= maxDate || time.getTime() <= minDate;
        }
      },

      auditPlanId: "",
      //点击提交传入的参数
      saveParams: [],
      //用户所属专项类型值
      userSpecialityArr: [],
      //驳回原因
      rejectReason:""
    };
  },
  components: {
    querys,
    SelectTree,
    ItemAddCom
  },
  watch: {
    deptCode() {
      this.param.createOrg = this.deptCode;
      this.getTable(this.currentPage, this.currentNum);
    }
  },
  created() {
    //表格自动高度
    // this.tableHeight=window.innerHeight-220;
  },
  mounted() {
    this.getSpecialityIds();
    this.getTableWithTitle(1, 10);
    //增加查询工作状态，为原因赋值 add by gao.feng
    this.getWorkItemStatus()
    // this.getTreeDeptCode()
  },
  computed: {
    ...mapGetters([
      // 'token',
    ])
  },
  methods: {
    //add by gao.feng 增加查询工作状态接口
    getWorkItemStatus(){
        let _this = this
        let processInstID = _this.$route.query.processInstID;
        let formData = {}
        formData.processInstID = processInstID
        getWorkItemStatus(formData).then(res=>{
            if(res.data.successful){
                console.log(res)
                let arr = res.data.resultValue
                _this.rejectReason = arr[arr.length-1].auditDesc
            }
        })
    },
    getValue(value) {
      this.deptCode = value;
    },
    // 子组件向父组件传递参数
    changeStatus(status) {
      this.showDrawer = status;
      console.log("status" + status);
    },
    //展示项目详细信息
    showDetailDialog(row) {
      this.showData.itemId = row.itemId;
      this.showData.phash = 0;
      this.showDrawer = true;
      this.timer = new Date().getTime();
    },
    //获取单位树形结构
    getTreeDeptCode() {
      let _this = this;
      getUserDept().then(response => {
        let valueId = response.data.resultValue;
        getTreeDeptCode().then(response => {
          console.log(response);
          if (response.data.successful) {
            _this.valueId = valueId;
            _this.deptArr = response.data.resultValue;
          }
        });
      });
    },

    //保存审核意见
    saveAudtiInfo() {
      let params = {
        "user": this.$route.query.iscUserId,
        "workItemID": this.$route.query.workItemID
      }
      this.loadingBtn1 = true
      finishWorkItem(params).then(res => {
        if(res.data.status == '0'){
          let params1 = {
            processInstID:  this.$route.query.processInstID,
            itemId:  this.auditItemParam.itemIds,
            userId:  this.$route.query.iscUserId,
            isAudit: 1,
            workItemId:  this.$route.query.workItemID,
            auditDesc: this.auditItemParam.auditAdvice,
            itemPhaseDet:3
          }
          console.log(this.auditItemParam.itemPhaseDet)
          let tonext =false
          if(this.auditItemParam.itemPhaseDet == "已提交立项申请" || this.auditItemParam.itemPhaseDet =="立项申请退回") {
            params1.itemPhaseDet = 2;
            tonext =true
          }else if(this.auditItemParam.itemPhaseDet =="可研编制已退回"){
            params1.itemPhaseDet = 25;
            tonext =true
          }
          if(tonext){
            saveWorkItem(params1).then(result => {
              this.loadingBtn1 = true
              if (result.data.successful){
                this.$alert('操作成功', '提示', {
                  confirmButtonText: '确定',
                  callback: () => {
                    parent.GetChildValue("true");
                  }
                }); 
              }
              else {
                let error = result.data.resultHint;
                this.$message.error(error);
              }
            });
          }
          
        }else {
          this.$message.error(item.itemName + res.data.message);
        }
      })
    },

    changeQuery(state, height) {
      if (state) {
        this.tableHeight = height;
        this.isShowQuery = true;
        this.showQuery = true;
      } else {
        this.tableHeight = height;
        this.isShowQuery = false;
        this.showQuery = false;
      }
    },
    getSpecialityIds() {
      let _this = this;
      getSpecialityIds().then(response => {
        if (response.data.successful) {
          let str = response.data.resultValue;
          if (str != "") {
            _this.userSpecialityArr = str.split(",");
          }
          console.log(response);
        }
      });
    },
    getRowKeys(row) {
      return row.itemId; //返回唯一ID
    },
    //时间戳字符串的转化
    formartDate(param) {
      var time = new Date(param);
      var y = time.getFullYear();
      var m = time.getMonth() + 1;
      var d = time.getDate();
      // var h = time.getHours();
      // var mm = time.getMinutes();
      // var s = time.getSeconds();
      // return y+'-'+this.add0(m)+'-'+this.add0(d)+' '+this.add0(h)+':'+this.add0(mm)+':'+this.add0(s);
      return y + "-" + this.add0(m) + "-" + this.add0(d);
    },
    add0(m) {
      return m < 10 ? "0" + m : m;
    },
    tableRowClassName({ row, rowIndex }) {
      if (row.havePlan == 1) {
        return "success-row";
      } else {
        return "";
      }
    },
    switchFilter() {
      this.isShowFilter = !this.isShowFilter;
    },
    //获取字段类型字典
    getColumnTypeArr(labelList) {
      let map = new Map();
      for (let i = 0; i < labelList.length; i++) {
        let temp = labelList[i];
        map.set(temp.colDname, temp.colType);
      }
      return map;
    },

    selectItem() {
      this.currentPage = 1;
      this.getTable(1, this.currentNum);
    },
    //监听表单勾选事件
    handleSelectionChange(val) {
      let _this = this;
      _this.auditItemParam.itemIds = "";
      let itemIds = "";
      val.forEach(function(item) {
        itemIds += "," + item.itemId;
      });
      _this.auditItemParam.itemIds = itemIds.substr(1);
    },
    //查询表头以及列表数据
    getTableWithTitle(size) {
      //（page,size）
      let _this = this;
      _this.loading = true;
      // _this.param.pageIndex = page; 党志乾
      _this.param.pageSize = size;
      _this.param.itemPhaseDet = "2";
      if (this.specClass == "") {
        let path = _this.$route.path;
        _this.specClass = path.substr(path.lastIndexOf("/") + 1, path.length);
      }
      _this.param.speciality = _this.specClass;
      let formartDate = {};
      formartDate.speciality = _this.specClass;

      //modify by gao.feng
      queryItemTitle(formartDate).then(response => {
        _this.loading = false;
        if (response.data.successful) {
          //获取表头
          _this.tableLabel = response.data.resultValue;
          //从表头中抽取出字典信息列表
          _this.dictMap = _this.getDictArr(_this.tableLabel);
          //获取字段类型对应关系
          _this.columnTypeMapDict = _this.getColumnTypeArr(_this.tableLabel);
          //从路由中获取流程实例id
          let processInstID = _this.$route.query.processInstID;
          //获取列表数据
          //根据流程实例id查询项目id
          getItemInfoByProcessInstId({ processInstID: processInstID }).then(
            res => {
              if (res.data.successful) {
                debugger
                console.log(res);
                let itemInfo = res.data.resultValue;
                _this.auditItemParam.itemIds = itemInfo.itemId
                _this.auditItemParam.speciality = itemInfo.specialityT
                _this.auditItemParam.specDetailClass = itemInfo.specDetailClass
                _this.auditItemParam.specClass = itemInfo.specClass
                _this.auditItemParam.itemPhaseDet = itemInfo.itemPhaseDet;
                let arr = [];
                if (itemInfo != null) {
                  arr.push(itemInfo);
                  _this.tableDataShow = arr;
                  _this.totalNum = 1;
                }
              }
            }
          );
          // queryAll(_this.param).then(res => {
          //     if (res.data.successful) {
          //         _this.tableData = res.data.resultValue.prjStItemBaseList;
          //         _this.totalNum = res.data.resultValue.total;
          //         _this.tableDataShow = _this.getShowDateTable(
          //             _this.tableData,
          //             _this.dictMap,
          //             _this.columnTypeMapDict
          //         );
          //     } else {
          //         this.$message.error("查询列表出错");
          //     }
          // });
        } else {
          this.$message.error("查询表头信息出错");
        }
      });
    },
    //获取列表数据
    getTable(size) {
      //党志乾 （page,size）
      let _this = this;
      _this.loading = true;
      // _this.param.pageIndex = page;  党志乾
      _this.param.pageSize = size;
      _this.param.itemPhaseDet = "2";
      queryAll(_this.param).then(res => {
        _this.loading = false;
        _this.tableData = res.data.resultValue.prjStItemBaseList;
        _this.totalNum = res.data.resultValue.total;
        _this.tableDataShow = _this.getShowDateTable(
          _this.tableData,
          _this.dictMap,
          _this.columnTypeMapDict
        );
      });
    },

    //获取项目信息详情展示弹框表头
    getLabelList(labelList) {
      let _this = this;
      labelList.forEach(function(item) {
        _this.$set(item, "value", "");
      });
      let common_table_info = {}; //数组转对象，避免类型报错
      for (let i in labelList) {
        common_table_info[i] = labelList[i];
      }
      return common_table_info;
    },
    getDictArr(labelList) {
      //根据当前用户id查询用户所属的单位
      //获取数据字典
      let _this = this;
      let map = new Map();
      for (let i = 0; i < labelList.length; i++) {
        let temp = labelList[i];
        if (temp.codeList != null) {
          if (temp.colDname == "speciality") {
            //过滤当前登录人的专项
            let arr = [];
            for (let i = 0; i < temp.codeList.length; i++) {
              for (let j = 0; j < _this.userSpecialityArr.length; j++) {
                if (temp.codeList[i].codeValue == _this.userSpecialityArr[j]) {
                  arr.push(temp.codeList[i]);
                }
              }
            }
            temp.codeList = arr;
          }
          map.set(temp.colDname, temp.codeList);
        }
      }
      return map;
    },
    getShowDateTable(tableData, dictMap, columnTypeMap) {
      let _this = this;
      let arr = [];
      for (let i = 0; i < tableData.length; i++) {
        let tempObj = tableData[i];
        //拷贝对象
        let copyObj = new Object();
        //遍历获取的数据中的属性，如果属性名有和数据字典中的key相对应的，取出数据字典对应的值赋值过去
        for (let [key, value] of Object.entries(tempObj)) {
          console.log(key + ":" + value);
          if (dictMap.has(key)) {
            let dictValueList = dictMap.get(key);
            for (let j = 0; j < dictValueList.length; j++) {
              let tempObj2 = dictValueList[j];
              if (tempObj2.codeValue == value) {
                copyObj[key] = tempObj2.codeName;
              }
            }
          } else if (columnTypeMap.get(key) == "DATE") {
            copyObj[key + "Rel"] = value;
            let timeStr = _this.formartDate(value);
            copyObj[key] = timeStr;
          } else {
            copyObj[key] = value;
          }

          if (key == "deptCode") {
            copyObj["deptCode"] = value;
          }
        }
        arr.push(copyObj);
      }
      return arr;
    }
  }
};
</script>
