<template>
  <div>
    <!-- 组长汇总评审意见待办 -->
    <div class="table-item-box">
      <div :class="showDrawer?'table-wrapper-run':'table-wrapper-init'">
        <el-table
          class="project"
          :data="tableDataShow"
          v-loading="loading"
          :height="tableHeight"
          border
          :header-cell-style="{background:'#359691',color:'#fff'}"
          cell-class-name="column"
          :highlight-current-row="true"
          row-key="itemId"
          ref="itemTable"
          :tree-props="{children: 'children'}"
        >
          <el-table-column type="selection" width="45"></el-table-column>
          <el-table-column type="index" width="50" label="序号"></el-table-column>
          <el-table-column
            v-for="(item, index) in tableLabel"
            v-if="item.colDname != 'itemName' && item.showType == 1"
            :key="index"
            :prop="item.colDname"
            :label="item.dispName"
            :show-overflow-tooltip="true"
            min-width="200"
          ></el-table-column>
          <el-table-column
            align="left"
            :show-overflow-tooltip="true"
            v-else-if="item.colDname == 'itemName'"
            :label="item.dispName"
            min-width="200"
          >
            <template slot-scope="scope">
              <span v-if="scope.row.isBatchPackage ==1" >{{ scope.row.itemName }}</span>
              <span v-else class="blue" @click="showDetailDialog(scope.row)">{{ scope.row.itemName }}</span>
            </template>
          </el-table-column>
        </el-table>
        <div class="table-item-box" style="height: 50% !important;">
          <div class="table-box" :class="showDrawer?'table-wrapper-run':'table-wrapper-init'">
            <!-- 挂接正式评审文件 -->
            <div class="upload-file">
                <el-form :inline="true" label-position="right" class="demo-form-inline">
                <el-form-item label="项目可研评审文号" style="display:block;">
                  <el-input v-model="auditFileNo" placeholder="项目可研评审文号"></el-input>
                </el-form-item>
                  <el-form-item >
                      <el-upload
                      class="upload"
                      ref="uploadFileSubmit"
                      :action="GLOBALURL+'/workFlow/uploadNormalFile'"
                      :headers="{'userId':userId,'token':token}"
                      :on-success="uploadSuccess2"
                      :on-change="changeFile"
                      :on-remove="handleRemove"
                      :data="finalFileData"
                      :file-list="fileList3"
                      :multiple='false'
                      :show-file-list="true"
                      :auto-upload="false"
                      accept=".CEB,.PDF">
                      <el-button size="small" type="primary">上传</el-button>
                    </el-upload>
                  </el-form-item>

              </el-form>
            </div>
            <div slot="footer" class="dialog-footer option">
              <div class="div">
                <el-button type="primary" @click="saveAudtiInfo" :loading="loadingBtn1">确认</el-button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div :class="showDrawer?'item-add-wrapper-run':'item-wrapper-init'">
        <!-- 新增 修改 浏览 引itemaddcom -->
        <item-add-com
          v-if="showDrawer"
          :key="timer"
          :showData="showData"
          :showAddDialog="showDrawer"
          :isShowQuery="showQuery"
          :tableHeightTab="tableHeight"
          @changeStatus="changeStatus"
        ></item-add-com>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import { mapGetters } from "vuex";
import SelectTree from "@/components/treeSelect.vue";
import itemDialog from "@/views/project/item.vue";
import ItemAddCom from "@/views/project/itemAddCom";

import {
  auditBySpeciality,
  getTreeDeptCode,
  getUserDept,
  queryItemTitle,
  queryAll,
  getProcessStatus,
  updateProcessFinished,
  getItemInfoByProcessInstId,
  queryTitle,
  getItemInfoByProcessInstIdWithBatchGroup,
  checkPlan,
  createProcess,
  saveWorkItem,
  isAutoSelectExpert,
  expertAudit,
  findExpertsAdvice,
  doPostApplyItemCode,
  updateItemCode,
  getChildrenItem,
  getChildIdList
} from "@/api/data";
import { getBM } from "@/api/getBM";
import { queryBySpeciality, getSpecialityIds } from "@/api/data";
import querys from "@/components/querys";

import { finishWorkItem ,noticeAdd} from "@/api/workflow";

export default {
  name: "questions",
  data() {
    return {
        isAuto:false,
      itemId:"",
      showQuery:false,
      tableHeightTab:"",
      timer: "",
      showAddDialog: false, //弹窗是否展示-新增 修改 浏览
      showData: {
        itemId: null, //项目id
        specClass: "", //专项类型
        specClassName: "", //专项类型名称
        oldPath: "", //原来路由
        tableLabelArr: "", //表头
        phash: "1", //阶段1=立项 2=可研 3=储备 0=浏览
        parentItemId: null //父项id-新增下级时用
      },
      showDrawer: false,
      deptCode: "",
      isClearable: true, // 可清空（可选）
      valueId: "", // 初始ID（可选）
      props: {
        // 配置项（必选）
        value: "id",
        label: "name",
        children: "children",
        code: "deptCode"
        // disabled:true
      },
      deptArr: [],
      //加载保存审核意见
      loadingBtn1: false,
      loadingBtn2: false,
      isShowQuery: false,
      dialogVisibleL: false,
      dialogLoading: false,
      loading: false,
      lableList: [{ value: "" }],
      totalNum: 0,
      currentPage: 1,
      currentNum: 10,
      year: "",
      dialogFormVisible: false,
      form: {},
      options: [],
      value: "",
      input: "",
      // 子组件的表格数据
      tableData: [],
      // 子组件的表头数据
      tableLabel: [],
      //查询条件
      param: {
        createOrg: "", //单位编码
        itemName: "", //项目名称
        itemType: "", //项目类型
        pageSize: 10,
        speciality: "", //专业类型
        voltLevel: "", //电压等级
        ghYear: "", //规划年份
        itemSource: ""
      },
      dictMap: new Map(),
      tableDataShow: [],
      //项目id
      tempItemId: "",
      tempIsBatch:false,
      //项目名称
      tempItemName: "",
      //是否展示项目详情弹框
      itemShow: false,
      saveLabel: {},
      //获取字段类型字典，用于日期类型的展示
      columnTypeMap: new Map(),
      //自动表格高度

      tableHeight: "65vh",
      //用户所属专项类型值
      userSpecialityArr: [],
      token:"",
      userId:"",
      itemSpeciality:"",
      fileType:"",
      fileTypeList:[],
       //上传正式评审文件
      finalFileData:{},
      auditFileNo:"",
      fileList3: []
    };
  },
  components: {
    querys,
    SelectTree,
    ItemAddCom
  },
  watch: {
    deptCode() {
      this.param.createOrg = this.deptCode;
      this.getTable(this.currentPage, this.currentNum);
    }
  },
  created() {
    //表格自动高度
    this.tableHeight=window.innerHeight-220;
  },


  mounted() {
    // this.getSpecialityIds();
    this.getTableWithTitle(1, 10);
    this.userId = this.$route.query.iscUserId;
    this.token = sessionStorage.getItem('token');
  },
  methods: {
    cancel() {
        top.opener=null;
        top.close();     //注意,   不可跨域
        // parent.window.close();
        // var index = parent.layer.getFrameIndex(window.name);
        // parent.layer.close(index);
    },
    getValue(value) {
      this.deptCode = value;
    },
    // 子组件向父组件传递参数
    changeStatus(status) {
      this.showDrawer = status;
      console.log("status" + status);
    },
    //展示项目详细信息
    showDetailDialog(row) {
      this.showData.itemId = row.itemId;
      this.showData.phash = 0;
      this.showDrawer = true;
      this.timer = new Date().getTime();
    },
    //获取单位树形结构
    getTreeDeptCode() {
      let _this = this;
      getUserDept().then(response => {
        let valueId = response.data.resultValue;
        getTreeDeptCode().then(response => {
          console.log(response);
          if (response.data.successful) {
            _this.valueId = valueId;
            _this.deptArr = response.data.resultValue;
          }
        });
      });
    },

    changeFile(file){
      this.fileType = file.name.split(".")[1]
      this.fileTypeList.push(this.fileType)
      console.log(this.fileTypeList)
    },
    handleRemove(file, fileList){
      this.fileTypeList = []
      for(let i=0;i<fileList.length; i++){
        let fileType = ""
        fileType = fileList[i].name.split(".")[1]
        this.fileTypeList.push(fileType)
      }
      
      console.log(this.fileTypeList)
    },

    //保存审核意见
    saveAudtiInfo() {
        let params = {
            "user": this.$route.query.iscUserId,
            "workItemID": this.$route.query.workItemID
        }
        
        if(!this.auditFileNo){
          this.$message({
            showClose: true,
            message: '请输入项目可研评审文号',
            type: 'warning'
          });
          return false
        }
        if(this.fileTypeList =="" || this.fileTypeList == undefined){
          this.$message({
            showClose: true,
            message: '请上传格式为ceb或者pdf的文件',
            type: 'warning'
          });
          return false
        }
        for(let i=0; i<this.fileTypeList.length; i++){
          if(this.fileTypeList[i] != "ceb" && this.fileTypeList[i] != "pdf" && this.fileTypeList[i] != "CEB" && this.fileTypeList[i] != "PDF"){
            this.$message({
              showClose: true,
              message: '只能上传格式为ceb或者pdf的文件',
              type: 'warning'
            });
            return false
          }
        }
        this.loadingBtn1 = true
        finishWorkItem(params).then(res => {
          this.loadingBtn1 = false
            if(res.data.status == '0'){
              this.finalFileData.auditFileNo = this.auditFileNo
              this.$refs.uploadFileSubmit.submit()    
            }else {
              this.$message.error(res.data.message);
            }

        })
    },

    // 上传成功之后调用
    uploadSuccess2(response){
       let _this = this
       _this.showCommit = false
       _this.fileLoading = false
        if(response.successful){
          let resVal = response.resultValue
            _this.$message.success("操作成功")
              //上传正式评审文件之后调用通知流程  add by gao.feng
           if(resVal.length >0 ){
              
              resVal.forEach(function(item){  
                let noticeArr = []
                let noticeData = {}
                noticeData.content = item.itemName + '项目，已经上传正式评审文件，评审文号 ' + item.auditFileNo
                noticeData.noticeUser = item.createor;
                noticeArr.push(noticeData)//流程通知所需参数
                noticeAdd(noticeArr).then(response =>{
                    console.log(response)
                })
              })
           }
            

            //上传正式评审文件之后调用自动编码
            //获取调用国电通编码器所使用的参数 add by gao.feng
            let itemId = _this.finalFileData.itemId
            let thisParam ={
              'itemId':itemId,
              'isBatchPackage':_this.tempIsBatch
            }
            getChildIdList(thisParam).then(res => {
              if(res.data.successful){
                let childTableList = res.data.resultValue
                for(let i=0;i<childTableList.length;i++){
                    let tempData = childTableList[i]
                    console.log("调用国电通编码器")
                      doPostApplyItemCode({"itemId":tempData.itemId}).then(response=>{
                      if(response.data.successful){
                        let param = response.data.resultValue;
                        console.log(param)
                        getBM(param).then(res=>{
                            console.log(res)
                            let status = res.data.status
                            if(status.indexOf("200")!=-1){//调用失败
                                // _this.$message.error("调用编码器失败,权限校验失败")
                                console.log("调用编码器失败,权限校验失败")
                            }else if(status.indexOf("100")!=-1){
                                let applyResultList = res.data.applyResult
                                let result = applyResultList[0]
                                if(result.status.indexOf("100")!=-1){//调用成功
                                      let baseCode = result.baseCode
                                      //更新项目编码
                                      updateItemCode({"itemId":tempData.itemId,"code":baseCode}).then(res1=>{
                                            if(res1.data.successful){
                                                // _this.$message.success("编码成功")
                                                _this.$alert('编码成功', '提示', {
                                                  confirmButtonText: '确定',
                                                    callback: () => {
                                                      parent.GetChildValue("true");
                                                    }
                                                });
                                            }else{
                                                _this.$message.error('编码失败')
                                            }
                                      })
                                  }else if(result.status.indexOf("200")!=-1){
                                      let errormsg = ''
                                      for(let i in result.applyResult){
                                          errormsg = result.applyResult[i]
                                      }
                                      // _this.$message.error(errormsg)
                                      console.log(errormsg)
                                  }
                            }
                          })
                      }
                    })
                }
              }
            })
        }else{
            _this.$message.error("上传失败")
        }
    },

    // getSpecialityIds() {
    //   let _this = this;
    //   getSpecialityIds().then(response => {
    //     if (response.data.successful) {
    //       let str = response.data.resultValue;
    //       if (str != "") {
    //         _this.userSpecialityArr = str.split(",");
    //       }
    //       console.log(response);
    //     }
    //   });
    // },
    //时间戳字符串的转化
    formartDate(param) {
      var time = new Date(param);
      var y = time.getFullYear();
      var m = time.getMonth() + 1;
      var d = time.getDate();
      // var h = time.getHours();
      // var mm = time.getMinutes();
      // var s = time.getSeconds();
      // return y+'-'+this.add0(m)+'-'+this.add0(d)+' '+this.add0(h)+':'+this.add0(mm)+':'+this.add0(s);
      return y + "-" + this.add0(m) + "-" + this.add0(d);
    },
    add0(m) {
      return m < 10 ? "0" + m : m;
    },
    //获取字段类型字典
    getColumnTypeArr(labelList) {
      let map = new Map();
      for (let i = 0; i < labelList.length; i++) {
        let temp = labelList[i];
        map.set(temp.colDname, temp.colType);
      }
      return map;
    },

    //查询表头以及列表数据
    getTableWithTitle(size) {
      //（page,size）
      let _this = this;
      _this.loading = true;
      queryTitle().then(response => {
        _this.loading = false;
        if (response.data.successful) {
          //获取表头
          _this.tableLabel = response.data.resultValue
          //从表头中抽取出字典信息列表
          _this.dictMap = _this.getDictArr(_this.tableLabel);
          //获取字段类型对应关系
          _this.columnTypeMapDict = _this.getColumnTypeArr(_this.tableLabel);
          //从路由中获取流程实例id
          let processInstID = _this.$route.query.processInstID;
          //获取列表数据
          getItemInfoByProcessInstIdWithBatchGroup({ processInstID: processInstID }).then(
            res => {
              if (res.data.successful) {
                console.log(res);
                let itemInfo = res.data.resultValue;
                // 保存或者退回需要用到的参数
                _this.tempItemId = itemInfo.itemId;
                let _itemPhaseDet = ""
                if(itemInfo.isBatchPackage == 1){
                  _this.tempIsBatch =true
                  _itemPhaseDet = itemInfo.children[0].itemPhaseDet
                }else{
                  _this.tempIsBatch =false
                  _itemPhaseDet = itemInfo.itemPhaseDet
                }
                 let arr =[]
                if (itemInfo != null) {
                  arr.push(itemInfo);
                }
                _this.tableDataShow = _this.getShowDateTable(arr, _this.dictMap, _this.columnTypeMapDict);
                _this.auditFileNo = ""
                _this.fileList3 = []
                _this.fileTypeList =[]
                _this.fileType = ""
                _this.finalFileData = {"itemId":itemInfo.itemId,"itemPhaseDet":_itemPhaseDet,"auditFileNo":_this.auditFileNo,
                                      'isBatchPackage':_this.tempIsBatch}
              }
            }
          );
        } else {
          this.$message.error("查询表头信息出错");
        }
      });
    },
    getDictArr(labelList) {
      //根据当前用户id查询用户所属的单位
      //获取数据字典
      let _this = this;
      let map = new Map();
      for (let i = 0; i < labelList.length; i++) {
        let temp = labelList[i];
        if (temp.codeList != null) {
          map.set(temp.colDname, temp.codeList);
        }
      }
      return map;
    },
    //获取展示数据
    getShowDateTable(tableData, dictMap, columnDictMap) {
        let _this = this
        let arr = [];
        for (let i = 0; i < tableData.length; i++) {
            let tempObj = tableData[i];
            //拷贝对象
            let copyObj = new Object();
            //遍历获取的数据中的属性，如果属性名有和数据字典中的key相对应的，取出数据字典对应的值赋值过去
            for (let [key, value] of Object.entries(tempObj)) {
                //console.log(key + ':' + value);
                let lowerKey = key.toLowerCase()
                if (dictMap.has(key)) {
                    let dictValueList = dictMap.get(key);
                    for (let j = 0; j < dictValueList.length; j++) {
                        let tempObj2 = dictValueList[j];
                        if (tempObj2.codeValue == value) {
                            copyObj[key] = tempObj2.codeName;
                        }
                    }
                } else if (dictMap.has(lowerKey)) {
                    let dictValueList = dictMap.get(key.toLowerCase);
                    for (let j = 0; j < dictValueList.length; j++) {
                        let tempObj2 = dictValueList[j];
                        if (tempObj2.codeValue == value) {
                            copyObj[key] = tempObj2.codeName;
                        }
                    }
                } else if (columnDictMap.get(key) == "date" || columnDictMap.get(key) == "month"
                    || columnDictMap.get(key) == "year") {
                    if(value!=null&&value!=''){
                        copyObj[key] = _this.formartDate(value, columnDictMap.get(key));
                    }  
                } else {
                    copyObj[key] = value;
                }

                if (key == "itemClass") {
                    copyObj["itemClassT"] = value
                }

                if (key == "conDept") {
                    copyObj["conDept"] = value
                }
                if (key == "itemLevel") {
                    copyObj["itemLevelT"] = value
                }

                //add by liangwu
                if (key == "specDetailClass") {
                    copyObj["specDetailClassT"] = value
                }

                if (key == "specClass") {
                    copyObj["specClassT"] = value
                }
                //add end
            }
            if (typeof (tempObj.children) != 'undefined' && tempObj.children != null && tempObj.children.length > 0) {
                let tempArr = _this.getShowDateTable(tempObj.children, dictMap, columnDictMap)
                copyObj.children = tempArr
            }
            arr.push(copyObj);
        }
        return arr;
    },
  }
};
</script>
