// import 'babel-polyfill'
import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import ElementUI from 'element-ui';
// import '@/utils/global'//全局
// import './public/url.js'
// import '@/assets/url'
import axiosUrl from 'axios'
import 'element-ui/lib/theme-chalk/index.css';
import './style/index.css' // global css
import "./style/iconfont.css"
import '@/permission'
// process.env.VUE_APP_BASE_URL

function getServerConfig () {
	return new Promise ((resolve, reject) => {
		axiosUrl({
        method: 'get',
        // baseURL:'dist/',
        baseURL:'',
        url: 'url.json',//接口地址
    }).then((result) => {
		  // let config = result.data;
		  // let ajaxUrl = process.env.NODE_ENV == 'production' ? config.production:config.develop;
		  // Vue.prototype.$ajaxUrl=ajaxUrl; //设置全局
		  // store.commit('app/setworkflowurl',result.data.workflowurl);//存储到vuex中
		  store.dispatch('app/setworkflowurl', result.data.workflowurl)
		  store.dispatch('app/setBmUrl', result.data.getBMUrl)
		  Vue.prototype.workflowurl = result.data.workflowurl
		  console.log('测试配置',Vue.prototype.workflowurl)
		  resolve();
		}).catch((error) => {
		  console.log(error)
		  reject()
		})
	})
}

async function init() {
	await getServerConfig();
	Vue.prototype.GLOBALURL = process.env.VUE_APP_BASE_URL
	Vue.use(ElementUI);
	Vue.config.productionTip = false
	new Vue({
	  router,
	  store,
	  render: h => h(App)
	}).$mount('#app')
}
init();



