// workflow
export function createProcessInstance(params) {//创建工作流
    let params1 = {
        "user": params.user, //"ff8080817147f2100172c6f4f57e2437",
        "processDefName": params.processDefName, // "project_approval",
        "processInstName": params.processInstName, // "project_approval_1",
        "processInstDesc": params.processInstDesc, //"县公司测试",
        "isStart": "true"
    }
    return wfaxios({
        method: 'post',
        url: store.state.app.workflowurl + 'bpm/BPMPIManager/createProcessInstance',//接口地址
        data: params1
    }).then(res1 => {
        let param2 = {
            "user": params.user, //"ff8080817147f2100172c6f4f57e2437",
            "personID":  params.user, //ff8080817147f2100172c6f4f57e2437",
            "permission": "ALL",
            "scope": "ALL",
            "workItem": {"_entity": "com.eos.workflow.data.WFWorkItem", "/_and[1]/_expr[1]/processInstID": res1.data.data},
            "pageCond": {"begin": "0", "length": "10", "isCount": "true"}
        }
        return wfaxios({
            method: 'post',
            url: store.state.app.workflowurl + 'bpm/BPMWorklistQueryManager/queryPersonWorkItems4SDOByWorkItem',//接口地址
            data: param2
        }).then(res2 => {
                let param3 = {
                    "user": params.user,
                    "orgId":params.orgIds,
                    "orgRoleIds": params.roleIds,
                    "processDefID":res2.data.data[0].processDefID,
                    "activityDefID": "audit",
                    "workItemID": res2.data.data[0].workItemID
                }
                return wfaxios({
                    method: 'post',
                    url: store.state.app.workflowurl + 'bpm/FlowCenter/reassignAndfinishWorkItem',//接口地址
                    data: param3
                }).then(res =>{
                    let result ={
                        "processInstID": res1.data.data,
                        "workItemID":res2.data.data[0].workItemID
                    }
                    return new Promise(

                        function (resolve, reject) {
                            resolve(result) // 数据处理完成
                        }
                    )
                });
            }
        );
    });
}
//add by liangwu
export function createAndReassignAndfinishWorkItemWithOption (params) {
    let param = {
        "user":params.user,
        "processDefName":params.processDefName,
        "processInstName":params.processInstName,
        "processInstDesc":params.processInstDesc,
        "createOption":{"relativeData":{"businessID":""}},
        "orgId":params.orgIds,
        "orgRoleIds":params.roleIds,
        "activityDefID":params.activityDefID,
        "sumbitOption":{"checkPermission":"false"}
    }

    return wfaxios({
        method: 'post',
        url:  store.state.app.workflowurl + 'bpm/FlowCenter/createAndReassignAndfinishWorkItemWithOption',//接口地址
        data:param
    });
}

