<template>
  <div class="container">
    
    <div class="main clearfix" :style="{'margin-left':$store.state.app.isCollapse ? '84px':'240px'}">
      
      <div class="main_container">
        <!-- <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item v-for="mat in $route.matched" ><span class="color_tip">{{mat.name}}</span></el-breadcrumb-item>
        </el-breadcrumb> -->
        <querys @changeQuery="changeQuery" :showSwitch = "true">
          <div slot="queryTabs">

            <div class="query_tab">
              <div class="query_label">项目名称</div>
              <div class="query_select">
                <el-input v-model="param.itemName" clearable @change="selectItem" size="small" placeholder="请输入内容"></el-input>
              </div>
            </div>

            <div class="query_tab">
              <div class="query_label">所属单位</div>
              <div class="query_select">
                  <!-- <el-select v-model="param.deptCode" @change="selectItem" placeholder="请选择" size="small" clearable>
                    <el-option v-for="item in deptArr" :key="item.deptCode" :label="item.deptName" :value="item.deptCode"></el-option>
                  </el-select> -->
                  <!-- <SelectTree 
                    :props="props"
                    :options="deptArr"
                    :value="valueId"
                    :clearable="isClearable"
                    :accordion="isAccordion"
                    @getValue="getValue($event)"/> -->
                    <select-tree @getValue="getValue"></select-tree>
              </div>
            </div>

            <div class="query_tab">
              <div class="query_label"><!-- 评审计划<br> -->开始时间</div>
              <div class="query_select">
                
                <el-date-picker 
                type="date" 
                size="small"
                 @change="selectItem"  
                 value-format="yyyy-MM-dd" 
                 class="diy_date" 
                 placeholder="选择日期" 
                 :picker-options = 'startTime'
                 v-model="param.planStartDate" >
                 </el-date-picker>
              </div>
            </div>

            <div class="query_tab">
              <div class="query_label"><!-- 评审计划<br> -->结束时间</div>
              <div class="query_select">
                
                <el-date-picker 
                type="date" 
                size="small" 
                @change="selectItem" 
                value-format="yyyy-MM-dd" 
                class="diy_date" 
                placeholder="选择日期" 
                :picker-options = 'endTime'
                v-model="param.planEndDate" ></el-date-picker>
              </div>
            </div>


            <div class="query_tab">
						<div class="query_label">项目层级</div>
						<div class="query_select">
							<el-select v-model="param.itemLevel" @change="selectItem" v-if="selectParamDict != undefined"
										placeholder="请选择" size="small" clearable>
								<el-option v-for="item in selectParamDict.get('itemLevel')" :key="item.codeValue"
											:label="item.codeName" :value="item.codeValue"></el-option>
							</el-select>
						</div>
					</div>

				    <div class="query_tab">
				      <div class="query_label">项目来源</div>
				      <div class="query_select">
						<el-select v-model="param.itemSource" v-if="selectParamDict != undefined" @change="selectItem" placeholder="请选择" size="small" clearable>
							<el-option v-for="item in selectParamDict.get('itemSource')" :key="item.codeValue" :label="item.codeName" :value="item.codeValue"></el-option>
						</el-select>
				      </div>
				    </div>

            <div class="query_tab">
				      <div class="query_label">电压等级</div>
				      <div class="query_select">
						<el-select v-model="param.voltLevel" v-if="selectParamDict != undefined" @change="selectItem" placeholder="请选择" size="small" clearable>
							<el-option v-for="item in selectParamDict.get('voltLevel')" :key="item.codeValue" :label="item.codeName" :value="item.codeValue"></el-option>
						</el-select>
				      </div>
				    </div>

            
          </div>
          <div slot="queryBtns">
            <el-button type="primary" size="small" @click="collect">汇总</el-button>
            

          </div>
        </querys>
       
        <div class="table-item-box">
					<div :class="showDrawer?'table-wrapper-run':'table-wrapper-init'">
            <el-table 
              :data="tableDataShow"
              :height="tableHeight"
              border
              :header-cell-style="{background:'#359691',color:'#fff'}"
              @selection-change="handleSelectionChange"
              row-class-name="row"
              cell-class-name="column"
              :highlight-current-row="true"
              v-loading="tableDataLoding"
              lazy
              :load="tableLoad"
              row-key="itemId"
              :tree-props="{children: 'children', hasChildren: 'hasChildren'}"
              >
              <el-table-column
                      type="selection"
                      width="45"
                      >
              </el-table-column>
              <el-table-column
                      type="index"
                      width="50"
                      label="序号">
              </el-table-column>
              <el-table-column
                      v-for="(item, index) in tableLabel"
                      v-if ="item.colDname != 'itemName'&&item.showType==1"
                      :key="index"
                      :prop="item.colDname"
                      :label="item.dispName"
                      min-width='220'
                      :show-overflow-tooltip="true">
              </el-table-column>
              <el-table-column v-else-if="item.colDname == 'itemName'"
                              :label="item.dispName"  min-width='200'
                              :show-overflow-tooltip="true">
                <template slot-scope="scope" >
                  <span class="blue" @click="showDetailDialog(scope.row)">{{scope.row.itemName}}</span>
                </template>
              </el-table-column>
            </el-table>
            
            <div class="page">
              
              <el-pagination
                background
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-size="20"
                :page-sizes="[10,20,30,50]"
                layout="sizes, total, prev, pager, next, jumper"
                :total="totalNum">
              </el-pagination>
            </div>
          </div>
          <div :class="showDrawer?'item-add-wrapper-run':'item-wrapper-init'">
						<!-- 新增 修改 浏览 引itemaddcom -->
						<item-add-com 
						v-if="showDrawer" 
						:key="timer" 
						:showData="showData" 
						:showAddDialog="showDrawer" 
            :isShowQuery="showQuery"
            :tableHeightTab="tableHeight"
						@changeStatus="changeStatus">
						</item-add-com>
					</div>
        </div>

        

      </div>
    </div>
    <!-- <right></right> -->
    <!--项目基本信息弹框-->
   <el-dialog :visible.sync="itemShow" :close-on-click-modal = "false" :modal-append-to-body='false'>
          <item-dialog :itemId="tempItemId"></item-dialog>
    </el-dialog>
  </div>
</template>

<script>
// @ is an alias to /src
import { mapGetters } from 'vuex'
import itemDialog from '@/views/project/item.vue'
import SelectTree from "@/components/treeSelect.vue";
import right from '@/views/right.vue'
import ItemAddCom from "@/views/project/itemAddCom"
import {getPlan,getItemLabelInfo,collectionInfo,getTreeDeptCode,getUserDept,queryFilterParam,getChildrenItem} from '@/api/data'
import querys from '@/components/querys'


export default {
  name: 'questions',
  data() {
  	return {
      isShowQuery:false,
      showQuery:false,
      tableHeightTab:"",
      timer:"",
			showAddDialog:false,   //弹窗是否展示-新增 修改 浏览
			showData:{
				itemId:null, //项目id
				specClass: "",//专项类型
				specClassName:"",//专项类型名称
				oldPath:"",//原来路由
				tableLabelArr:"",//表头
				phash:"1",//阶段1=立项 2=可研 3=储备 0=浏览
				parentItemId:null //父项id-新增下级时用
			},
			showDrawer:false,
      deptCode:"",
			isClearable:true,      // 可清空（可选）
			isAccordion:true,      // 可收起（可选）
			valueId:"",            // 初始ID（可选）
			props:{                // 配置项（必选）
				value: 'id',
				label: 'name',
				children: 'children',
				code: 'deptCode'
				// disabled:true
			},
      deptArr:[],
      isShowQuery:false,
      isShowFilter:false,
      dialogLoading:false,
      loading:false,
      delData:{
        "ids": [],
      },
      lableList:[{value: ''}],
      totalNum:0,
      currentPage:1,
      currentNum:20,
      year: '',
      dialogFormVisible:false,
      form: {
        name:''
      },
      options: [],
      value:'',
      formLabelWidth: '120px',
      input: '',
      tableTest:[
        
      ],
  		// 子组件的表格数据(接口返回)
      tableData: [],
      //表格展示数据
      tableDataShow:[],
  		// 子组件的表头数据
      tableLabel: {},
      //表格字典数据
      dictMap: new Map,
      //查询条件
      selectParamDict: new Map(),
      //查询条件
      param:{
        deptCode: '',
				index: 1,
				itemName: '',
				itemType: '',
				pageSize: 10,
				speciality: '',
        voltLevel: '',
        auditPlanStatus:'',
        planStartDate:'',
        planEndDate:''
      },
      //是否展示项目详情弹框
      itemShow: false,
      //项目表头信息
      itemLabel:{},
      //项目信息字典
      itemMap: new Map,
      //项目可研报告
      reportFileName:"",
      //
      selectPlanId:[],
      //获取字段类型字典，用于日期类型的展示
      columnTypeMap: new Map,
      tempItemId:"",
      tableDataLoding: false,
      tableHeight:'',
      /* 结束时间不能大于开始时间 */
      endTime: {
          disabledDate: this.disabledEnd
      },
			startTime: {
				disabledDate: this.disabledStart
			},
  	}
  },
  components: {
     "item-dialog":itemDialog,
     'querys':querys,
     SelectTree,
     ItemAddCom
  },
  watch:{
		deptCode(){
			this.param.deptCode = this.deptCode
			this.getTable(this.currentPage,this.currentNum)
		}
	},
  created(){
		//表格自动高度
    this.tableHeight=window.innerHeight-220;
		
  },
  computed: {
		...mapGetters([
			// 'token',
		])
  },
  mounted(){
    this.getItemLabelSearch()
    this.getTable(1,20)
    //获取项目表头信息以及字典
    this.getItemLabel()

    // this.getTreeDeptCode()
  },
  methods: {
    // 结束时间不能早于开始时间
		disabledEnd(time) {
			if(this.param.planStartDate != ""&& this.param.planStartDate != undefined && this.param.planStartDate != null){
				return time.getTime() < new Date(this.param.planStartDate).getTime()
			}
		},
		disabledStart(time) {
			if(this.param.planEndDate != "" && this.param.planEndDate != undefined && this.param.planEndDate != null){
				return time.getTime() > new Date(this.param.planEndDate).getTime()
			}
    },
    // 懒加载table树形子数据
    tableLoad(row, treeNode, resolve) {
      let params = {
        'parentItemId':row.itemId
      }
      getChildrenItem(params).then(res => {
        if(res.data.successful){
          let childData =  this.getShowDateTable(res.data.resultValue, this.dictMap, this.columnTypeMap);
					resolve(childData)
        }
      })
    },
    getValue(value){
			this.deptCode = value
    },
    // 子组件向父组件传递参数
		changeStatus(status) {
			this.showDrawer = status
			console.log('status'+status)
		},
		//展示项目详细信息
		showDetailDialog(row) {
			this.showData.itemId = row.itemId
			this.showData.phash = 0
			this.showDrawer = true;
			this.timer = new Date().getTime()
    },
    //获取查询条件下拉框
		getItemLabelSearch() {
			let _this = this
			queryFilterParam().then(response => {
				console.log('response', response)//后端返回数据
				if (response.data.successful) {
					let labelList = response.data.resultValue.tableLable
					_this.selectParamDict.set('voltLevel', response.data.resultValue.voltCodeList)
					_this.selectParamDict.set('itemLevel', response.data.resultValue.itemLevelList)
					_this.selectParamDict.set('itemSource', response.data.resultValue.itemSourceList)
          _this.$forceUpdate()
				}
			})
		},
		//获取单位树形结构
		getTreeDeptCode(){
			let _this = this
			getUserDept().then(response=>{
				let valueId = response.data.resultValue
				getTreeDeptCode().then(response=>{
					console.log(response)
					if(response.data.successful){
						_this.valueId = valueId
						_this.deptArr = response.data.resultValue
					}
				})
			})
		},
    changeQuery(state,height){
      if(state){
        this.tableHeight = height
        this.isShowQuery = true
        this.showQuery =true
      }else{
        this.tableHeight = height
        this.isShowQuery = false
        this.showQuery = false
      }
    },
    selectItem() {
      this.currentPage = 1;
			this.getTable(1, this.currentNum);
		},
    //重置查询---没用到的方法
		resetItem() {
			let _this = this;
			_this.param = {
				deptCode: '',
				index: 1,
				itemName: '',
				itemType: '',
				pageSize: 20,
				speciality: '',
				voltLevel: '',
				// itemPhase: 1,
				feaStatus: 0,
        ghYear: '',
        planStartDate:'',
        planEndDate:''
        
			};
			this.getTable(1, 20);
		},
    switchFilter(){
      this.isShowFilter = !this.isShowFilter
    },
     //时间戳字符串的转化
    formartDate(param) {
      var time = new Date(param);
      var y = time.getFullYear();
      var m = time.getMonth()+1;
      var d = time.getDate();
      // var h = time.getHours();
      // var mm = time.getMinutes();
      // var s = time.getSeconds();
      // return y+'-'+this.add0(m)+'-'+this.add0(d)+' '+this.add0(h)+':'+this.add0(mm)+':'+this.add0(s);
      return y+'-'+this.add0(m)+'-'+this.add0(d);
    },
    add0(m){
      return m<10?'0'+m:m;
    },
    //汇总
    collect(){
        let _this = this
        _this.loading = true
        console.log(_this.selectPlanId.ids)
        if(_this.selectPlanId.ids == undefined ||_this.selectPlanId.ids==""){
                  //汇总失败
              _this.$message({
                showClose: true,
                message: '请选择要汇总的项目',
                type: 'warning'
              });

              return false
        }
       this.$confirm('是否汇总所选项目评审计划?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
			  }).then(() => {
              collectionInfo(_this.selectPlanId).then(res =>{
              _this.loading = false
              if(res.data.successful){
                    _this.$message({
                        showClose: true,
                        message: '汇总成功',
                        type: 'success'
                      });
                    this.getTable(1,this.currentNum)
                }else{
                    //汇总失败
                      _this.$message({
                        showClose: true,
                        message: '汇总信息失败',
                        type: 'error'
                      });
                  }
                })

        }).catch(() => {

        });



        
    },
    //监听表单选中事件
    handleSelectionChange(val){
      let _this = this
      _this.selectPlanId = {
        "cTotalTime":"",
        "ids": [],
      }
      val.forEach(function(item){
        _this.selectPlanId.ids.push(item.auditPlanId)
      })
    },
    //获取项目表头信息，获取项目编码字典
    getItemLabel(){
      let _this = this
      getItemLabelInfo().then(response =>{
          console.log('response',response)//后端返回数据
          if(response.data.successful){
            let labelList = response.data.resultValue.tableLable
            //获取字典
            _this.itemMap = _this.getDictArr(labelList);
            //获取插入或修改时的表头
            _this.itemLabel = _this.getLabelList(labelList)
          }
      })
    },
    //获取字典
    getDictArr(labelList){ 
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        if(temp.codeList!=null){
          map.set(temp.colDname,temp.codeList);
        }
      }
      return map
    },
     //获取字段类型字典
    getColumnTypeArr(labelList){
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        map.set(temp.colDname,temp.colType);
      }
      return map
    },
    //获取插入和修改的表头
    getLabelList(labelList){ 
      let _this =this
      labelList.forEach(function (item) {
        _this.$set(item,'value','');
      })
      let common_table_info = {};//数组转对象，避免类型报错
      for(let i in labelList){
        common_table_info[i] = labelList[i];
      }
      return common_table_info
    },
    //获取当前每页条数
    handleSizeChange(val) {
      this.currentNum = val;
      this.getTable(this.currentPage,val)
    },
    //分页查询
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage = val;
      this.getTable(val,this.currentNum)
    },
    //查询表格信息
    getTable(page,size){
      let _this = this
      _this.tableDataLoding = true
      let filter = 'cTotalTimeStr=0&itemPhaseDet=5&isPassed=0'
      //拼接查询条件
      for(let [key, value] of Object.entries(_this.param)) {
          if(key=="pageIndex"||key=="pageSize"||value==''||value==null){
              continue
          }
          console.log(key + ':' + value);
          filter+='&'+key+'='+value
      }
      let searchParam = {}
      searchParam.pageIndex = page
      searchParam.pageSize = size
      searchParam.filter = filter      
      
      getPlan(searchParam).then(response => {//{}写需要的参数
          _this.tableDataLoding = false
          console.log('response',response)//后端返回数据
          if(response.data.successful){
              _this.totalNum = response.data.resultValue.totalNum
              let labelArr = response.data.resultValue.tableLable
              _this.tableLabel = _this.getLabelList(labelArr)
              _this.dictMap = _this.getDictArr(labelArr)
              _this.columnTypeMap = _this.getColumnTypeArr(labelArr)
              _this.tableData = response.data.resultValue.tableData
              _this.tableDataShow = _this.getShowDateTable(_this.tableData,_this.dictMap,_this.columnTypeMap)
          }
          _this.loading = false
          
        }).catch(error => {
          console.log(error)//error
        })
        
    },
    //获取展示数据
    getShowDateTable(tableData,dictMap,columnTypeMap){
      let _this = this
      let arr = [];
      for(let i=0;i<tableData.length;i++){
        let tempObj = tableData[i];
        //拷贝对象
        let copyObj = new Object();
        //遍历获取的数据中的属性，如果属性名有和数据字典中的key相对应的，取出数据字典对应的值赋值过去
        for (let [key, value] of Object.entries(tempObj)) {
          console.log(key + ':' + value);
          if(dictMap.has(key)){
            let dictValueList = dictMap.get(key);
            for(let j=0;j<dictValueList.length;j++){
              let tempObj2 = dictValueList[j];
              if(tempObj2.codeValue==value){
                copyObj[key] = tempObj2.codeName;
              }
            }
          }else if(columnTypeMap.get(key)=="DATE"){
               copyObj[key+"Rel"] = value
               let timeStr = _this.formartDate(value)
               copyObj[key] = timeStr;
          }else{
            copyObj[key] = value;
          }
        }
        arr.push(copyObj);
      }
      return arr;
    },
  }
}
</script>
