   padding-right:10px;
  }
  .select_table{
    border-bottom:solid 1px #d4d4d4;
    margin-bottom:10px;
    padding-bottom:10px;
    margin-top:10px
  }
  
  .color{color:#5892e5;cursor: pointer;}

  
 
</style>
