<script>
// @ is an alias to /src
import { mapGetters } from 'vuex'
import itemDialog from '@/views/project/item.vue';
import SelectTree from "@/components/treeSelect.vue";
import ItemAddCom from "./itemAddCom"
import { prjCmItem, 
getSaveTable, 
savePrjStDmd, 
getSubmitPlanLabel, 
submitPlane,
getByAuditPlanId,
changePlaneStatus,
getSpecialityIds,
getTreeDeptCode,
getUserDept,
queryFilterParam,
getChildrenItem } from '@/api/data';
import querys from '@/components/querys'

export default {
	name: 'Compile',
	data() {
		return {
			editLoading:false,
			statusLoading:false,
			submitLoading:false,
			isEdit:false,
			editData:{
				itemId:[]
			},
			timer:"",
			showAddDialog:false,   //弹窗是否展示-新增 修改 浏览
			showData:{
				itemId:null, //项目id
				specClass: "",//专项类型
				specClassName:"",//专项类型名称
				oldPath:"",//原来路由
				tableLabelArr:"",//表头
				phash:"1",//阶段1=立项 2=可研 3=储备 0=浏览
				parentItemId:null //父项id-新增下级时用
			},
			showDrawer:false,
			userId:'',
			token:'',
			createOrg:"",
			isClearable:true,      // 可清空（可选）
			isAccordion:true,      // 可收起（可选）
			valueId:"",            // 初始ID（可选）
			props:{                // 配置项（必选）
				value: 'id',
				label: 'name',
				children: 'children',
				code: 'createOrg'
				// disabled:true
			},
			deptArr:[],
			showQuery:false,
   			isShowQuery:false,
			dialogVisibleL: false,
			dialogLoading: false,
			loading: false,
			delData: {
				ids: []
			},
			lableList: [{ value: '' }],
			totalNum: 0,
			currentPage: 1,
			currentNum:20,
			year: '',
			dialogFormVisible: false,
			form: {},
			options: [],
			value: '',
			formLabelWidth: '33%',
			formLabelWidth1: '50%',
			input: '',
			// 子组件的表格数据
			tableData: [],
			// 子组件的表头数据
			tableLabel: [],
			//查询条件
			param: {
				createOrg: '',  //单位编码
				index: 1,
				itemName: '',  //项目名称
				itemType: '',  //项目类型
				pageSize: 10,  
				speciality: '',  //专业类型
				voltLevel: '', //电压等级
				itemPhase: 1,
				itemPhaseDet: '26',  //可研状态
				ghYear: '',     //规划年份
				itemSource:''
			},
			// dictMap: Object,
			dictMap: new Map(),
			//查询条件
      		selectParamDict: new Map(),
			tableDataShow: [],
			saveData: {
				kssj: ''
			},
			//提交评审计划弹框中的信息
			pleanTableLabel: [],
			planLabel: {},
			//项目id
			tempItemId: '',
			//项目名称
			tempItemName: '',
			//是否展示项目详情弹框
			itemShow: false,
			saveLabel: {},
			//项目可研报告文件名
			reportFileName: '',
			//获取字段类型字典，用于日期类型的展示
			columnTypeMap: new Map(),
			isShowFilter: false,
			//自动表格高度
			tableHeight: '',
			tableHeightTab:"",
			minDate: "",
			maxDate: "",
			pickerOptionsStart: {
				disabledDate: time => {
					let maxDate = new Date(this.maxDate);
					let minDate = Date.now() - 8.64e7;
					return time.getTime() <= minDate || time.getTime() > maxDate;
				}
			},
			pickerOptionsEnd: {
				disabledDate: time => {
					let minDate = new Date(this.minDate);
					let maxDate = Date.now() - 8.64e7;
					return  time.getTime()<=maxDate  || time.getTime() <= minDate ;
				}
			},
			auditPlanId:"",
			//点击提交传入的参数
			saveParams:[],
			//用户所属专项类型值
			userSpecialityArr:[]
		};
	},
	components: {
		querys,
		SelectTree,
		ItemAddCom
	},
	created() {
		//表格自动高度
    	this.tableHeight=window.innerHeight-270;		
	},
	mounted() {
		this.getItemLabel()
		this.getSpecialityIds()
		this.getTable(1, 20);
		//表格自动高度
		// window.onresize = () => {
		// 	return (() => {
		// 		this.tableHeight = window.innerHeight - 320 + 'px';
		// 	})();
		// };
		this.userId = sessionStorage.getItem('userId');
		this.token = sessionStorage.getItem('token');
		// this.getTreeDeptCode()
		console.log('compile')
	},
	computed: {
		...mapGetters([
			// 'token',
		])
	},
	watch:{
		createOrg(){
			this.param.createOrg = this.createOrg
			this.getTable(this.currentPage,this.currentNum)
		}
	},
	methods: {
		// 懒加载table树形子数据
		tableLoad(row, treeNode, resolve) {
			let params = {
				'parentItemId':row.itemId
			}
			getChildrenItem(params).then(res => {
				if(res.data.successful){
					let resultData = res.data.resultValue
					resultData.forEach(function(item){
						item.isChild = true
					})
					let childData =  this.getShowDateTable(resultData, this.dictMap, this.columnTypeMap);
					resolve(childData)
				}
			})
		},
		isChildren(row) {
			if(row.isChild){
				return false
			}else{
				return true
			}
		},
		// 批量编辑评审计划
		editSomePlan(){
			let _this = this
			_this.isEdit = true;
			if(_this.editData.itemId.length >0){
				_this.dialogFormVisible = true;
				_this.editLoading = true
				getSubmitPlanLabel()
				.then(response => {
					//{}写需要的参数
					_this.editLoading = false
					console.log('response', response); //后端返回数据
					if (response.data.successful) {
						_this.pleanTableLabel = response.data.resultValue.items;
						_this.pleanTableLabel.forEach(function(item) {
							_this.$set(item, 'value', '');
						});
						let common_table_info = {}; //数组转对象，避免类型报错
						for (let i in _this.pleanTableLabel) {
							let tempObj = response.data.resultValue.items[i];
							common_table_info[i] = tempObj;
							//构建是否必填规则
							if (tempObj.isMandatory == 1) {
								//必填项
								let rule = new Object();
								rule.required = true;
								rule.message = tempObj.dispName + '不能为空';
								rule.trigger = 'blur';
								common_table_info[i].rule = rule;
							} else {
								let rule = new Object();
								rule.required = false;
								common_table_info[i].rule = rule;
							}
							// 添加手机号校验
							if(tempObj.colDname == "mgtUserTel" || tempObj.colDname == "actUserTel"){
								//手机号
								let rule = new Object();
								rule.required=false; 
								rule.validator= _this.validaMobile;
								rule.message = "手机号格式不正确"
								rule.trigger= "blur" 
								common_table_info[i].rule = rule;
							}
						}
						_this.planLabel = common_table_info;
					}
					_this.loading = false;
				})
				.catch(error => {
					console.log(error); //error
				});
			}else{
				_this.$message({
				showClose: true,
				message: '请选择至少一条数据进行编辑',
				type: 'warning'
				});
			}
			
			// _this.planLabel = {};
		},
		// 手机号校验
		validaMobile(rule, value, callback) {
			let telephoneReg = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/;
			if (value) {
				if (!telephoneReg.test(value)) {
					callback(new Error("手机号格式不正确"));
				} else {
					callback();
				}
			} else {
				callback();
			}
		},
		//获取查询条件下拉框
		getItemLabel() {
			let _this = this
			queryFilterParam().then(response => {
				console.log('response', response)//后端返回数据
				if (response.data.successful) {
					let labelList = response.data.resultValue.tableLable
					_this.selectParamDict.set('voltLevel', response.data.resultValue.voltCodeList)
					_this.selectParamDict.set('itemLevel', response.data.resultValue.itemLevelList)
					_this.selectParamDict.set('itemSource', response.data.resultValue.itemSourceList)
					_this.selectParamDict.set('speciality', response.data.resultValue.specialityList)
					_this.$forceUpdate()
				}
			})
		},
		// 子组件向父组件传递参数
		changeStatus(status) {
			this.showDrawer = status
			console.log('status'+status)
		},
		//展示项目详细信息
		showDetailDialog(row) {
			this.showData.itemId = row.itemId
			this.showData.phash = 0
			this.showDrawer = true;
			this.timer = new Date().getTime()
		},
		exportTmp(){
			let _this = this
			if(_this.delData.ids.length){
				let exportData = _this.delData
				exportData.ids = exportData.ids.toString()
				window.location.href=_this.GLOBALURL+'/prjStOnAuditPlan/planToExcel?ids='+exportData.ids+'&token='+_this.token
				
			}else{
				_this.$message({
				showClose: true,
				message: '请选择至少一条数据',
				type: 'warning'
				});
			}
		},
		getValue(value){
			this.createOrg = value
		},
		//获取单位树形结构
		getTreeDeptCode(){
			let _this = this
			getUserDept().then(response=>{
				let valueId = response.data.resultValue
				getTreeDeptCode().then(response=>{
					console.log(response)
					if(response.data.successful){
						_this.valueId = valueId
						_this.deptArr = response.data.resultValue
					}
				})
			})
			
		},
		changeQuery(state,height){
	      if(state){
	        this.tableHeight = height-50;
			this.isShowQuery = true
			this.showQuery = true
	      }else{
	        this.tableHeight = height-50;
			this.isShowQuery = false
			this.showQuery = false

	      }
	    },
		getSpecialityIds(){
			let _this = this
			getSpecialityIds().then(response=>{
				if(response.data.successful){
					let str = response.data.resultValue
					if(str!=''){
						_this.userSpecialityArr = str.split(',')
					}
					console.log(response)
				}
			})
		},
		//点击提交，更改可研状态
		changeFeaStatus(){
			let _this = this
			//校验提交参数
			if(_this.saveParams.length==0){
				this.$message({
                  showClose: true,
                  message: "请选择要提交的项目",
                  type: 'warning'
                });
				return  false
			}
			for(let i=0;i<_this.saveParams.length;i++){
				if(_this.saveParams[i].havePlan==0){
					this.$message({
						showClose: true,
						message: "所选的项目中有未提交计划的数据",
						type: 'warning'
					});
					return  false
				}
			}
			console.log(_this.saveParams)
			_this.statusLoading = true
			changePlaneStatus(_this.saveParams).then(response=>{
				_this.statusLoading = false
				if(response.data.successful){
					this.$message.success("提交评审计划成功")
					_this.getTable(_this.currentPage,_this.currentNum)
					_this.saveParams = []
					_this.$refs.itemTable.clearSelection()
				}else{
					this.$message.error("提交评审计划失败·")
				}
			})
		},
		getRowKeys(row){
			return row.itemId;//返回唯一ID
		},
		 //时间戳字符串的转化
		formartDate(param) {
			var time = new Date(param);
			var y = time.getFullYear();
			var m = time.getMonth()+1;
			var d = time.getDate();
			// var h = time.getHours();
			// var mm = time.getMinutes();
			// var s = time.getSeconds();
			// return y+'-'+this.add0(m)+'-'+this.add0(d)+' '+this.add0(h)+':'+this.add0(mm)+':'+this.add0(s);
			return y+'-'+this.add0(m)+'-'+this.add0(d);
		},
		add0(m){
			return m<10?'0'+m:m;
		},
		tableRowClassName({row, rowIndex}) {
			if (row.havePlan == 1) {
				return 'success-row';
			}else{
			    return '';
			} 
		
		},
		uploadSuccess(response){
		  let _this = this;
			this.fileUploadLoading = false
			if(response.successful){
				this.$message({
					showClose: true,
					message: '上传成功',
					type: 'success'
				});
				_this.tzkeVisible1 = false;
				_this.fileList = [];
				this.currentPage =1; 
				this.getTable(1, _this.currentNum);
			}else{
				let str = response.resultHint
				this.$message({
					showClose: true,
					message: '上传失败 '+str,
					dangerouslyUseHTMLString: true,
					type: 'error'
				});
			}
		},
		changeStartDate(val) {
				if(val != null) {
					//分别取到开始日期和结束日期
					if(this.planLabel.planEndDate!=null){
						this.maxDate = this.planLabel.planEndDate;
					}
					this.minDate = val
				}
		},
		changeEndDate(val){
			if(val != null) {
				this.maxDate = val;
			}
		},
		switchFilter() {
			this.isShowFilter = !this.isShowFilter;
		},
		//获取字段类型字典
		getColumnTypeArr(labelList) {
			let map = new Map();
			for (let i = 0; i < labelList.length; i++) {
				let temp = labelList[i];
				map.set(temp.colDname, temp.colType);
			}
			return map;
		},
		//查询
		selectItem() {
			this.currentPage = 1;
			this.getTable(1, this.currentNum);
		},
		//重置查询
		resetItem() {
			let _this = this;
			_this.param = {
				createOrg: '',
				index: 1,
				itemName: '',
				itemType: '',
				pageSize: 10,
				speciality: '',
				voltLevel: '',
				// itemPhase: 1,
				feaStatus: 0,
				ghYear: ''
			};
			this.getTable(1, _this.currentNum);
		},
		//取消提交评审计划
		cancelSumbmitPlan(){
			let _this = this
			_this.dialogFormVisible = false;
			_this.minDate = ""
			_this.maxDate = ""
			
		},
		//提交评审计划
		submitPlan() {
			let _this = this
			_this.$refs["lableFormQuestion1"].validate(valid => {
				if(valid){
					let subdata = { items: [] };
					let arr = {};
					let exportData = _this.editData
					exportData.itemId = exportData.itemId.toString()
					Object.keys(_this.planLabel).forEach(function(key) {
						console.log(key, _this.planLabel[key]);

						arr[_this.planLabel[key].colDname] = _this.planLabel[key].value;
					});
					
					if(_this.isEdit == true){
						arr['itemId'] = exportData.itemId
						
					}else{
						arr['itemId'] = _this.tempItemId;
						if(_this.auditPlanId!=''){
							arr['auditPlanId'] = _this.auditPlanId
						}
					}
					
					subdata.items.push(arr);
					_this.dialogFormVisible = false;
					_this.submitLoading = true
					submitPlane(subdata).then(response => {
						console.log(response);
						_this.submitLoading = false
						if (response.data.successful) {
							this.$message({
								showClose: true,
								message: '保存成功',
								type: 'success'
							});
							_this.getTable(_this.currentPage, _this.currentNum);
							this.$refs.itemTable.clearSelection()
							_this.minDate = ""
							_this.maxDate = ""
						} else {
							this.$message({
								showClose: true,
								message: '保存失败',
								type: 'error'
							});
						}
						_this.loading = false;
					});
					_this.loading = false;
				}else{
					console.log('error submit!!');
					return false;
				}
			})
			
		},
		//展示提交评审计划弹框
		showSubmitPlan(data) {
			let _this = this;
			_this.dialogFormVisible = true;
			_this.tempItemId = data.itemId;
			getSubmitPlanLabel()
				.then(response => {
					//{}写需要的参数
					console.log('response', response); //后端返回数据
					if (response.data.successful) {
						_this.pleanTableLabel = response.data.resultValue.items;
						_this.pleanTableLabel.forEach(function(item) {
							_this.$set(item, 'value', '');
						});
						let common_table_info = {}; //数组转对象，避免类型报错
						for (let i in _this.pleanTableLabel) {
							let tempObj = response.data.resultValue.items[i];
							common_table_info[i] = tempObj;
							//构建是否必填规则
							if (tempObj.isMandatory == 1) {
								//必填项
								let rule = new Object();
								rule.required = true;
								rule.message = tempObj.dispName + '不能为空';
								rule.trigger = 'blur';
								common_table_info[i].rule = rule;
							} else {
								let rule = new Object();
								rule.required = false;
								common_table_info[i].rule = rule;
							}
							// 添加手机号校验
							if(tempObj.colDname == "mgtUserTel" || tempObj.colDname == "actUserTel"){
								//手机号
								let rule = new Object();
								rule.required=false; 
								rule.validator= _this.validaMobile;
								rule.message = "手机号格式不正确"
								rule.trigger= "blur" 
								common_table_info[i].rule = rule;
							}
						}
						if(data.havePlan==1){//如果有计划，查询计划详情
							getByAuditPlanId(data.itemId).then(res=>{
								if(res.data.successful){
									let temObj2 = res.data.resultValue
									for(let index in common_table_info){
										let tempData = common_table_info[index]
										tempData.value = temObj2[tempData.colDname]
										if(tempData.colDname=="planEndDate"||tempData.colDname=="planStartDate"){
											let dataStr =  temObj2[tempData.colDname]
											tempData.value = _this.formartDate(dataStr)
										}else{
											tempData.value = temObj2[tempData.colDname]
										}
										common_table_info[index] = tempData
									}
									_this.auditPlanId = temObj2.auditPlanId
								}

							})
						}
						_this.planLabel = common_table_info;
					}
					_this.loading = false;
				})
				.catch(error => {
					console.log(error); //error
				});
		},
		updateItem() {
			this.dialogFormVisible1 = true;
		},
		//监听表单勾选事件
		handleSelectionChange(val) {
			let _this = this
			_this.editData = {
				"itemId": []
			}
			_this.saveParams = []
			_this.delData = {
				"ids": [],
			}
			val.forEach(function(item,idx){
				_this.delData.ids.push(item.itemId)
				_this.editData.itemId.push(item.itemId)
			})
			val.forEach(function(item){
				let obj = {}
				obj.itemId = item.itemId
				obj.auditPlanId = item.auditPlanId
				obj.havePlan = item.havePlan
				_this.saveParams.push(obj)
			})
		},
		del() {
			let _this = this;
			// if(_this.delData.ids.length){
			this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning'
			})
				.then(() => {
					this.$message({
						showClose: true,
						message: '删除成功',
						type: 'success'
					});
				})
				.catch(() => {
					this.$message({
						type: 'warning',
						message: '已取消删除'
					});
				});
		},
		submitForm(formName) {
			let _this = this;
			_this.$refs[formName].validate(valid => {
				if (valid) {
					let subdata = { items: [] };
					console.log(_this.lableList);
					let arr = {};
					_this.lableList.forEach(function(item, idx) {
						arr[item.colDname] = item.value;
					});
					subdata.items.push(arr);
					// console.log(subdata)
					_this.dialogFormVisible = false;

					_this.loading = true;
					savePrjStDmd(subdata).then(response => {
						console.log(response);
						if (response.data.successful) {
							this.$message({
								showClose: true,
								message: '添加成功',
								type: 'success'
							});
							_this.getTable(_this.currentPage, _this.currentNum);
						} else {
							this.$message({
								showClose: true,
								message: '添加失败',
								type: 'error'
							});
						}
						_this.loading = false;
					});
				} else {
					console.log('error submit!!');
					return false;
				}
			});
		},
		showDialog() {
			let _this = this;
			_this.dialogFormVisible = true;
			_this.dialogLoading = true;

			getSaveTable().then(response => {
				console.log(response);
				if (response.data.successful) {
					response.data.resultValue.items.forEach(function(item, idx) {
						item.value = '';
					});

					_this.lableList = response.data.resultValue.items;
				}
				_this.dialogLoading = false;
			});
		},
		//获取当前表格条数
		handleSizeChange(val) {
			console.log(`每页 ${val} 条`);
			this.currentNum = val;
			this.getTable(this.currentPage, val);
		},
		//获取当前页码
		handleCurrentChange(val) {
			console.log(`当前页: ${val}`);
			this.currentPage = val;
			this.getTable(val, this.currentNum);
		},
		getTable(page, size) {
			let _this = this;
			_this.loading = true;
			_this.param.index = page;
			_this.param.pageSize = size;

			prjCmItem(_this.param)
				.then(response => {
					//{}写需要的参数
					console.log('response', response); //后端返回数据
					if (response.data.successful) {
						_this.totalNum = response.data.resultValue.totalNum;
						_this.tableLabel = response.data.resultValue.tableLable;
						_this.dictMap = _this.getDictArr(_this.tableLabel);
						_this.saveLabel = _this.getLabelList(_this.tableLabel);
						_this.columnTypeMap = _this.getColumnTypeArr(_this.tableLabel);
						_this.tableData = response.data.resultValue.tableData;
						_this.tableDataShow = _this.getShowDateTable(_this.tableData, _this.dictMap, _this.columnTypeMap);
					}
					_this.loading = false;
				})
				.catch(error => {
					console.log(error); //error
				});
		},
		//获取项目信息详情展示弹框表头
		getLabelList(labelList) {
			let _this = this;
			labelList.forEach(function(item) {
				_this.$set(item, 'value', '');
			});
			let common_table_info = {}; //数组转对象，避免类型报错
			for (let i in labelList) {
				common_table_info[i] = labelList[i];
			}
			return common_table_info;
		},
		getDictArr(labelList) {	
			//根据当前用户id查询用户所属的单位
			//获取数据字典
			let _this = this
			let map = new Map();
			for (let i = 0; i < labelList.length; i++) {
				let temp = labelList[i];
				if (temp.codeList != null) {
					if(temp.colDname=="speciality"){
						//过滤当前登录人的专项
						let arr = []
						for(let i=0;i<temp.codeList.length;i++){
							for(let j=0;j<_this.userSpecialityArr.length;j++){
								if(temp.codeList[i].codeValue==_this.userSpecialityArr[j]){
									arr.push(temp.codeList[i])
								}
							}
						}
						temp.codeList = arr
					}
					map.set(temp.colDname, temp.codeList);
				}
			}
			return map;
		},
		getShowDateTable(tableData, dictMap, columnTypeMap) {
			let _this = this;
			let arr = [];
			for (let i = 0; i < tableData.length; i++) {
				let tempObj = tableData[i];
				//拷贝对象
				let copyObj = new Object();
				//遍历获取的数据中的属性，如果属性名有和数据字典中的key相对应的，取出数据字典对应的值赋值过去
				for (let [key, value] of Object.entries(tempObj)) {
					console.log(key + ':' + value);
					if (dictMap.has(key)) {
						let dictValueList = dictMap.get(key);
						for (let j = 0; j < dictValueList.length; j++) {
							let tempObj2 = dictValueList[j];
							if (tempObj2.codeValue == value) {
								copyObj[key] = tempObj2.codeName;
							}
						}
					} else if (columnTypeMap.get(key) == 'DATE') {
						copyObj[key + 'Rel'] = value;
						let timeStr = _this.formartDate(value);
						copyObj[key] = timeStr;
					} else {
						copyObj[key] = value;
					}

					//所属单位不去查询码表
					if(key=="createOrg"){
						copyObj["createOrg"] = value
					}
				}
				arr.push(copyObj);
			}
			return arr;
		}
	}
};
</script>
