<template>
  <div class="container">
    
    <div class="main clearfix" :style="{'margin-left':$store.state.app.isCollapse ? '84px':'240px'}">
      
      <div class="main_container plan-summary">
        <!-- <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item v-for="mat in $route.matched" ><span class="color_tip">{{mat.name}}</span></el-breadcrumb-item>
        </el-breadcrumb> -->
        <div class="btns query_btns">
            <el-button type="primary" size="small" @click="updateHZByCtotalTime">提交</el-button>
        </div>

        <div class="template_main">
          <div class="template_left">
            <el-table 
              class="plan-table"
              :data="colTimeList"
              border
              :header-cell-style="{background:'#359691',color:'#fff'}"
              row-class-name="row"
              cell-class-name="column"
              :highlight-current-row="true"
              @current-change="handleChangeColTimeList"
              v-loading="timeListloading"
              :height="tableHeight"
              >
              <el-table-column
                label="评审计划汇总列表"
                prop="jhps"
                >
              </el-table-column>
            </el-table>
          </div>
          <div class="template_right">
            <div class="table-item-box">
					    <div :class="showDrawer?'table-wrapper-run':'table-wrapper-init'">
                <el-table 
                  :data="tableDataShow"
                  border
                  :header-cell-style="{background:'#359691',color:'#fff'}"
                  @selection-change="handleSelectionChange"
                  :row-class-name="tableRowClassName"
                  cell-class-name="column"
                  :highlight-current-row="true"
                  v-loading="tableDataLoding"
                  :height="tableHeight"
                  lazy
                  :load="tableLoad"
                  row-key="itemId"
                  :tree-props="{children: 'children', hasChildren: 'hasChildren'}"
                  >
                  <el-table-column
                          type="index"
                          width="50"
                          label="序号" >
                  </el-table-column>
                  <el-table-column
                          min-width="220"
                          v-for="(item, index) in tableLabel"
                          v-if ="item.colDname != 'itemName'&&item.showType==1"
                          :key="index"
                          :prop="item.colDname"
                          :label="item.dispName"
                          :show-overflow-tooltip="true" >
                  </el-table-column>
                  <el-table-column v-else-if="item.colDname == 'itemName'"
                                  :label="item.dispName" width="200" 
                                  :show-overflow-tooltip="true">
                    <template slot-scope="scope" >
                      <span class="blue" @click="showDetailDialog(scope.row)">{{scope.row.itemName}}</span>
                    </template>
                  </el-table-column>
                  <el-table-column label="操作">
                    <template slot-scope="scope" fixed v-if="!scope.row.isChild">
                      <span class="blue" @click="showUpdataPlaneTimeDialog(scope.row)">编辑</span>
                    </template>
                  </el-table-column>
                </el-table>
                <div class="page">
          
                  <el-pagination
                    background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10,20,30,50]"
                    :page-size="20"
                    layout="sizes, total, prev, pager, next, jumper"
                    :total="totalNum">
                  </el-pagination>
                </div>
              </div>
              <div class="planDrawer" :class="showDrawer?'item-add-wrapper-run':'item-wrapper-init'">
                <!-- 新增 修改 浏览 引itemaddcom -->
                <item-add-com 
                v-if="showDrawer" 
                :key="timer" 
                :showData="showData" 
                :showAddDialog="showDrawer"
                :isShowQuery="showQuery"
                :tableHeightTab="tableHeight"
                @changeStatus="changeStatus">
                </item-add-com>
              </div>
            </div>
                
          </div>
          
        </div>
        
        

      </div>
    </div>
    <!-- <right></right> -->

    <!-- 编辑项目计划信息 -->
    <el-dialog :title="curItemName" :close-on-click-modal = "false" :visible.sync="showUpdatePlaneTime"
    :modal-append-to-body='false'>
      
        <el-form :model="updateForm" ref="form" label-position="right" v-loading="dialogLoading" size="small">
          <el-form-item label="计划评审开始时间">
            <el-col :span="11">
              <el-date-picker type="date" 
                      value-format="yyyy-MM-dd" placeholder="选择日期" 
                      v-model="updateForm.planStartDate" style="width: 100%;"
                      @change="changeStartDate"
                      :picker-options="pickerOptionsStart">
               </el-date-picker>
            </el-col>
            
          </el-form-item>
          <el-form-item label="计划评审结束时间">
            <el-col :span="11">
              <el-date-picker type="date" 
                      value-format="yyyy-MM-dd" 
                      placeholder="选择日期" 
                      v-model="updateForm.planEndDate" 
                      @change="changeEndDate"
                      style="width: 100%;"
                      :picker-options="pickerOptionsEnd"></el-date-picker>
            </el-col>
            
          </el-form-item>
          
        </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="showUpdatePlaneTime = false">取 消</el-button>
        <el-button type="primary" @click="updataPlaneTime()">保存</el-button>
      </div>
    </el-dialog>

    <!--项目详细信息-->
    <el-dialog :visible.sync="itemShow" :close-on-click-modal = "false" :modal-append-to-body='false'>
        <item-dialog :itemId="tempItemId"></item-dialog>
    </el-dialog>
  </div>
</template>

<script>
// @ is an alias to /src
import itemDialog from '@/views/project/item.vue'
import right from '@/views/right.vue'
import ItemAddCom from "@/views/project/itemAddCom"
import {getPlan,getColTimeList,updateAuditPlanTime,updateHZByCtotalTime,getChildrenItem} from '@/api/data'


export default {
  name: 'planSummary',
  data() {
  	return {
      childIds:[],
      isShowQuery:false,
      showQuery:false,
      tableHeightTab:"",
      timer:"",
			showAddDialog:false,   //弹窗是否展示-新增 修改 浏览
			showData:{
				itemId:null, //项目id
				specClass: "",//专项类型
				specClassName:"",//专项类型名称
				oldPath:"",//原来路由
				tableLabelArr:"",//表头
				phash:"1",//阶段1=立项 2=可研 3=储备 0=浏览
				parentItemId:null //父项id-新增下级时用
			},
			showDrawer:false,
      dialogLoading:false,
      loading:false,
      delData:{
        "ids": [],
      },
      lableList:[{value: ''}],
      totalNum:0,
      currentPage:1,
      currentNum:20,
      year: '',
      dialogFormVisible:false,
      dialogFormVisible2:false,
      form: {
        time1:'',
        time2:''
      },
      options: [],
      value:'',
      formLabelWidth: '120px',
      input: '',
      //汇总时间列表
      colTimeList:[],
  		// 子组件的表格数据
  		tableData: [],
  		// 子组件的表头数据
      tableLabel: [],
      //表格展示数据列表
      tableDataShow:[],
      //选中的计划id
      selectPlanId:{},
      //获取字段类型字典，用于日期类型的展示
      columnTypeMap: new Map,
      //查询条件
      param:{
          "itemName":"",
          "deptCode":"",
          "auditPlanStatus":"",
          "specialClass":"",
          "itemType":"",
          "voltLevel":"",
          "filter":""
      },
      //当前选中的项目id
      tempItemId:"",
      //是否展示项目信息
      itemShow:false,
      //当前选中日期
      currentColTime:"",
      //当前项目名称
      curItemName:"",
      //是否展示编辑弹框
      showUpdatePlaneTime:false,
      //编辑框中的数据
      updateForm:{
        "planStartDate":"",
        "planEndDate":""
      },
      //评审计划id
      auditPlanId:"",
      timeListloading: false,
      tableDataLoding: false,
      tableHeight:'',
      //时间校验
      minDate: "",
			maxDate: "",
			pickerOptionsStart: {
				disabledDate: time => {
					let maxDate = new Date(this.maxDate);
					let minDate = Date.now() - 8.64e7;
					return time.getTime() <= minDate || time.getTime() > maxDate;
				}
			},
			pickerOptionsEnd: {
				disabledDate: time => {
					let minDate = new Date(this.minDate);
					let maxDate = Date.now() - 8.64e7;
					return  time.getTime()<=maxDate  || time.getTime() <= minDate ;
				}
			}
  	}
  },
  components: {
     "item-dialog":itemDialog,
     ItemAddCom
  },
  mounted(){
    this.getColTimeList()
  //   window.onresize = () => {
		// 	return (() => {
		// 		this.tableHeight=window.innerHeight-320+'px';
		// 	})()
		// }
  },
  created(){
		//表格自动高度
		this.tableHeight=window.innerHeight-220;
	},
  methods: {
    // 子组件向父组件传递参数
		changeStatus(status) {
			this.showDrawer = status
			console.log('status'+status)
    },
    // 懒加载table树形子数据
    tableLoad(row, treeNode, resolve) {
      let params = {
        'parentItemId':row.itemId
      }
      getChildrenItem(params).then(res => {
        if(res.data.successful){
          let resultData = res.data.resultValue
          resultData.forEach(function(item){
						item.isChild = true
					})
          let childData =  this.getShowDateTable(res.data.resultValue, this.dictMap, this.columnTypeMap);
					resolve(childData)
        }
      })
    },
		//展示项目详细信息
		showDetailDialog(row) {
			this.showData.itemId = row.itemId
			this.showData.phash = 0
			this.showDrawer = true;
			this.timer = new Date().getTime()
		},
    //待审核的项目显示为红色
		tableRowClassName({row, rowIndex}) {
			if (row.auditPlanStatus == 2) {
				return 'success-row';
			}else{
			    return '';
			} 
		
		},
    changeStartDate(val) {
				if(val != null) {
					//分别取到开始日期和结束日期
					if(this.updateForm.planEndDate!=null){
						this.maxDate = this.updateForm.planEndDate;
					}
					this.minDate = val
				}
		},
		changeEndDate(val){
			if(val != null) {
				this.maxDate = val;
			}
		},
    //提交可研评审
    updateHZByCtotalTime(){
       let _this = this
       let formData  = new FormData()
       if(_this.currentColTime==""||_this.currentColTime==null){
          this.$message({
                    showClose: true,
                    message: "请选择时间批次",
                    type: 'warning'
              });
            return false
       }

        this.$confirm('是否提交评审计划?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
			  }).then(() => {
              formData.append("cTotalTime",_this.currentColTime)
              updateHZByCtotalTime(formData).then(response=>{
                  if(response.data.successful){
                      this.$message({
                            showClose: true,
                            message: "提交成功",
                            type: 'success'
                      });
                      _this.getColTimeList()
                  }else{
                      this.$message({
                            showClose: true,
                            message: "提交失败",
                            type: 'error'
                      });
                  }
              })
        }).catch(() => {});

    },
    //修改计划评审和结束时间
    updataPlaneTime(){
        let _this = this
        // let formData = new FormData()
        // formData.append("planStartDate",_this.updateForm.planStartDate)
        // formData.append("planEndDate",_this.updateForm.planEndDate)
        // formData.append("auditPlanId",_this.auditPlanId)
        let updateTimeParam = {}
        updateTimeParam.planStartDate = _this.updateForm.planStartDate
        updateTimeParam.planEndDate = _this.updateForm.planEndDate
        updateTimeParam.auditPlanId = _this.auditPlanId
        // let jsonStr = JSON.stringify(updateTimeParam)
        // _this.tableDataLoding = true
        // let formData = new FormData()
        // formData.append("jsonObj",jsonStr)
        updateAuditPlanTime(updateTimeParam).then(response => {
          _this.tableDataLoding = false
            console.log(response)
            if(response.data.successful){
                this.$message({
                    showClose: true,
                    message: "修改成功",
                    type: 'success'
                });
                //当前页码和当前每页条数
                _this.getTable(_this.currentPage,_this.currentNum)
                _this.showUpdatePlaneTime = false
            }else{
                this.$message({
                    showClose: true,
                    message: "修改失败",
                    type: 'error'
                });
            }
      })
        
    },
    //时间戳字符串的转化
    formartDate(param) {
      var time = new Date(param);
      var y = time.getFullYear();
      var m = time.getMonth()+1;
      var d = time.getDate();
      // var h = time.getHours();
      // var mm = time.getMinutes();
      // var s = time.getSeconds();
      // return y+'-'+this.add0(m)+'-'+this.add0(d)+' '+this.add0(h)+':'+this.add0(mm)+':'+this.add0(s);
      return y+'-'+this.add0(m)+'-'+this.add0(d);
    },
    add0(m){
      return m<10?'0'+m:m;
    },
    //编辑评审计划开始和结束时间
    showUpdataPlaneTimeDialog(val){
        let _this = this;
        _this.showUpdatePlaneTime = true
        _this.curItemName = val.itemName
        // _this.updateForm.planStartDate = val.planStartDate
        // _this.updateForm.planEndDate = val.planEndDate
        _this.auditPlanId = val.auditPlanId
        _this.tempItemId = val.itemId
        _this.$set(_this.updateForm,'planStartDate',val.planStartDate);
        _this.$set(_this.updateForm,'planEndDate',val.planEndDate);
        _this.changeStartDate(val.planStartDate)
        _this.changeEndDate(val.planEndDate) 
        let params = {
          'parentItemId':val.itemId
        } 
        getChildrenItem(params).then(res => {
          if(res.data.successful){
            let resultData = res.data.resultValue
            // _this.childIds = _this.childIds.push(_this.auditPlanId)
            resultData.forEach(function(item){
              _this.childIds.push(item.auditPlanId)
            }) 
            _this.auditPlanId = _this.auditPlanId+","+_this.childIds.toString()
            console.log(_this.auditPlanId)
          }
        })

    },
    //当点击不同的汇总日期时
    handleChangeColTimeList(val){
        let _this = this
        if(_this.currentColTime!=val.jhps){
            _this.currentColTime = val.jhps
            //传参传入当前每页条数
            _this.getTable(_this.currentPage,_this.currentNum)
        }
    },
    //获取评审计划信息列表
    getColTimeList(){
      let _this = this
      _this.timeListloading = true
      getColTimeList("5").then(response=>{
          _this.timeListloading = false
          console.log(response)
          if(response.data.successful){
              let arr = response.data.resultValue
              _this.colTimeList = []
              arr.forEach(function(item){
                  let tempObj = {}
                  tempObj.jhps = item
                  _this.colTimeList.push(tempObj)
              })
              if(arr.length>0){
                _this.currentColTime = arr[0]
              }
              _this.getTable(1,_this.currentNum)
          }else{
            this.$message({
              showClose: true,
              message: '获取评审计划信息列表失败',
              type: 'error'
            });
          }
      })
    },
    //展示项目详细信息
    showItemInfo(data){
      let _this = this;
			_this.tempItemId = data.itemId;
			this.$router.push({path:'/itemInfo',query:{itemId:_this.tempItemId ,oldPath:_this.$route.path}});
    },
    //监听表单选中事件
    handleSelectionChange(val){
      let _this = this
      _this.selectPlanId = {
        "ids": [],
      }
      val.forEach(function(item){
        _this.selectPlanId.ids.push(item.auditPlanId)
      })
    },
    //获取当前每页条数
    handleSizeChange(val) {
      this.currentNum = val;
      this.getTable(this.currentPage,val)
    },
    //分页查询
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage = val;
      this.getTable(val,this.currentNum)
    },
    //获取数据列表
    getTable(page,size){
        let _this = this
        _this.tableDataLoding = true
        let filter = 'cTotalTimeStr=1&itemPhaseDet=5'
        if(_this.currentColTime!=""){
            filter +='&cTotalTime='+_this.currentColTime
        }
        //拼接查询条件
        for(let [key, value] of Object.entries(_this.param)) {
            if(key=="pageIndex"||key=="pageSize"||value==''){
                continue
            }
            console.log(key + ':' + value);
            filter+='&'+key+'='+value
        }
        let searchParam = {}
        searchParam.pageIndex = page
        searchParam.pageSize = size
        searchParam.filter = filter      
        
        getPlan(searchParam).then(response => {//{}写需要的参数
            _this.tableDataLoding = false
            console.log('response',response)//后端返回数据
            if(response.data.successful){
                _this.totalNum = response.data.resultValue.totalNum
                let labelArr = response.data.resultValue.tableLable
                _this.tableLabel = _this.getLabelList(labelArr)
                _this.dictMap = _this.getDictArr(labelArr)
                _this.columnTypeMap = _this.getColumnTypeArr(labelArr)
                _this.tableData = response.data.resultValue.tableData
                _this.tableDataShow = _this.getShowDateTable(_this.tableData,_this.dictMap,_this.columnTypeMap)
            }
            _this.loading = false
            
          }).catch(error => {
            console.log(error)//error
          })
        
    },
     //获取字典
    getDictArr(labelList){ 
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        if(temp.codeList!=null){
          map.set(temp.colDname,temp.codeList);
        }
      }
      return map
    },
    //获取字段类型字典
    getColumnTypeArr(labelList){
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        map.set(temp.colDname,temp.colType);
      }
      return map
    },
    //获取插入和修改的表头
    getLabelList(labelList){ 
      let _this =this
      labelList.forEach(function (item) {
        _this.$set(item,'value','');
      })
      let common_table_info = {};//数组转对象，避免类型报错
      for(let i in labelList){
        common_table_info[i] = labelList[i];
      }
      return common_table_info
    },
    //获取展示数据
    getShowDateTable(tableData,dictMap,columnTypeMap){
      let _this = this
      let arr = [];
      for(let i=0;i<tableData.length;i++){
        let tempObj = tableData[i];
        //拷贝对象
        let copyObj = new Object();
        //遍历获取的数据中的属性，如果属性名有和数据字典中的key相对应的，取出数据字典对应的值赋值过去
        for (let [key, value] of Object.entries(tempObj)) {
          console.log(key + ':' + value);
          if(dictMap.has(key)){
            let dictValueList = dictMap.get(key);
            for(let j=0;j<dictValueList.length;j++){
              let tempObj2 = dictValueList[j];
              if(tempObj2.codeValue==value){
                copyObj[key] = tempObj2.codeName;
              }
            }
          }else if(columnTypeMap.get(key)=="DATE"){
               copyObj[key+"Rel"] = value
               let timeStr = _this.formartDate(value)
               copyObj[key] = timeStr;
          }else{
              copyObj[key] = value;
          }
        }
        arr.push(copyObj);
      }
      return arr;
    },
  }
}
</script>
<style type="text/css">
  /* .plan-summary .el-table .el-table__body-wrapper {
    height: 90% !important;
  } */
  
</style>
