    width:20%;
    margin-right:10px;
    text-align:center
    height: calc(100vh - 190px);
  }
  .template_right{
    flex-grow:9;
    width:80%;
    position:relative
    height: calc(100vh - 190px);
  }
  .color{
    color:#5892e5;
    cursor: pointer;
  }

  .template_main .el-table{
    height: 100% !important;
  }

  .el-table .el-table__body-wrapper.is-scrolling-left {
    height: 90% !important;
  }
  
