

const state = {
  workflowurl:'',
  bmUrl:'',
  isCollapse: false,
  isHideMenu:false
}

const mutations = {
  SET_ISCOLLAPSE: (state, iscollapse) => {
    state.isCollapse = iscollapse
  },
  SET_HIDEMENU:(state, ishide) => {
    state.isHideMenu = ishide
  },
  SET_WORKFLOWURL:(state, url) => {
    state.workflowurl = url
  },
  SET_BMURL:(state, url) => {
    state.bmUrl = url
  },
}

const actions = {
  setBmUrl({ commit }, url) {
    return new Promise((resolve, reject) => {
      commit('SET_BMURL', url)
    }).catch(error => {
      reject(error)
    })
  },
  setworkflowurl({ commit }, url) {
    return new Promise((resolve, reject) => {
      commit('SET_WORKFLOWURL', url)
    }).catch(error => {
      reject(error)
    })
  },
  setIscollapse({ commit }, iscollapse) {
    return new Promise((resolve, reject) => {
      commit('SET_ISCOLLAPSE', iscollapse)
    }).catch(error => {
      reject(error)
    })
  },
  setHideMenu({ commit }, ishide){
    return new Promise((resolve, reject) => {
      commit('SET_HIDEMENU', ishide)
    }).catch(error => {
      reject(error)
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
