                ></el-radio>
              </template>
            </el-table-column>
            <el-table-column
              v-for="(item, index) in expertLabel"
              v-if="item.colDname != 'expertName'&&item.showType==1"
              :key="index"
              :prop="item.colDname"
              :label="item.dispName"
              min-width="150"
              :show-overflow-tooltip="true"
            ></el-table-column>
            <el-table-column
              v-else-if="item.colDname == 'expertName'"
              :label="item.dispName"
              min-width="150"
