      >
        <el-table-column type="index" width="50" label="序号"></el-table-column>
        <el-table-column
          v-for="(item, index) in expertLabel"
          v-if="item.colDname != 'expertName'&&item.showType==1"
          :key="index"
          :prop="item.colDname"
          :label="item.dispName"
          min-width="150"
        ></el-table-column>
        <el-table-column
          v-else-if="item.colDname == 'expertName'"
          :label="item.dispName"
          min-width="150"
        >
          <template slot-scope="scope">
            <span>{{scope.row.expertName}}</span>
          </template>
        </el-table-column>
      </el-table>
      <div class="page">
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="submitSelectExpert" v-loading="sureBtn1">确 认</el-button>
          <el-button @click="cancleShowSelectExpertResult">取 消</el-button>
        </div>
