    </el-dialog>
  </div>
</template>

<script>
// @ is an alias to /src
import { mapGetters } from "vuex";
import SelectTree from "@/components/treeSelect.vue";
import itemDialog from "@/views/project/item.vue";
import ItemAddCom from "@/views/project/itemAddCom";

import {
  auditBySpeciality,
  getTreeDeptCode,
  getUserDept,
  queryItemTitle,
  queryAll,
  saveWorkItem,
  getProcessStatus,
  updateProcessFinished,
  getItemInfoByProcessInstId,
  queryTitle,
  getItemInfoByProcessInstIdWithBatchGroup,
  checkPlan,
  getExpertsListByWorkId,
  getExperts,
  selectExpertsByIds,
  exportAdviceFile,
  updateAuditRunStateByIds,
  prjStFeaAuditNew,
  getItemLabelInfo,
  createProcess,
  updateItemPhaseDet,
  getExpertsNumByProcessId,
  getWorkItemStatus,
  getNotJoinExpertInfoByProcessId,
  getDictMap
} from "@/api/data";

import { queryBySpeciality, getSpecialityIds } from "@/api/data";
import querys from "@/components/querys";

import { finishWorkItem, backActivity ,terminateProcessInstance,
createWithOptionAndReturnWorkItem ,appointActivityParticipantWithIscLoginName} from "@/api/workflow";

export default {
  name: "questions",
  data() {
    return {
      professionalExpert:"",
      professionalList:[],
      rejectName:"",
      isLeader:"",
      rejectReason:"",
      itemId:"",
      showQuery:false,
      tableHeightTab:"",
      timer: "",
      showAddDialog: false, //弹窗是否展示-新增 修改 浏览
      showData: {
        itemId: null, //项目id
        specClass: "", //专项类型
        specClassName: "", //专项类型名称
        oldPath: "", //原来路由
        tableLabelArr: "", //表头
        phash: "1", //阶段1=立项 2=可研 3=储备 0=浏览
        parentItemId: null //父项id-新增下级时用
      },
      showDrawer: false,
      deptCode: "",
      isClearable: true, // 可清空（可选）
      isAccordion: true, // 可收起（可选）
      valueId: "", // 初始ID（可选）
      props: {
        // 配置项（必选）
        value: "id",
        label: "name",
        children: "children",
        code: "deptCode"
        // disabled:true
      },
      deptArr: [],
      //加载保存审核意见
      loadingBtn1: false,
      //评审意见参数
      auditItemParam: {
        audited: "1",
        auditAdvice: "",
        itemIds: "",
          itemPhaseDet:""
      },
      isShowQuery: false,
      dialogVisibleL: false,
      dialogLoading: false,
      loading: false,
      delData: {
        ids: []
      },
      lableList: [{ value: "" }],
      totalNum: 0,
      currentPage: 1,
      currentNum: 10,
      year: "",
      dialogFormVisible: false,
      form: {},
      options: [],
      value: "",
      formLabelWidth: "140px",
      input: "",
      // 子组件的表格数据
      tableData: [],
      // 子组件的表头数据
      tableLabel: [],

      //查询条件
      param: {
        createOrg: "", //单位编码
        itemName: "", //项目名称
        itemType: "", //项目类型
        pageSize: 10,
        speciality: "", //专业类型
        voltLevel: "", //电压等级
        // feaStatus: 0,  //可研状态
        ghYear: "", //规划年份
        itemSource: ""
      },
      // dictMap: Object,
      dictMap: new Map(),
      tableDataShow: [],
      saveData: {
        kssj: ""
      },
      //提交评审计划弹框中的信息
      pleanTableLabel: [],
      planLabel: {},
      //项目id
      tempItemId: "",
      //项目名称
      tempItemName: "",
      //是否展示项目详情弹框
      itemShow: false,
      saveLabel: {},
      //项目可研报告文件名
      reportFileName: "",
      //获取字段类型字典，用于日期类型的展示
      columnTypeMap: new Map(),
      isShowFilter: false,
      //自动表格高度

      tableHeight: "65vh",
      minDate: "",
      maxDate: "",
      auditPlanId: "",
      //点击提交传入的参数
      saveParams: [],
      //用户所属专项类型值
      userSpecialityArr: [],
      //评审专家数据**********
      tempIsBatch:false,
      //专家列表
      professorArr:[],
      //选择专家要提交的数据
      selectProfessorArr:[],
      groupLeaderName:"",
      //选择专家后展示的数据
      selectProfessorArrShow:[],
      //专家表头
      expertLabel:{},
      //专家
      expertShowTable:[],
      //专家字典
      expertDictMap: new Map,
      //专家字段字典
      expertColumnMap: new Map,
      //专家数据
      expertTableData: [],
      expertCurrentPage:1,
      expertCurrentNum:5,
      //搜索专家名称
      leaderExpertName:"",
      //选择组长弹框
      showExpectDialog:false,
      //展示手动选择专家结果
      showSelectExpertResult2:false,
      //选择专家确认按钮加载（手动）
      sureBtn1:false,
      totalExpertNum:""
    };
  },
  components: {
    querys,
    SelectTree,
    ItemAddCom
  },
  watch: {
    deptCode() {
      this.param.createOrg = this.deptCode;
      this.getTable(this.currentPage, this.currentNum);
    }
  },
  created() {
    //表格自动高度
    this.tableHeight=window.innerHeight-220;
  },


  mounted() {
    this.getSpecialityIds();
    this.getTableWithTitle(1, 10);
    this.getNotJoinReason()
    this.getExpertMajorDict()
  },
  computed: {
    ...mapGetters([
      // 'token',
    ])
  },
  methods: {
    cancel() {
        top.opener=null;
        top.close();     //注意,   不可跨域
        // parent.window.close();
        // var index = parent.layer.getFrameIndex(window.name);
        // parent.layer.close(index);
    },
    getValue(value) {
      this.deptCode = value;
    },
    // 子组件向父组件传递参数
    changeStatus(status) {
      this.showDrawer = status;
      console.log("status" + status);
    },
    //展示项目详细信息
    showDetailDialog(row) {
      this.showData.itemId = row.itemId;
      this.showData.phash = 0;
      this.showDrawer = true;
      this.timer = new Date().getTime();
    },
    //获取单位树形结构
    getTreeDeptCode() {
      let _this = this;
      getUserDept().then(response => {
        let valueId = response.data.resultValue;
        getTreeDeptCode().then(response => {
          console.log(response);
          if (response.data.successful) {
            _this.valueId = valueId;
            _this.deptArr = response.data.resultValue;
          }
        });
      });
    },
     getRowKeys(row){
      return row.expertId;//返回唯一ID
    },

    //查询专家专业下拉列表
    getExpertMajorDict(){
        let _this = this
        getDictMap({"codeValue":"professional"}).then(response=>{
             if(response.data.successful){
               let arr = response.data.resultValue;
               for(let i=0;i<arr.length;i++){
                  let item = arr[i]
                    if(item.codeValue=="professional"||
                          item.codeValue=="0007"||
                          item.codeValue=="0008"||item.codeValue=="0006"){
                         continue
                    }
                    _this.professionalList.push(item)
               }
             }else{
                this.$message.error("查询专家专业失败")
             }
        })
    },

        //手动选专家点击取消
    cancleSelectExpert(){
       let _this = this
       _this.selectProfessorArr = []
       _this.showExpectDialog = false
    },

    //手动选专家结果点击取消
    cancleShowSelectExpertResult(){
      let _this = this
      _this.selectProfessorArrShow = []
      _this.selectProfessorArr = []
      _this.showSelectExpertResult2 = false

    },

    //   查询驳回原因
    getWorkItemStatus(){
        let _this = this
        let processInstID = _this.$route.query.processInstID;
        let formData = {}
        formData.processInstID = processInstID
        getWorkItemStatus(formData).then(res=>{
            if(res.data.successful){
                console.log(res)
                let arr = res.data.resultValue
                _this.rejectReason = arr[arr.length-1].auditDesc
            }
        })
    },

    // 查看专家不同意原因
    getNotJoinReason() {
      let _this = this
      let processInstID = _this.$route.query.processInstID;
      let formData = {}
      formData.processInstId = processInstID
      getNotJoinExpertInfoByProcessId(formData).then(res => {
        if(res.data.successful){
          let resVal = res.data.resultValue
          _this.rejectName = resVal.expertName 
          _this.rejectReason =  resVal.notJoinReason
        }
      })
    },

    // 每页条数
    handleSizeExpert1(val) {
      this.expertCurrentNum = val;
      this.chooseExperts(this.expertCurrentPage,val)
    },
    // 翻页
    handleCurrentExpert1(val){
      this.expertCurrentPage = val;
      this.chooseExperts(val,this.expertCurrentNum)
    },
    // 搜索
    searchExperts() {
      this.chooseExperts(this.expertCurrentPage,this.expertCurrentNum)
    },
    // 查询专家列表
    chooseExperts(page,size) {
      let _this = this
      let paramTable = {
        processId:_this.$route.query.processInstID,
        index:page,
        pageSize:size,
        expertName:_this.leaderExpertName,
        professional:_this.professionalExpert
      }
      getExpertsListByWorkId(paramTable).then(response => {
        if(response.data.successful){
           _this.isLeader = response.data.resultValue.isLeader
            _this.totalExpertNum = response.data.resultValue.totalNum
            let arr = response.data.resultValue.tableLable
            _this.expertLabel = _this.getLabelList(arr)
            _this.expertDictMap = _this.getDictArr(arr)
            _this.expertColumnMap = _this.getColumnTypeArr(arr)
            _this.expertTableData = response.data.resultValue.tableData
            _this.expertShowTable = _this.getShowDateTable(_this.expertTableData,_this.expertDictMap,_this.expertColumnMap)
            _this.showExpectDialog = true
        }
      })
    },

    //选择专家组长触发
    getCurrentRow(row){ //获取选中数据
        let _this = this
        _this.selectProfessorArr = row.expertId
        _this.groupLeaderName = row.userName
        _this.selectProfessorArrShow = []
        _this.selectProfessorArrShow = _this.selectProfessorArrShow.concat(row)
    },

    //手动选择专家
    selectExpect(){
        let _this = this
        let _expert = _this.selectProfessorArr
        if(_expert== "" || _expert == undefined){
          _this.$message({
            showClose: true,
            message: '请选择',
            type: 'warning'
          });
          return false
        }
        _this.showSelectExpertResult2 = true

    },

    //确认
    submitSelectExpert() {
        let endParam = {
            "user": this.$route.query.iscUserId,
            "processInstID":this.$route.query.processInstID
        }
        this.loadingBtn1 = true
        // 先结束流程
        terminateProcessInstance(endParam).then(res => {
          console.log(res)
            if(res.data.status == 0){
              // 再给返回的专家创建新的代办
              //重新选择专家时，通过流程id查询原来选择的专家人数 add by gao.feng
              getExpertsNumByProcessId({"processInstId":endParam.processInstID}).then(resT=>{
                  console.log(res)
                  if(resT.data.successful){
                      let totalNum = resT.data.resultValue
                      this.createProcessAgainByExpert(this.isLeader,this.selectProfessorArrShow[0],totalNum)
                  }
              })
                      
            }else{
              this.loadingBtn1 = false
            }
        })
    },

    createProcessAgainByExpert(isLeader,item,totalNum) {
      let _this = this
      let userid = this.$route.query.iscUserId
      let params = {
          "user": userid,
          "processDefName": "expert_selection",
          "processInstName": '',
          "processInstDesc": ''
      }
      params.itemId = _this.tempItemId
      params.processInstName = _this.tempItemName+'-专家确认'
      params.processInstDesc = _this.tempItemName+'-专家确认'
      createWithOptionAndReturnWorkItem(params).then(res => {
        if(res.data.status == 0){

          let workItemID = res.data.data.workItem.workItemID
          let processInstID = res.data.data.processInstID
          let param = {
              "user":userid,
              "workItemID":workItemID,//工作项id 上一步返回值中查找
              "activityDefID":"ZJQR",//活动定义id
              "participants":[]
          }
          param.participants.push(item.userName)
          // 指定专家给专家发待办
          appointActivityParticipantWithIscLoginName(param).then(res => {
            if(res.data.status == 0){
                let params = {
                  "user": userid,
                  "workItemID": param.workItemID
                }
                finishWorkItem(params).then(res => {
                  if(res.data.status == 0){
                    let finishparams = {
                        processInstID: processInstID,
                        userId: userid,
                        itemId:_this.tempItemId,
                        workItemID: param.workItemID,
                        isLeader:isLeader,
                        expertId:item.expertId,
                        itemPhaseDet:'10',
                        totalNum:totalNum
                    }
                    if(_this.tempIsBatch == 1){
                      finishparams.isBatchPackage = true
                      
                    }else{
                      finishparams.isBatchPackage = false
                      
                    }
                    // 将返回值信息及项目信息插入后端数据库
                    createProcess(finishparams).then(finishres => {
                        if (finishres.data.successful) {
                            let itemArr = []
                            let itemObj = {}
                            itemObj.expertId = item.expertId
                            itemObj.expertName = item.userName
                            itemObj.isLeader = isLeader
                            itemObj.itemId = _this.tempItemId
                            itemObj.isBatchPackage = finishparams.isBatchPackage
                            itemArr.push(itemObj) 
                            // //选择专家
                            selectExpertsByIds(itemArr).then(response=>{
                              this.loadingBtn1 = false
                              if(response.data.successful){
                                  this.$alert('操作成功', '提示', {
                                    confirmButtonText: '确定',
                                    callback: () => {
                                      parent.GetChildValue("true");
                                    }
                                  });
                              }else{
                                    _this.$message({
                                      showClose: true,
                                      message: '操作失败',
                                      type: 'error'
                                    });
                              }
                            })
                          
                        }else {
                            // let error = finishres.data.resultHint;
                            this.$message.error("操作失败");
                            this.loadingBtn1 = false
                        }
                    });
                  }else{
                    this.loadingBtn1 = false
                  }
                })
              
            }else{
              this.loadingBtn1 = false
            }    
          }) 
        }else{
          this.loadingBtn1 = false
        }
    })
    },


    getSpecialityIds() {
      let _this = this;
      getSpecialityIds().then(response => {
        if (response.data.successful) {
          let str = response.data.resultValue;
          if (str != "") {
            _this.userSpecialityArr = str.split(",");
          }
          console.log(response);
        }
      });
    },
    //时间戳字符串的转化
    formartDate(param) {
      var time = new Date(param);
      var y = time.getFullYear();
      var m = time.getMonth() + 1;
      var d = time.getDate();
      // var h = time.getHours();
      // var mm = time.getMinutes();
      // var s = time.getSeconds();
      // return y+'-'+this.add0(m)+'-'+this.add0(d)+' '+this.add0(h)+':'+this.add0(mm)+':'+this.add0(s);
      return y + "-" + this.add0(m) + "-" + this.add0(d);
    },
    add0(m) {
      return m < 10 ? "0" + m : m;
    },
    switchFilter() {
      this.isShowFilter = !this.isShowFilter;
    },
    //获取字段类型字典
    getColumnTypeArr(labelList) {
      let map = new Map();
      for (let i = 0; i < labelList.length; i++) {
        let temp = labelList[i];
        map.set(temp.colDname, temp.colType);
      }
      return map;
    },

    //查询表头以及列表数据
    getTableWithTitle(size) {
      //（page,size）
      let _this = this;
      _this.loading = true;
      queryTitle().then(response => {
        _this.loading = false;
        if (response.data.successful) {
          //获取表头
          _this.tableLabel = response.data.resultValue
          //从表头中抽取出字典信息列表
          _this.dictMap = _this.getDictArr(_this.tableLabel);
          //获取字段类型对应关系
          _this.columnTypeMapDict = _this.getColumnTypeArr(_this.tableLabel);
          //从路由中获取流程实例id
          let processInstID = _this.$route.query.processInstID;
          //获取列表数据
          //根据流程实例id查询项目id
          getItemInfoByProcessInstIdWithBatchGroup({ processInstID: processInstID }).then(
            res => {
              if (res.data.successful) {
                console.log(res);
                let itemInfo = res.data.resultValue;
                // 保存或者退回需要用到的参数
                _this.auditItemParam.itemIds = itemInfo.itemId;
                _this.auditItemParam.itemPhaseDet = itemInfo.itemPhaseDet;
                _this.tempItemName = itemInfo.itemName
                _this.tempItemId = itemInfo.itemId;
                _this.tempIsBatch = itemInfo.isBatchPackage;
                let arr = [];
                if (itemInfo != null) {
                  arr.push(itemInfo);
                  
                }
                _this.tableDataShow = _this.getShowDateTable(arr, _this.dictMap, _this.columnTypeMapDict);
              }
            }
          );
        } else {
          this.$message.error("查询表头信息出错");
        }
      });
    },
    getDictArr(labelList) {
      //根据当前用户id查询用户所属的单位
      //获取数据字典
      let _this = this;
      let map = new Map();
      for (let i = 0; i < labelList.length; i++) {
        let temp = labelList[i];
        if (temp.codeList != null) {
          map.set(temp.colDname, temp.codeList);
        }
      }
      return map;
    },
    //获取插入和修改的表头
    getLabelList(labelList) {
      let _this = this;
      labelList.forEach(function (item) {
        _this.$set(item, "value", "");
      });
      let common_table_info = {}; //数组转对象，避免类型报错
      for (let i in labelList) {
        common_table_info[i] = labelList[i];
      }
      return common_table_info;
    },
    //获取展示数据
    getShowDateTable(tableData, dictMap, columnDictMap) {
        let _this = this
        let arr = [];
        for (let i = 0; i < tableData.length; i++) {
            let tempObj = tableData[i];
            //拷贝对象
            let copyObj = new Object();
            //遍历获取的数据中的属性，如果属性名有和数据字典中的key相对应的，取出数据字典对应的值赋值过去
            for (let [key, value] of Object.entries(tempObj)) {
                //console.log(key + ':' + value);
                let lowerKey = key.toLowerCase()
                if (dictMap.has(key)) {
                    let dictValueList = dictMap.get(key);
                    for (let j = 0; j < dictValueList.length; j++) {
                        let tempObj2 = dictValueList[j];
                        if (tempObj2.codeValue == value) {
                            copyObj[key] = tempObj2.codeName;
                        }
                    }
                } else if (dictMap.has(lowerKey)) {
                    let dictValueList = dictMap.get(key.toLowerCase);
                    for (let j = 0; j < dictValueList.length; j++) {
                        let tempObj2 = dictValueList[j];
                        if (tempObj2.codeValue == value) {
                            copyObj[key] = tempObj2.codeName;
                        }
                    }
                } else if (columnDictMap.get(key) == "date" || columnDictMap.get(key) == "month"
                    || columnDictMap.get(key) == "year") {
                    if(value!=null&&value!=''){
                        copyObj[key] = _this.formartDate(value, columnDictMap.get(key));
                    }  
                } else {
                    copyObj[key] = value;
                }

                if (key == "itemClass") {
                    copyObj["itemClassT"] = value
                }

                if (key == "conDept") {
                    copyObj["conDept"] = value
                }
                if (key == "itemLevel") {
                    copyObj["itemLevelT"] = value
                }

                //add by liangwu
                if (key == "specDetailClass") {
                    copyObj["specDetailClassT"] = value
                }

                if (key == "specClass") {
                    copyObj["specClassT"] = value
                }
                //add end
            }
            if (typeof (tempObj.children) != 'undefined' && tempObj.children != null && tempObj.children.length > 0) {
                let tempArr = _this.getShowDateTable(tempObj.children, dictMap, columnDictMap)
                copyObj.children = tempArr
            }
            arr.push(copyObj);
        }
        return arr;
    },
  }
};
</script>
