<template>
  <div class="isclogin-container" v-loading="iscLoading" element-loading-text="正在登录...">
    isclogin

  </div>
</template>

<script>
import { getUrlKey } from '@/utils/auth'
import { mapGetters } from 'vuex'

export default {
  name: 'isclogin',
  data() {
    return {
      iscLoading:true
    }
  },
  computed: {
    ...mapGetters([
      'isIscLogin',
      'isSimulateLogin',
      'iscUrl'
    ])
  },
  mounted(){
    let _this = this
    sessionStorage.clear();//由于无法退出登录 清除storage
    console.log("清除了localStorage",sessionStorage)
    // setTimeout(function () {
     let key = getUrlKey("ticket",window.location.href)
     let iscUserId = getUrlKey("iscUserId",window.location.href)
     let loginName = getUrlKey("loginName",window.location.href)
     _this.$store.dispatch('user/setLocationUrl',window.location.href)//设置跳转地址

     var storage = sessionStorage
     storage.setItem("iscUserId",iscUserId);
     storage.setItem("loginName",loginName);
     storage.setItem("ticket",key);


      // let key = getUrlKey("ticket",'http://localhost:8080/xmzt/cbzx/index.html?ticket=xxxxx#/isclogin')
      // let iscUserId = getUrlKey("iscUserId",'http://localhost:8080/xmzt/cbzx/index.html?username=xxxxx&uid=ididi#/isclogin')
      // let loginName = getUrlKey("loginName",'http://localhost:8080/xmzt/cbzx/index.html?username=xxxxx&uid=ididi#/isclogin')
      console.log('key',key,'iscUserId',iscUserId,'loginName',loginName)
      if(key && key.length > 1 || iscUserId){
        console.log('iscUserId')
        _this.$store.dispatch('user/setKey', key).then(() => {
          if(_this.isIscLogin){//第二种登录
            console.log('第二种登录')
            _this.$store.dispatch('user/iscLogin', _this.$store.getters.key).then(() => {
              let path = getUrlKey("path",window.location.href)
              console.log('path'+path)
              debugger
              if(path){
                //modify by liangwu
                if(path == "projectApproval" ||path == "projectSubmitApproval"
                ||path == "planAssessmentApproval" ||path == "planAssessAgainApproval" 
                ||path == "expertsConfirmApproval" ||path == "expertsAgainApproval"
                ||path == "reviewCommentsApproval" ||path == "collectCommentsApproval"
                ||path == "countersignatureApproval" ||path == "perfectFileApproval" ||path == "uploadFileApproval" ) {
                  let processInstID = getUrlKey("processInstID",window.location.href)
                  let workItemID = getUrlKey("workItemID",window.location.href)
                  let activityInstID = getUrlKey("activityInstID",window.location.href)
                  let iscUserId = getUrlKey("iscUserId",window.location.href)
                  _this.$router.push({ path: path,query: { path:path,processInstID:processInstID,workItemID:workItemID,activityInstID:activityInstID,iscUserId:iscUserId } })
                }
                else {
                  _this.$router.push({ path: path,query: { path:path} })
                }
              }else{
                _this.$router.push({ path: _this.redirect || '/' })
              }

              // _this.$router.push({ path: _this.redirect || '/' })
            })
          }else{//第三种登录
            console.log('第三种登录')
            _this.$store.dispatch('user/simulateLogin', _this.$store.getters.key).then(() => {
              _this.$router.push({ path: _this.redirect || '/' })
            })
          }

        })


      }else if(iscUserId && loginName && loginName != 'null' && iscUserId != 'null'){//iscuserid
        // let info ={
        //   iscUserId: iscUserId,
        //   loginName: loginName
        // }
        // _this.$store.dispatch('user/setInfo', info).then(() => {//尝试不存state
          if(_this.isIscLogin){//第二种登录
            console.log('第二种登录')
            _this.$store.dispatch('user/iscLogin').then(() => {
              _this.$router.push({ path: _this.redirect || '/' })
            })
          }else{//第三种登录
            console.log('第三种登录')
            _this.$store.dispatch('user/simulateLogin').then(() => {
              _this.$router.push({ path: _this.redirect || '/' })
            })
          }
        // })
      }else{
        // window.location.href="http://sso.isc.com:8989/isc_sso/login?service="+encodeURIComponent(window.location.href)
        window.location.href=_this.iscUrl+"/isc_sso/login?service="+encodeURIComponent(window.location.href)

      }
      // console.log(getUrlKey("ticket",'https://www.baidu.com/?ticket=ST-219-zaKZDxQRsXTs7RpnmPbW-uapsso.example.org'))
      // },500)
  },


  methods: {


  }
}
</script>
