import axios from 'axios'
import store from '@/store'
import { MessageBox, Message } from 'element-ui'
import {removeToken , removeKey} from '@/utils/auth'
import router from '@/router'


axios.defaults.baseURL = process.env.VUE_APP_BASE_URL


// 添加请求拦截器
axios.interceptors.request.use(
  config => {
    var strArr = config.url.split("/")
    var str = strArr[strArr.length-1]
    // 自定义header信息（比如token）
// console.log('配置地址',store.state.app.workflowurl,str)


    if( str != 'ticketLogin' && str != 'getUserInfo' && str != 'login'){

    	config.headers['userId'] = sessionStorage.getItem("userId");
    	config.headers['token'] = sessionStorage.getItem("token");

    }
    return config;
  }, function (error) {
    // 对请求错误做些什么
    // Message.error({message: '请求超时!'});

    return Promise.reject(error);
  });

//  RESPONSE 响应异常拦截
axios.interceptors.response.use(data=> {
//==============  所有请求完成后都要执行的操作  ==================
	// console.log('所有请求完成后都要执行的操作',data.data.successful)

  // 仅对200和error状态处理
  if (data.status && data.status == 200 && typeof(data.data.successful)!='undefined'
  		&&!data.data.successful) {
  	console.log('仅对200和error状态处理',data.data.resultHint)
  	if(data.data.resultHint != '没有访问权限'){
  		// Message.error({message:data.data.resultHint});
  		Message({
                showClose: true,
                dangerouslyUseHTMLString: true,
                message: data.data.resultHint,
                type: 'error'
        });
  	}

    if(data.data.resultHint == '没有访问权限'){//登录超时等
    	removeToken()
	  	removeKey()
	  	sessionStorage.clear()
	  	let loca = store.state.user.locationUrl
      if(loca.indexOf("?") != -1){
            loca = loca.split("?")[0];
        }
        console.log('错误登录跳转地址',loca)
        // let loca = encodeURIComponent('http://'+location.host+'/isclogin')
        window.location.href=store.state.user.iscUrl+"/isc_sso/login?service="+loca
        // window.location.href="http://sso.isc.com:8989/isc_sso/login?service="+loca
	  	// router.replace({
	   //      path: '/isclogin'
	   //  })
    }
    return data;
  }
  //登录错误
  // if(data.status && data.status == 200 && data.data=='authorizeFail'){
  // 	// location.replace(`/user/login`)
  // 	removeToken()
  // 	removeKey()
  // 	router.replace({
  //       path: '/isclogin'
  //   })
  //   return;

  // }
  return data;
}, err=> {
//==============  错误处理  ====================
   if (err && err.response) {
        switch (err.response.status) {
            case 400: err.message = '请求错误(400)'; break;
            case 401: err.message = '未授权，请重新登录(401)'; break;
            case 403: err.message = '拒绝访问(403)'; break;
            case 404: err.message = '请求出错(404)'; break;
            case 408: err.message = '请求超时(408)'; break;
            case 500: err.message = '服务器错误(500)'; break;
            case 501: err.message = '服务未实现(501)'; break;
            case 502: err.message = '网络错误(502)'; break;
            case 503: err.message = '服务不可用(503)'; break;
            case 504: err.message = '网络超时(504)'; break;
            case 505: err.message = 'HTTP版本不受支持(505)'; break;
            default: err.message = `连接出错(${err.response.status})!`;
        }
    } else {
        err.message = '连接服务器失败!'
    }

    Message.error({message:err.message } )

  return Promise.resolve(err);
})

