// @ is an alias to /src
import { mapGetters } from 'vuex'
import SelectTree from "@/components/treeSelect.vue";
import itemDialog from '@/views/project/item.vue';
import ItemAddCom from "./itemAddCom"

import {auditBySpeciality,getTreeDeptCode,getUserDept,queryItemTitle,
	queryAll } from '@/api/data';

import {
queryBySpeciality,
getSpecialityIds,
