const state = {
    treeListInfo : ""
}

const mutations = {
    SET_TREEINFO: (state,treeListInfo) => {
        state.treeListInfo = treeListInfo
    }
}

const actions = {
    set_treeInfo: ({ commit },treeListInfo) => {
        commit('SET_TREEINFO',treeListInfo)
    }
}

export default {
    namespaced: true,
    state,
    mutations,
    actions
  }