    getTable(str,page,size){
      let _this = this
      _this.loading = true
      let data
      if(str == "batchData"){

        data = { ..._this.getBatchData}
        console.log('getBatchData',data)
        _this.batchSubmission.orgNo = data.orgNo//_this.getBatchData
        _this.batchSubmission.orgDept = data.orgDept
        _this.batchSubmission.typeList = data.typeList
        //筛选批量右边表格 typelist string 保存的时候要数组
        data.typeList = data.typeList.toString()

      }else{
        data = _this.getTableData
      }
      data.pageIndex = page;
      data.pageSize = size;
       getPrjStProb(data).then(response => {//{}写需要的参数
          console.log('response',response)//后端返回数据
          if(response.data.successful){
              if(str == "batchData"){//批量打包筛选
                _this.batchData  = response.data.resultValue.tableData
                _this.showBatchData = _this.getShowDateTable(_this.batchData,_this.dictMap,_this.columnTypeMap)
                _this.batchLabel = response.data.resultValue.tableLable
              }else{
                _this.totalNum = response.data.resultValue.totalNum
                _this.tableLabel = response.data.resultValue.tableLable
                let arr = response.data.resultValue.tableLable
                _this.tableLabel = _this.getLabelList(arr)
                _this.dictMap = _this.getDictArr(arr)
                console.log('dictMap',_this.dictMap)
                _this.columnTypeMap = _this.getColumnTypeArr(arr)
                _this.tableData = response.data.resultValue.tableData
                _this.tableShowData = _this.getShowDateTable(_this.tableData,_this.dictMap,_this.columnTypeMap)
            //     setTimeout(() => {
            // 　　  this.toggleRowSelect(_this.tableShowData);
            //  　　}, 500)
                
              }

          }
          _this.loading = false
          
        }).catch(error => {
          console.log(error)//error
        })
        
    },
    toggleRowSelect(rows) {
      const _this = this
      if (rows.length > 0) {
        this.$nextTick(() => {
          rows.forEach(row => {
            _this.$refs.questionTable.toggleRowSelection(row, true)
          })
        })
      } else {
        this.$refs.questionTable.clearSelection()
      }
    },
   
    //获取插入和修改的表头
    getLabelList(labelList){ 
      let _this =this
      labelList.forEach(function (item) {
        _this.$set(item,'value','');
      })
      let common_table_info = {};//数组转对象，避免类型报错
      for(let i in labelList){
        common_table_info[i] = labelList[i];
      }
      return common_table_info
    },
    //获取字典
    getDictArr(labelList){ 
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        if(temp.codeList!=null){
          map.set(temp.colDname,temp.codeList);
        }
      }
      return map
    },
     //获取字段类型字典
    getColumnTypeArr(labelList){
      let map = new Map();
      for(let i=0;i<labelList.length;i++){
        let temp = labelList[i];
        map.set(temp.colDname,temp.colType);
      }
      return map
    },
    //获取展示数据
    getShowDateTable(tableData,dictMap,columnTypeMap){
      let _this = this
      let arr = [];
      for(let i=0;i<tableData.length;i++){
        let tempObj = tableData[i];
        //拷贝对象
        let copyObj = new Object();
        //遍历获取的数据中的属性，如果属性名有和数据字典中的key相对应的，取出数据字典对应的值赋值过去
        for (let [key, value] of Object.entries(tempObj)) {
          // console.log(key + ':' + value);
          if(key=="feaStatus"){
               copyObj["feaStatusT"] = value;
          }
          if(dictMap.has(key)){
            let dictValueList = dictMap.get(key);
            for(let j=0;j<dictValueList.length;j++){
              let tempObj2 = dictValueList[j];
              if(tempObj2.codeValue==value){
                copyObj[key] = tempObj2.codeName;
              }
            }
          }else if(columnTypeMap.get(key)=="DATE"){
               copyObj[key+"Rel"] = value
               let timeStr = _this.formartDate(value,key)
               copyObj[key] = timeStr;
          }else{
            copyObj[key] = value;
          }
          if(key=="orgNo"){
            copyObj["orgNo"] = value
          }
        }
        arr.push(copyObj);
      }
      return arr;
    },
    //时间戳字符串的转化
    formartDate(param,key) {
      var time = new Date(param);
      var y = time.getFullYear();
      var m = time.getMonth()+1;
      var d = time.getDate();
      // var h = time.getHours();
      // var mm = time.getMinutes();
      // var s = time.getSeconds();
      // return y+'-'+this.add0(m)+'-'+this.add0(d)+' '+this.add0(h)+':'+this.add0(mm)+':'+this.add0(s);
      // return y+'-'+this.add0(m)+'-'+this.add0(d);
      if(key == 'year'){
        return y
      }else{
        return y+'-'+this.add0(m)+'-'+this.add0(d);
      }
    },
    add0(m){
      return m<10?'0'+m:m;
    },
    beforeupload(){
      // if(row.userNo &&this.userId != row.userNo){
      //   this.$message.warning("只有创建者能上传和覆盖附件")
      //   return false
      // }
      // this.loading =  true
    },
    errorUpload(){

      this.loading = false
      this.$message({
            type: 'error',
            message: '上传失败'
          });  
    },
    uploadSuccess(response, file, fileList){
      let _this = this
      console.log(response)
      _this.uploading =  false
      _this.fileList = fileList
      console.log(_this.fileList)
      if(response.successful){
          // _this.closePfDialog()
          _this.$message({
                showClose: true,
                message: "上传成功",
                type: 'success'
        });
        _this.currentPage = 1;
        _this.getTable("",1,_this.currentNum)
      }else{
        _this.$message({
                showClose: true,
                message: response.resultHint,
                dangerouslyUseHTMLString: true,
                type: 'error'
        });
      }
    },
    exceed(){
      this.$message({
          showClose: true,
          message: '只能上传一个文件',
          type: 'warning'
        });
    },
  }
}
